import CjsGetMovieClipLabelsByName from '../../utils/CjsGetMovieClipLabelsByName';
import CjsLabelPositionsByName from '../CjsLabelPositionsByName';
import { CjsButtonLabels } from './CjsButtonLabels';
import CjsButtonsOverHelperKludge from './CjsButtonsOverHelperKludge';

type DisplayObject = createjs.DisplayObject;
type MovieClip = createjs.MovieClip;
type FrameLabel = number | string;

export default class CjsButtonHelper {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly target: MovieClip;

  downLabel: FrameLabel;

  outLabel: FrameLabel;

  overLabel: FrameLabel;

  selectedLabel: FrameLabel;

  private readonly labelsByName: CjsLabelPositionsByName;

  private _isDown: boolean;

  private _isOver: boolean;

  private _isSelected: boolean;

  private _shouldUseDisabledState: boolean;

  private _enabled!: boolean;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(target: MovieClip, outLabel: FrameLabel, overLabel: FrameLabel,
    downLabel: FrameLabel, hitArea?: DisplayObject, hitLabel?: FrameLabel,
    selectedLabel?: FrameLabel) {
    this.onTargetRollOver = this.onTargetRollOver.bind(this);
    this.onTargetRollOut = this.onTargetRollOut.bind(this);
    this.onTargetMouseDown = this.onTargetMouseDown.bind(this);
    this.onTargetPressUp = this.onTargetPressUp.bind(this);

    this.target = target;
    this.labelsByName = CjsGetMovieClipLabelsByName(target);
    this.downLabel = downLabel || CjsButtonLabels.Down;
    this.overLabel = overLabel || CjsButtonLabels.Over;
    this.outLabel = outLabel || CjsButtonLabels.Out;
    this.selectedLabel = selectedLabel || CjsButtonLabels.Selected;
    this._isDown = false;
    this._isOver = false;
    this._isSelected = false;
    this._shouldUseDisabledState = true;

    if (target instanceof createjs.Container) {
      // prevents issues when children are removed from the display list when state changes.
      target.mouseChildren = false;
    }

    this.enabled = target.mouseEnabled;

    this.initHitArea(hitArea, hitLabel);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    const { target } = this;
    CjsButtonsOverHelperKludge.delete(target);
    target.cursor = null as unknown as string;
    target.removeEventListener('rollover', this.onTargetRollOver);
    target.removeEventListener('rollout', this.onTargetRollOut);
    target.removeEventListener('mousedown', this.onTargetMouseDown);
    target.removeEventListener('pressup', this.onTargetPressUp);
  }

  // ----------------------------------------------------------------------------------------------
  get enabled(): boolean {
    return (this._enabled);
  }

  // ----------------------------------------------------------------------------------------------
  set enabled(value: boolean) {
    this.setEnabled(value);
  }

  // ----------------------------------------------------------------------------------------------
  get isDown(): boolean {
    return (this._isDown);
  }

  // ----------------------------------------------------------------------------------------------
  get isOver(): boolean {
    return (this._isOver);
  }

  // ----------------------------------------------------------------------------------------------
  // eslint-disable-next-line class-methods-use-this
  onTargetVisibilityChanged(): void {
    // TODO: needed?
  }

  // ----------------------------------------------------------------------------------------------
  setEnabled(isEnabled: boolean): this {
    if (isEnabled === this._enabled) {
      return (this);
    }

    this._enabled = isEnabled;

    const { target } = this;
    if (isEnabled) {
      target.cursor = 'pointer';
      // TODO: make CjsEvents.ROLL_OVER and ROLL_OUT
      target.addEventListener('rollover', this.onTargetRollOver);
      target.addEventListener('rollout', this.onTargetRollOut);
      target.addEventListener('mousedown', this.onTargetMouseDown);
      target.addEventListener('pressup', this.onTargetPressUp);
    } else {
      target.cursor = null as unknown as string;
      target.removeEventListener('rollover', this.onTargetRollOver);
      target.removeEventListener('rollout', this.onTargetRollOut);
      target.removeEventListener('mousedown', this.onTargetMouseDown);
      target.removeEventListener('pressup', this.onTargetPressUp);
    }

    if (this.enabled) {
      this.updateEnabled();
    } else {
      this.updateDisabled();
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get selected(): boolean {
    return (this._isSelected);
  }

  // ----------------------------------------------------------------------------------------------
  set selected(value: boolean) {
    this.setSelected(value);
  }

  // ----------------------------------------------------------------------------------------------
  setSelected(isSelected: boolean): this {
    if (!this.enabled) {
      return (this);
    }

    if (isSelected === this._isSelected) {
      return (this);
    }

    this._isSelected = isSelected;

    if (this._isOver) {
      this.target.gotoAndStop(this.overLabel);
    } else if (this._isDown) {
      this.target.gotoAndStop(this.downLabel);
    } else {
      this.target.gotoAndStop(this.getActingOutFrameLabel());
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setShouldUseDisabledState(should: boolean): this {
    if (should !== this._shouldUseDisabledState) {
      this._shouldUseDisabledState = should;
      this.update();
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get shouldUseDisabledState(): boolean {
    return (this._shouldUseDisabledState);
  }

  // ----------------------------------------------------------------------------------------------
  set shouldUseDisabledState(value: boolean) {
    this.setShouldUseDisabledState(value);
  }

  // ----------------------------------------------------------------------------------------------
  update(): void {
    if (this.enabled) {
      this.updateEnabled();
    } else {
      this.updateDisabled();
    }
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private getActingOutFrameLabel(): FrameLabel {
    const keyLabel = this.selected ? this.selectedLabel : this.outLabel;
    const actingOutLabel = this.labelsByName[keyLabel];
    return (actingOutLabel !== undefined ? actingOutLabel : this.outLabel);
  }

  // ----------------------------------------------------------------------------------------------
  private initHitArea(hitArea: DisplayObject | undefined, hitLabel: FrameLabel | undefined): void {
    if (hitArea instanceof createjs.MovieClip) {
      this.initHitAreaUsingCustomArea(hitArea, hitLabel);
    } else {
      this.initHitAreaUsingDefaultArea();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initHitAreaUsingCustomArea(hitArea: createjs.MovieClip,
    hitLabel: FrameLabel | undefined): void {
    if (hitLabel) {
      hitArea.actionsEnabled = false;
      hitArea.gotoAndStop(hitLabel);
    }

    this.target.hitArea = hitArea;
  }

  // ----------------------------------------------------------------------------------------------
  private initHitAreaUsingDefaultArea(): void {
    const { target } = this;
    const labelsByName = CjsGetMovieClipLabelsByName(target);
    const hitFrame = labelsByName[CjsButtonLabels.Hit];
    if (hitFrame === undefined) {
      return;
    }

    const prevFrame = target.currentFrame;
    target.gotoAndStop(hitFrame);
    const childHitDisplay = target.children[0];
    if (childHitDisplay) {
      if (childHitDisplay instanceof createjs.MovieClip) {
        childHitDisplay.actionsEnabled = false;
      }

      target.hitArea = childHitDisplay;
    }

    target.gotoAndStop(prevFrame);
  }

  // ----------------------------------------------------------------------------------------------
  private onTargetMouseDown(): void {
    this._isDown = true;
    this.target.gotoAndStop(this.downLabel);
  }

  // ----------------------------------------------------------------------------------------------
  private onTargetPressUp(): void {
    this._isDown = false;
    this.target.gotoAndStop(this._isOver ? this.overLabel : this.getActingOutFrameLabel());
  }

  // ----------------------------------------------------------------------------------------------
  private onTargetRollOver(): void {
    this._isOver = true;
    CjsButtonsOverHelperKludge.add(this.target);
    this.target.gotoAndStop(this._isDown ? this.downLabel : this.overLabel);
  }

  // ----------------------------------------------------------------------------------------------
  private onTargetRollOut(): void {
    this._isOver = false;
    CjsButtonsOverHelperKludge.delete(this.target);
    this.target.gotoAndStop(this._isDown ? this.overLabel : this.getActingOutFrameLabel());
  }

  // ----------------------------------------------------------------------------------------------
  private updateDisabled(): void {
    this._isOver = false;
    this._isDown = false;
    this.target.gotoAndStop(
      this.shouldUseDisabledState ? CjsButtonLabels.Disabled : this.getActingOutFrameLabel(),
    );
  }

  // ----------------------------------------------------------------------------------------------
  private updateEnabled(): void {
    this._isOver = CjsButtonsOverHelperKludge.has(this.target);
    this._isDown = false;
    this.target.gotoAndStop(this._isOver ? CjsButtonLabels.Over : this.getActingOutFrameLabel());
  }
}
