import { SbHiliteIncorrectConfig } from './utils/SbHiliteIncorrectConfig';
import SbButtonsMenuButtonCallback from './SbButtonsMenuButtonCallback';

type SbButtonsMenuButtonConfig = {
  callback?: SbButtonsMenuButtonCallback;
  callbackContext?: unknown;
  incorrectConfig?: SbHiliteIncorrectConfig;
  isCorrect?: boolean | string;
  isDisabled?: boolean;
  isHidden?: boolean;
};

function SbResolveButtonsMenuButtonConfig(
  buttonConfig: SbButtonsMenuButtonConfig | undefined,
): SbButtonsMenuButtonConfig {
  const sourceButtonConfig: SbButtonsMenuButtonConfig = buttonConfig
    ?? {} as SbButtonsMenuButtonConfig;

  const config = {
    callback: sourceButtonConfig.callback,
    callbackContext: sourceButtonConfig.callbackContext,
    incorrectConfig: sourceButtonConfig.incorrectConfig,
    isCorrect: sourceButtonConfig.isCorrect,
    isDisabled: sourceButtonConfig.isDisabled,
    isHidden: sourceButtonConfig.isHidden,
  };

  return (config);
}

export {
  SbButtonsMenuButtonConfig,
  SbResolveButtonsMenuButtonConfig,
};
