import RandomIntegerInclusive from '../../../../utils/math/RandomIntegerInclusive';
import ChallengeData from './ChallengeData';

// ------------------------------------------------------------------------------------------------
function GenerateChallenge(): ChallengeData {
  const sum = RandomIntegerInclusive(1, 9);
  const value1 = RandomIntegerInclusive(0, sum);
  const value2 = sum - value1;
  const expr1 = `${value1} + ${value2}`;
  return ({
    expression: expr1,
    value: sum,
  });
}

export default GenerateChallenge;
