function removeExtension(filename: string): string {
  const lastDotIndex = filename.lastIndexOf('.');
  return (lastDotIndex === -1 ? filename : filename.substring(0, lastDotIndex));
}

// ------------------------------------------------------------------------------------------------
function getLastPath(filename: string): string {
  const lastDotIndex = filename.lastIndexOf('/');
  return (lastDotIndex === -1 ? filename : filename.substring(lastDotIndex + 1));
}

// ------------------------------------------------------------------------------------------------
function validateResourceName(name: string): string {
  // Remove invalid characters from the name
  let validName = removeExtension(name);
  validName = getLastPath(validName);
  validName = validName.replace(/[^a-zA-Z0-9_]/g, '');

  if (/^\d/.test(validName)) {
    // Prepend with underscore if starts with a number
    validName = `_${validName}`;
  }

  return (validName);
}

// ------------------------------------------------------------------------------------------------
function ValidateResourceNames<T extends string | string[]>(names: T): T {
  if (typeof (names) === 'string') {
    return (validateResourceName(names) as T);
  }

  const out: string[] = [];
  names.forEach((name) => {
    out.push(validateResourceName(name));
  });

  return (out as T);
}

export default ValidateResourceNames;
