import IMovieClipDisplayCtor from '../../../adobeAnimate/IMovieClipDisplayCtor';
import CjsGetButton from '../CjsGetButton';
import CjsButton from './CjsButton';
import ICjsCheckbox from './ICjsCheckbox';
import CjsButtonEvents from './CjsButtonEvents';
import CjsCheckboxCompNames from './CjsCheckboxCompNames';
import CjsDefaultCheckboxDisplayType from './CjsDefaultCheckboxDisplayType';
import CjsDefaultButtonDisplayType from './CjsDefaultButtonDisplayType';
import CjsCompoundButton from './CjsCompoundButton';

export default class CjsCheckbox<
  CheckboxDisplayType extends CjsDefaultCheckboxDisplayType = CjsDefaultCheckboxDisplayType,
  ButtonDisplayType extends CjsDefaultButtonDisplayType = CjsDefaultButtonDisplayType
> extends CjsCompoundButton<CheckboxDisplayType, ButtonDisplayType>
  implements ICjsCheckbox<CheckboxDisplayType> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private _offButton: CjsButton<ButtonDisplayType> | undefined;

  private _onButton: CjsButton<ButtonDisplayType> | undefined;

  private _isOn!: boolean;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(display: CheckboxDisplayType, isOn = false,
    hitAreaOrAnLibClipCtor: createjs.DisplayObject | IMovieClipDisplayCtor
    | undefined = undefined, onDisplayName = CjsCheckboxCompNames.OnButton,
    offDisplayName = CjsCheckboxCompNames.OffButton) {
    super(display, hitAreaOrAnLibClipCtor, [onDisplayName, offDisplayName]);
    this._onButton = this.getButtonByName(onDisplayName) as CjsButton<ButtonDisplayType>;
    this._offButton = this.getButtonByName(offDisplayName) as CjsButton<ButtonDisplayType>;
    this.isOn = isOn;
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();

    this._offButton?.dispose();
    this._offButton = undefined;

    this._onButton?.dispose();
    this._onButton = undefined;
  }

  // ----------------------------------------------------------------------------------------------
  override get isDown(): boolean {
    return (this.activeButton?.isDown ?? false);
  }

  // ----------------------------------------------------------------------------------------------
  get isOn(): boolean {
    return (this._isOn);
  }

  // ----------------------------------------------------------------------------------------------
  set isOn(value: boolean) {
    this.setOn(value);
  }

  // ----------------------------------------------------------------------------------------------
  override get isOver(): boolean {
    return (this.activeButton?.isOver ?? false);
  }

  // ----------------------------------------------------------------------------------------------
  get offButton(): CjsButton<ButtonDisplayType> | undefined {
    return (this._offButton);
  }

  // ----------------------------------------------------------------------------------------------
  get onButton(): CjsButton<ButtonDisplayType> | undefined {
    return (this._onButton);
  }

  // ----------------------------------------------------------------------------------------------
  setOn(isOn: boolean): this {
    if (isOn === this._isOn) {
      return (this);
    }

    this.activeButton?.setVisible(false);
    this._isOn = isOn;
    this.activeButton?.setVisible(true);
    return (this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected override onInitButtons(
    _container: CheckboxDisplayType,
    buttonDisplayNames: string[],
    hitAreaOrAnLibClipCtor: createjs.DisplayObject | IMovieClipDisplayCtor | undefined,
  ): (CjsButton<ButtonDisplayType> | undefined)[] | undefined {
    const [onDisplayName, offDisplayName] = buttonDisplayNames;
    let onButton: CjsButton<ButtonDisplayType> | undefined;
    let offButton: CjsButton<ButtonDisplayType> | undefined;

    if (onDisplayName) {
      onButton = this.initStateButton(onDisplayName, hitAreaOrAnLibClipCtor);
    }

    if (offDisplayName) {
      offButton = this.initStateButton(offDisplayName, hitAreaOrAnLibClipCtor);
    }

    return ([
      onButton,
      offButton,
    ]);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private get activeButton(): CjsButton | undefined {
    return (this.isOn ? this._onButton : this._offButton);
  }

  // ----------------------------------------------------------------------------------------------
  private initStateButton(
    displayName: string,
    HitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  ): CjsButton<ButtonDisplayType> | undefined {
    const button = CjsGetButton<ButtonDisplayType>(this.display, displayName, this.onButtonPressed,
      HitAreaOrAnLibClipCtor, this);

    if (!button) {
      console.warn(`no state button display found with name: ${displayName}`);
    }

    return (button?.setVisible(false));
  }

  // ----------------------------------------------------------------------------------------------
  private onButtonPressed(): void {
    this.isOn = !this._isOn;
    this.emit(CjsButtonEvents.PRESSED, this);
  }
}
