import CloneArray from '../collections/arrays/CloneArray';
import DataObject from './DataObject';
import IsPlainObject from './IsPlainObject';
import RecursiveReadonly from './RecursiveReadonly';

function CloneObject<T extends DataObject>(source: RecursiveReadonly<T>): T {
  const out: DataObject = {};

  Object.keys(source).forEach((key) => {
    const value = source[key];
    if (Array.isArray(value)) {
      out[key] = CloneArray(value);
    } else if (IsPlainObject(value)) {
      out[key] = CloneObject(value);
    } else {
      out[key] = value;
    }
  });

  return (out as T);
}

export default CloneObject;
