import DataObject from '../object/DataObject';
import IsPlainObject from '../object/IsPlainObject';

function ParseStringToDataObject<D extends DataObject>(rawData: string): D | undefined {
  let jData: D | undefined;
  try {
    jData = JSON.parse(rawData);
    if (!IsPlainObject(jData)) {
      return (undefined);
    }
  } catch (error) {
    console.warn(error);
    return (undefined);
  }

  return (jData);
}

export default ParseStringToDataObject;
