import CloneObject from '../../../../../utils/object/CloneObject';
import IsPlainObject from '../../../../../utils/object/IsPlainObject';
import ParseBoolean from '../../../../../utils/parsers/ParseBoolean';
import ParseNumber from '../../../../../utils/parsers/ParseNumber';
import SbDefaults from '../../../defaults/SbDefaults';
import SbUserAudioSettingData from '../../audio/SbUserAudioSettingData';
import SbUserAudioSettingsData from '../../audio/SbUserAudioSettingsData';

function parseBgm(source: unknown, defaultData: SbUserAudioSettingData): SbUserAudioSettingData {
  return (
    IsPlainObject(source)
      ? {
        isEnabled: ParseBoolean(source.isEnabled, defaultData.isEnabled),
        volume: ParseNumber(source.volume, defaultData.volume, 0.0, 1.0),
      }
      : defaultData
  );
}

// ------------------------------------------------------------------------------------------------
function SbParseAudioSettings(source: unknown): SbUserAudioSettingsData {
  const target = CloneObject(SbDefaults.Settings.audio);
  if (!IsPlainObject(source)) {
    return (target);
  }

  target.bgm = parseBgm(source.bgm, target.bgm);
  target.sound = parseBgm(source.sound, target.sound);
  target.voice = parseBgm(source.voice, target.voice);
  return (target);
}

export default SbParseAudioSettings;
