import deepEqual from 'deep-equal';
import IsPlainObject from '../../../../../utils/object/IsPlainObject';
import ParseBoolean from '../../../../../utils/parsers/ParseBoolean';
import ParseStringToDataObject from '../../../../../utils/parsers/ParseStringToDataObject';
import SbParseAudioSettings from './SbParseAudioSettings';
import SbParseDeviceSettings from './SbParseDeviceSettings';
import SbParseStorybookSettings from './SbParseStorybookSettings';
import SbUserSettingsData from '../../SbUserSettingsData';
import SbParseUserStorybooksStates from './SbParseUserStorybooksStates';

export type SbSettingsDataOutput = {
  target: SbUserSettingsData
  wasSourceMirrored: boolean;
};

function getSourceObject(source: unknown): SbUserSettingsData | undefined {
  if (IsPlainObject(source)) {
    return (source as SbUserSettingsData);
  }

  return (typeof (source) === 'string'
    ? ParseStringToDataObject(source)
    : undefined);
}

// ------------------------------------------------------------------------------------------------
function SbUserSettingsDataParser(source: unknown): SbSettingsDataOutput {
  const sourceObject = getSourceObject(source);
  const target: SbUserSettingsData = {
    audio: SbParseAudioSettings(sourceObject?.audio),
    device: SbParseDeviceSettings(sourceObject?.device),
    hasCompletedTutorial: ParseBoolean(sourceObject?.hasCompletedTutorial),
    storybook: SbParseStorybookSettings(sourceObject?.storybook),
    storybooksStates: SbParseUserStorybooksStates(sourceObject?.storybooksStates),
  };

  return ({
    target,
    wasSourceMirrored: deepEqual(target, sourceObject, {
      strict: true,
    }),
  });
}

export default SbUserSettingsDataParser;
