import Wait from '../../../utils/timers/Wait';
import IStorybookEngine from '../../IStorybookEngine';
import SbDesignData from '../../data/SbDesignData';
import SbScene from '../../storybookScene/SbScene';
import SbUiComponent from '../SbUiComponent';
import SbUiComponentEvents from '../events/SbUiComponentEvents';

const FrameLabels = {
  PlayIntroSound: 'play_intro_sound',
};

export default class SbTiggieIntro extends SbUiComponent {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly scene: SbScene;

  private isSoundResolved: boolean;

  private isSceneResolved: boolean;

  private soundInstance: createjs.AbstractSoundInstance | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: createjs.MovieClip, engine: IStorybookEngine) {
    super(container, engine);

    this.scene = new SbScene(engine, container);
    this.scene.addFrameCallback(FrameLabels.PlayIntroSound, this.onPlayIntroSound, this);
    this.scene.addFrameCallback(this.scene.totalFrames - 1, this.onComplete, this);
    this.isSceneResolved = false;
    this.isSoundResolved = false;
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    this.stopSound();
    this.isSceneResolved = false;
    this.isSoundResolved = false;
    this.scene.dispose();
    super.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  present(): this {
    this.stopSound();
    this.isSceneResolved = false;
    this.isSoundResolved = false;
    this.scene.playTo(this.scene.totalFrames - 1, 0);
    this.visible = true;
    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private async continueIfAllAreResolved(): Promise<void> {
    if (this.isSceneResolved && this.isSoundResolved) {
      await Wait(SbDesignData.IntroCompleteWaitDelayMs);
      this.emit(SbUiComponentEvents.CLOSE, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onPlayIntroSound(): void {
    this.soundInstance = this.engine.playSound({
      audioId: this.engine.lib.audioIds.Logo,
      completeCallback: () => {
        this.resolveSound();
      },
    });

    if (!this.soundInstance) {
      this.resolveSound();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onComplete(): void {
    this.resolveScene();
  }

  // ----------------------------------------------------------------------------------------------
  private resolveScene(): void {
    this.isSceneResolved = true;
    this.continueIfAllAreResolved();
  }

  // ----------------------------------------------------------------------------------------------
  private resolveSound(): void {
    this.isSoundResolved = true;
    this.continueIfAllAreResolved();
  }

  // ----------------------------------------------------------------------------------------------
  private stopSound(): void {
    this.soundInstance?.destroy();
    this.soundInstance = undefined;
  }
}
