import IsPlainObject from '../../../../utils/object/IsPlainObject';
import { SbNavAction } from '../../../config/nav/SbNavActions';

type SbNavToParam = {
  action?: SbNavAction;
  callback?: () => void;
  context?: unknown;
  frame?: number | string;
  endFrame?: number | string;
};

export function SbIsNavToParam(data: unknown): data is SbNavToParam {
  return (IsPlainObject(data)
    && (
      'action' in data
      || 'callback' in data
      || 'frame' in data
    ));
}

export default SbNavToParam;
