import IMovieClipDisplayCtor from '../../utils/adobeAnimate/IMovieClipDisplayCtor';

interface ISbAnCompLibClasses {
  CloseXButtonHitBoxDisplay: IMovieClipDisplayCtor;
  MessagePanelCloseDisplay: IMovieClipDisplayCtor;
  MessagePanelYNDisplay: IMovieClipDisplayCtor;
  NextButtonDisplay: IMovieClipDisplayCtor;
  PasscodeButtonBackDisplay: IMovieClipDisplayCtor;
  PrevtButtonDisplay: IMovieClipDisplayCtor;
  RadioButtonHitBoxDisplay: IMovieClipDisplayCtor;
  RestartButtonDisplay: IMovieClipDisplayCtor;
  SettingsButtonDisplay: IMovieClipDisplayCtor;
  TapMouseButtonStateDisplay: IMovieClipDisplayCtor;
  TutorialNextNavButtonHitDisplay: IMovieClipDisplayCtor;
  TutorialSkipNavButtonHitDisplay: IMovieClipDisplayCtor;
}

interface ISbAnLibManifestEntry {
  id: string
  src: string;
}

interface ISbAnCompLibProperties {
  color: string;
  fps: number;
  height: number;
  id: string;
  manifest: ISbAnLibManifestEntry[];
  opacity: number;
  width: number;
}

interface ISbAnCompLibHeader {
  contentVideo?: HTMLVideoElement;
  properties: ISbAnCompLibProperties;
}

interface ISbAnCompLib extends ISbAnCompLibHeader, ISbAnCompLibClasses {}

const SbSoundTypeIds = {
  Bgm: 'bgm',
  Sound: 'snd',
  Voice: 'voc',
} as const;

type SbSoundTypeId = typeof SbSoundTypeIds[keyof typeof SbSoundTypeIds];

const SbAudioIds = {
  BigButtonPres: 'sndbigbuttonpress',
  Logo: 'sndlogo',
  RadioHeavyButtonPress: 'sndradioheavybuttonpress',
  RadioLightButtonPress: 'sndradiolightbuttonpress',
  SyncTest: 'sndsynctest',
  VolumeTest: 'sndvolumetestwav',
} as const;

type SbAudioId = typeof SbAudioIds[keyof typeof SbAudioIds];

type SbAssetLib = {
  readonly animateClasses: ISbAnCompLibClasses;
  readonly audioIds: typeof SbAudioIds;
  readonly header: ISbAnCompLibHeader;
};

const SbFrameLabels = {
  SbInit: 'sbinit',
  Start: 'start',
} as const;

export {
  ISbAnCompLib,
  ISbAnCompLibClasses,
  ISbAnCompLibHeader,
  ISbAnCompLibProperties,
  ISbAnLibManifestEntry,
  SbAssetLib,
  SbAudioId,
  SbAudioIds,
  SbFrameLabels,
  SbSoundTypeId,
  SbSoundTypeIds,
};
