import IMovieClipDisplayCtor from '../../adobeAnimate/IMovieClipDisplayCtor';
import CjsGetMovieClip from './CjsGetMovieClip';
import CjsButton from './buttons/CjsButton';
import CjsButtonCallback from './buttons/CjsButtonCallback';
import CjsButtonEvents from './buttons/CjsButtonEvents';
import CjsDefaultButtonDisplayType from './buttons/CjsDefaultButtonDisplayType';

export default function CjsGetButton<
  ButtonDisplayType extends CjsDefaultButtonDisplayType = CjsDefaultButtonDisplayType
>(container: createjs.Container,
  movieClipDisplayOrName: string | ButtonDisplayType, callback?: CjsButtonCallback,
  hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  context?: unknown): CjsButton<ButtonDisplayType> | undefined {
  const display = typeof (movieClipDisplayOrName) === 'string'
    ? CjsGetMovieClip(container, movieClipDisplayOrName)
    : movieClipDisplayOrName;

  const button = display
    ? new CjsButton(display as ButtonDisplayType, hitAreaOrAnLibClipCtor)
    : undefined;

  if (button && callback) {
    button.addListener(CjsButtonEvents.PRESSED, callback, context);
  }

  return (button);
}
