import CjsButtonEvents from '../../../../../utils/createjs/objects/buttons/CjsButtonEvents';
import CjsCheckbox from '../../../../../utils/createjs/objects/buttons/CjsCheckbox';
import ICjsBaseButton from '../../../../../utils/createjs/objects/buttons/ICjsBaseButton';
import ICjsCheckbox from '../../../../../utils/createjs/objects/buttons/ICjsCheckbox';
import CjsCreateSlider from '../../../../../utils/createjs/objects/sliders/CjsCreateSlider';
import CjsSlider from '../../../../../utils/createjs/objects/sliders/CjsSlider';
import CjsSliderEvents from '../../../../../utils/createjs/objects/sliders/CjsSliderEvents';
import ICjsSlider from '../../../../../utils/createjs/objects/sliders/ICjsSlider';
import { SbAssetLib } from '../../../../data/SbAssetLib';
import SbUserAudioSettingsData from '../../../../data/settings/audio/SbUserAudioSettingsData';
import {
  SbUserAudioSettingsDataKey,
  SbUserAudioSettingsDataKeys,
  SbUserAudioSettingsDataKeysArray,
} from '../../../../data/settings/audio/SbUserAudioSettingsDataKeys';

import SbUiComponent from '../../../SbUiComponent';
import SbSettingsSubPanel from '../SbSettingsSubPanel';

type SliderCheckboxPair = {
  checkbox: CjsCheckbox | undefined;
  slider: CjsSlider | undefined;
};

type SliderCheckBoxPairs = {
  [K in keyof SbUserAudioSettingsData]: SliderCheckboxPair;
};

export default class SbSettingsAudioSubPanel extends SbSettingsSubPanel<SbUserAudioSettingsData> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly sliderCheckboxPairs: SliderCheckBoxPairs;

  private settingDataKeysByUiControl: Map<ICjsCheckbox | ICjsSlider, SbUserAudioSettingsDataKey>;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(mainPanel: SbUiComponent, lib: Readonly<SbAssetLib>) {
    super(mainPanel, lib);

    this.settingDataKeysByUiControl = new Map();
    this.sliderCheckboxPairs = this.initSliderCheckboxPairs();

    this.initSoundSlider();
    this.initVoiceSlider();
    this.initBgmSlider();
    this.initBgmCheckbox();
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected updateSettingsUiFromData(): void {
    const { settings } = this;
    if (!settings) {
      return;
    }

    SbUserAudioSettingsDataKeysArray.forEach((key) => {
      const setting = settings[key];
      const isSettingEnabled = setting.isEnabled;
      const uiPair = this.sliderCheckboxPairs[key];
      uiPair.checkbox?.setOn(isSettingEnabled);
      uiPair.slider?.setValue(isSettingEnabled ? setting.volume : 0);
    });
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private getCheckbox(key: SbUserAudioSettingsDataKey): CjsCheckbox | undefined {
    return (this.sliderCheckboxPairs[key].checkbox);
  }

  // ----------------------------------------------------------------------------------------------
  private getSlider(key: SbUserAudioSettingsDataKey): CjsSlider | undefined {
    return (this.sliderCheckboxPairs[key].slider);
  }

  // ----------------------------------------------------------------------------------------------
  private initBgmCheckbox(): void {
    const checkbox = this.getCheckbox(SbUserAudioSettingsDataKeys.Bgm);
    if (checkbox) {
      checkbox.on(CjsButtonEvents.PRESSED, this.onBgmCheckboxPressed, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initBgmSlider(): void {
    const slider = this.getSlider(SbUserAudioSettingsDataKeys.Bgm);
    if (slider) {
      slider.on(CjsSliderEvents.CHANGED, this.onBgmSliderChanged, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initSliderCheckboxPairs(): SliderCheckBoxPairs {
    const { container } = this.mainPanel;
    const pairs: SliderCheckBoxPairs = {} as SliderCheckBoxPairs;
    SbUserAudioSettingsDataKeysArray.forEach((key) => {
      const checkbox = this.mainPanel.initCheckbox(`${key}CheckboxDisplay`, this.onCheckboxPressed,
        false, container, this);

      const slider = CjsCreateSlider(container, `${key}SliderDisplay`, this.onSliderChanged, this);
      pairs[key] = {
        checkbox,
        slider,
      };

      if (checkbox) {
        this.settingDataKeysByUiControl.set(checkbox, key);
      }

      if (slider) {
        this.settingDataKeysByUiControl.set(slider, key);
      }
    });

    return (pairs);
  }

  // ----------------------------------------------------------------------------------------------
  private initSoundSlider(): void {
    const slider = this.getSlider(SbUserAudioSettingsDataKeys.Sound);
    if (slider) {
      slider.on(CjsSliderEvents.DRAG_STOPPED, this.onSoundSliderDragStopped, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initVoiceSlider(): void {
    const slider = this.getSlider(SbUserAudioSettingsDataKeys.Voice);
    if (slider) {
      slider.on(CjsSliderEvents.DRAG_STOPPED, this.onVoiceSliderDragStopped, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onBgmCheckboxPressed(): void {
    this.updateBgmVolume();
  }

  // ----------------------------------------------------------------------------------------------
  private onBgmSliderChanged(): void {
    this.updateBgmVolume();
  }

  // ----------------------------------------------------------------------------------------------
  private onCheckboxPressed(button: ICjsBaseButton): void {
    const checkbox = button as CjsCheckbox;
    const settingKey = this.settingDataKeysByUiControl.get(checkbox);
    if (!settingKey || !this.settings) {
      return;
    }

    const setting = this.settings[settingKey];
    this.setSettingProp(setting, 'isEnabled', checkbox.isOn);

    const uiPair = this.sliderCheckboxPairs[settingKey];
    uiPair.slider?.setValue(checkbox.isOn ? setting.volume : 0, false);
  }

  // ----------------------------------------------------------------------------------------------
  private onSliderChanged(slider: ICjsSlider): void {
    const settingKey = this.settingDataKeysByUiControl.get(slider);
    if (!settingKey || !this.settings) {
      return;
    }

    const isEnabled = slider.value > 0;

    const uiPair = this.sliderCheckboxPairs[settingKey];
    uiPair.checkbox?.setOn(isEnabled);

    const setting = this.settings[settingKey];
    this.setSettingProp(setting, 'isEnabled', isEnabled);
    this.setSettingProp(setting, 'volume', slider.value);
  }

  // ----------------------------------------------------------------------------------------------
  private onSoundSliderDragStopped(): void {
    this.mainPanel.engine.playSound(this.lib.audioIds.VolumeTest, this.settings?.sound.volume);
  }

  // ----------------------------------------------------------------------------------------------
  private onVoiceSliderDragStopped(): void {
    this.mainPanel.engine.playSound(this.lib.audioIds.VolumeTest, this.settings?.voice.volume);
  }

  // ----------------------------------------------------------------------------------------------
  private updateBgmVolume(): void {
    if (this.settings) {
      this.mainPanel.engine.setBgmVolume(
        this.settings.bgm.isEnabled ? this.settings.bgm.volume : 0,
      );
    }
  }
}
