import EventEmitter from 'eventemitter3';
import IncludesIs from '../../collections/arrays/IncludesIs';
import CjsEventCallback from './CjsEventCallback';
import { CjsEvent, CjsEventsArray } from './CjsEvents';
import ICjsEventsMap from './ICjsEventsMap';

export default class CjsEventsHandler<
  TargetType extends createjs.EventDispatcher = createjs.EventDispatcher
> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly target: TargetType;

  private readonly eventsEmitter: EventEmitter;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(target: TargetType) {
    this.target = target;
    this.eventsEmitter = new EventEmitter();
    this.onTargetCallback = this.onTargetCallback.bind(this);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.removeAll();
  }

  // ----------------------------------------------------------------------------------------------
  emit(eventName: string, ...args: unknown[]): this {
    this.eventsEmitter.emit(eventName, args);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  off<E extends CjsEvent>(eventName: E, callback?: CjsEventCallback<ICjsEventsMap[E]>,
    context?: unknown, isOnce?: boolean): this {
    this.eventsEmitter.off(eventName, callback, context, isOnce);
    if (this.eventsEmitter.listenerCount(eventName) === 0) {
      this.target.removeEventListener(eventName, this.onTargetCallback);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  on<E extends CjsEvent>(eventName: E, callback: CjsEventCallback<ICjsEventsMap[E]>,
    context?: unknown, isOnce = false): this {
    if (this.eventsEmitter.listenerCount(eventName) === 0) {
      this.target.addEventListener(eventName, this.onTargetCallback);
    }

    if (isOnce) {
      this.eventsEmitter.once(eventName, callback, context);
    } else {
      this.eventsEmitter.on(eventName, callback, context);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  once<E extends CjsEvent>(eventName: E, callback: CjsEventCallback<ICjsEventsMap[E]>,
    context?: unknown): this {
    return (this.on(eventName, callback, context, true));
  }

  // ----------------------------------------------------------------------------------------------
  removeAll<E extends CjsEvent>(eventName?: E): this {
    const eventNames = this.eventsEmitter.eventNames();
    this.eventsEmitter.removeAllListeners(eventName);
    if (eventName) {
      this.target.removeEventListener(eventName, this.onTargetCallback);
    } else {
      eventNames.forEach((name) => {
        if (IncludesIs(name, CjsEventsArray)) {
          this.target.removeEventListener(name, this.onTargetCallback);
        }
      });
    }

    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onTargetCallback(e: unknown): void {
    const event = e as createjs.Event;
    if (IncludesIs(event.type, CjsEventsArray)) {
      this.eventsEmitter.emit(event.type, event, this);
    }
  }
}
