import { CjsEvents } from '../../utils/createjs/events/CjsEvents';
import SbPlaySound from '../audio/SbPlaySound';

type ResolveCallback = (value: void | PromiseLike<void>) => void;

type StateData = {
  currentIndex: number;
  resolve: ResolveCallback;
  soundIds: string[];
  volume?: number;
};

function playNextVoice(stateData: StateData): void {
  if (stateData.currentIndex >= stateData.soundIds.length) {
    stateData.resolve();
    return;
  }

  const soundId = stateData.soundIds[stateData.currentIndex];
  if (!soundId) {
    stateData.currentIndex += 1;
    playNextVoice(stateData);
    return;
  }

  const sound = SbPlaySound(soundId, stateData.volume);
  if (!sound) {
    stateData.currentIndex += 1;
    playNextVoice(stateData);
    return;
  }

  sound.on(CjsEvents.COMPLETE, () => {
    stateData.currentIndex += 1;
    playNextVoice(stateData);
  }, undefined, true);

  sound.on(CjsEvents.ERROR, () => {
    console.warn('failed to play sound:', sound);
    stateData.currentIndex += 1;
    playNextVoice(stateData);
  });
}

// ------------------------------------------------------------------------------------------------
function SbPlayVoice(soundIds: string | string[], volume?: number): Promise<void> {
  return (new Promise<void>((resolve) => {
    const stateData: StateData = {
      currentIndex: 0,
      resolve,
      soundIds: Array.isArray(soundIds) ? [...soundIds] : [soundIds],
      volume,
    };

    playNextVoice(stateData);
  }));
}

export default SbPlayVoice;
