import CjsGetDisplayObject from './CjsGetDisplayObject';
import CjsSetText from './CjsSetText';

export default function CjsGetText(container: createjs.Container,
  name: string, message?: string): createjs.Text | undefined {
  const text = CjsGetDisplayObject(container, name);
  if (!(text instanceof createjs.Text)) {
    return (undefined);
  }

  CjsSetText(text, message);
  return (text);
}
