const CjsEvents = {
  CHANGE: 'change',
  CLICK: 'click',
  COMPLETE: 'complete',
  ERROR: 'error',
  FILE_LOAD: 'fileload',
  MOUSE_DOWN: 'mousedown',
  PRESS_MOVE: 'pressmove',
  PRESS_UP: 'pressup',
  PROGRESS: 'progress',
  TICK: 'tick',
} as const;

type CjsEvent = typeof CjsEvents[keyof typeof CjsEvents];

type CjsBaseEventType = createjs.Event | createjs.ErrorEvent | createjs.ProgressEvent
| createjs.TickerEvent;

const CjsEventsArray = Object.values(CjsEvents) as ReadonlyArray<CjsEvent>;

export {
  CjsBaseEventType,
  CjsEvent,
  CjsEvents,
  CjsEventsArray,
};
