const DisplayObjectNames = {
  DevicesPanel: 'devicesPanelDisplay',
  MainPanel: 'mainPanelDisplay',
  PasscodePanel: 'passcodePanelDisplay',
  SettingsPanel: 'settingsPanelDisplay',
  TiggieIntro: 'tiggieIntroDisplay',

  // TODO
  // StartButton: 'startButtonDisplay',

  VideoContainer: 'videoContainer',
} as const;

export default class StorybookEngine extends EventEmitter implements IStorybookEngine {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly exportRoot: SbScene;

  readonly lib: Readonly<SbAssetLib>;

  readonly contentHandler: SbContentHandler;

  readonly startTimelineFrame: number;

  private readonly userPerformanceData: SbUserPerformanceData;

  private readonly contextLayers: SbContextLayers;

  private readonly userSettings: SbUserSettings;

  private readonly engineConfig: SbEngineConfig;

  private readonly pressIndicatorMgr: SbPressIndicatorManager;

  private mainPanel: SbMainPanel | undefined;

  private passcodePanel: SbPasscodePanel | undefined;

  private settingsPanel: SbSettingsPanel | undefined;

  private devicesPanel: SbDevicesPanel | undefined;

  private buttonsMenu: SbButtonsMenu | undefined;

  private tiggieIntro: SbTiggieIntro | undefined;

  private sceneHandler: SbBaseSceneHandler | undefined;

  private navParams: SbNavToParams | undefined;

  private bgmSoundInstance: createjs.AbstractSoundInstance | undefined;

  private previousSettingsData: SbUserSettingsData | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(exportRoot: createjs.MovieClip, lib: ISbAnCompLib) {
    super();

    this.exportRoot = new SbScene(this, exportRoot);
    this.lib = {
      animateClasses: lib,
      audioIds: SbAudioIds,
      header: lib,
    };

    this.exportRoot.display.framerate = this.lib.header.properties.fps;
    this.engineConfig = CloneObject(SbDefaults.EngineConfig);

    // CjsAnPlaySoundInit(this);

    this.userSettings = new SbUserSettings();

    this.startTimelineFrame = this.getStartTimelineFrame();
    this.contextLayers = new SbContextLayers(this);
    this.contentHandler = new SbContentHandler(this);
    this.pressIndicatorMgr = new SbPressIndicatorManager(this);
    this.userPerformanceData = new SbUserPerformanceData(this);

    this.initMainPanel()
      .initPasscodePanel()
      .initSettingsPanel()
      .initDevicesPanel()
      .initTiggieIntro();

    createjs.Ticker.on(CjsEvents.TICK, () => {
      this.exportRoot.stop(SbFrameLabels.SbInit);
    }, this, true);
  }

  // ----------------------------------------------------------------------------------------------
  get config(): Readonly<Required<SbEngineConfig>> {
    return (this.engineConfig as Required<SbEngineConfig>);
  }

  // ----------------------------------------------------------------------------------------------
  get contentVideoElement(): HTMLVideoElement | undefined {
    return (this.contentHandler.videoElement);
  }

  // ----------------------------------------------------------------------------------------------
  createMessagePanel(
    message: string,
    messagePanelDisplay: IMovieClipDisplayCtor,
    config?: SbRawMessagePanelConfig,
  ): Promise<SbMessagePanelButtonId> {
    return (SbMessagePanel.Create(this.exportRoot.stage, message, messagePanelDisplay, config));
  }

  // ----------------------------------------------------------------------------------------------
  async deleteProgress(storyBookId?: string): Promise<boolean> {
    storyBookId ??= this.config.storybookId;
    if (!storyBookId) {
      console.warn('no storybook id specified');
      return (false);
    }

    const { storybooksStates } = this.userSettings.data;
    if (!(storyBookId in storybooksStates)) {
      return (true);
    }

    delete storybooksStates[storyBookId];
    if (await this.saveSettings()) {
      console.info(`progress deleted for storybook id "${storyBookId}"`);
      return (true);
    }

    return (false);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    super.removeAllListeners();

    this.buttonsMenu?.dispose();
    this.buttonsMenu = undefined;

    this.contentHandler.dispose();

    this.mainPanel?.dispose();
    this.mainPanel = undefined;

    this.passcodePanel?.dispose();
    this.passcodePanel = undefined;

    this.settingsPanel?.dispose();
    this.settingsPanel = undefined;
  }

  // ----------------------------------------------------------------------------------------------
  init(config?: SbEngineConfig): this {
    SbResolveEngineConfig(config, this.lib, this.engineConfig);

    console.info('Storybook engine config: %o', this.engineConfig);

    this.initSceneHandler();

    if (this.engineConfig.storybookId) {
      this.deleteProgressUsingQueryParam(this.engineConfig.storybookId);
    }

    // TODO
    /*
    const startButton = CjsGetButton(this.exportRoot.display, DisplayObjectNames.StartButton,
      this.onStartButtonPressed, undefined, this)
      ?.setVisible(false);

    if (!startButton) {
      console.warn(`No start button found. One should exist on frame "${SbFrameLabels.SbInit}"
       of the main timeline named "${DisplayObjectNames.StartButton}"'.`);
    }

    if (this.engineConfig.contentVideoUrl) {
      this.contentHandler.loadContentVideo(this.engineConfig.contentVideoUrl);
      this.contentHandler.once(SbContentHandlerEvents.VIDEO_LOAD_COMPLETE, () => {
        startButton?.setVisible(true);
      });
    } else {
      if (this.lib.header.contentVideo) {
        this.contentHandler.setContentVideo(this.lib.header.contentVideo);
      }

      startButton?.setVisible(true);
    }
    */

    this.presentDevicesPanel();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  static LogVersion(): void {
    const config = [
      'background: blue',
      'color: yellow',
      'display: block',
    ].join(';');

    console.info(`%cStorybookEngine v: ${SbDesignData.Version}`, config);
  }

  // ----------------------------------------------------------------------------------------------
  playBgm(soundId: string, volume?: number): boolean {
    if (!soundId) {
      return (false);
    }

    this.stopBgm();
    volume = this.userSettingsData.audio.bgm.isEnabled
      ? this.resolveSettingsVolume(SbUserAudioSettingsDataKeys.Bgm, volume)
      : 0;

    this.bgmSoundInstance = SbPlaySound(soundId, volume, true);
    return (!!this.bgmSoundInstance);
  }

  // ----------------------------------------------------------------------------------------------
  playSound(config: SbPlaySoundConfig): createjs.AbstractSoundInstance | undefined;

  playSound(
    audioId: string,
    volume?: number,
    loop?: boolean | number,
    completeCallback?: SbPlaySoundCompleteCallback,
    callbackContext?: unknown,
  ): createjs.AbstractSoundInstance | undefined;

  playSound(
    audioIdOrConfig: string | SbPlaySoundConfig,
    volume: number | undefined = 1.0,
    loop: boolean | number | undefined = false,
    completeCallback: SbPlaySoundCompleteCallback | undefined = undefined,
    callbackContext: unknown = undefined,
  ): createjs.AbstractSoundInstance | undefined {
    if (!this.userSettingsData.audio.sound.isEnabled) {
      return (undefined);
    }

    const config: SbPlaySoundConfig = typeof (audioIdOrConfig) === 'string'
      ? {
        audioId: audioIdOrConfig,
        callbackContext,
        completeCallback,
        loop,
        volume,
      }

      : audioIdOrConfig;

    if (!config.audioId) {
      return (undefined);
    }

    config.volume = this.resolveSettingsVolume(SbUserAudioSettingsDataKeys.Sound, config.volume);
    return (SbPlaySound(config));
  }

  // ----------------------------------------------------------------------------------------------
  playVoice(soundIds: string | string[], volume?: number): Promise<void> {
    volume = this.resolveSettingsVolume(SbUserAudioSettingsDataKeys.Voice, volume);
    return (SbPlayVoice(soundIds, volume));
  }

  // ----------------------------------------------------------------------------------------------
  presentMenu(config?: SbButtonsMenuConfig): this {
    this.unpresentMenu();
    this.buttonsMenu = new SbButtonsMenu(this);
    this.buttonsMenu.on(SbButtonsMenuEvents.OPTION_SELECTED,
      this.onButtonsMenuOptionPressed, this);

    const configOut = config ?? {} as SbButtonsMenuConfig;
    if (this.config.buttonsMenu) {
      configOut.correctSoundKey ??= this.config.buttonsMenu.defaultCorrectSoundKey;
      configOut.incorrectSoundKey ??= this.config.buttonsMenu.defaultInorrectSoundKey;
    }

    this.buttonsMenu.start(configOut);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  presentNav(config?: SbPresentNavConfig | SbNavToParams): this {
    const _config = SbResolvePresentNavConfig(config);
    this.navParams = _config.params;

    // TODO
    // this.contentHandler?.pause(SbPauseKinds.IsFromNav);

    this.mainPanel?.presentNavUi(_config);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get progress(): number {
    return (this.mainPanel?.progressBar?.value ?? 0);
  }

  // ----------------------------------------------------------------------------------------------
  set progress(value: number) {
    if (this.mainPanel?.progressBar) {
      this.mainPanel.progressBar.value = value;
    }
  }

  // ----------------------------------------------------------------------------------------------
  resolveSettingsVolume(key: SbUserAudioSettingsDataKey, volume?: number): number {
    if (IsValidPositiveZeroNumber(volume)) {
      return (volume);
    }

    const settings = this.userSettings.data.audio[key];
    return (settings?.isEnabled ? settings.volume : 0);
  }

  // ----------------------------------------------------------------------------------------------
  async saveProgress(storyBookId?: string, frameOrLabel?: number | string): Promise<boolean> {
    storyBookId ??= this.config.storybookId;
    if (!storyBookId) {
      console.warn('no storybook id specified');
      return (false);
    }

    if (frameOrLabel !== undefined) {
      if (!this.exportRoot.isFrameValid(frameOrLabel)) {
        console.warn(`invalid progress frame or label specified: ${frameOrLabel}`);
        return (false);
      }
    } else {
      frameOrLabel = this.exportRoot.currentFrame;
    }

    const { storybooksStates } = this.userSettings.data;
    let state = storybooksStates[storyBookId];
    if (!state) {
      state = {} as SbUserStorybookState;
      storybooksStates[storyBookId] = state;
    }

    state.progresFrameOrLabel = frameOrLabel;
    if (await this.saveSettings()) {
      console.info(`progress saved for storybook id "${storyBookId}" @: ${frameOrLabel}`);
    }

    return (false);
  }

  // ----------------------------------------------------------------------------------------------
  async saveSettings(config?: SbSaveSettingsConfig): Promise<boolean> {
    return (SbSaveSettings(this, this.userSettings, config));
  }

  // ----------------------------------------------------------------------------------------------
  static SbDocPrep(): void {
    SbDocPrepUtil();
  }

  // ----------------------------------------------------------------------------------------------
  setBgmVolume(volume: number): this {
    if (this.bgmSoundInstance) {
      this.bgmSoundInstance.volume = volume;
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  startPressIndicator(delayMs?: number, container?: createjs.Container,
    indicatorDisplayName?: string): this {
    this.pressIndicatorMgr.start(delayMs, container, indicatorDisplayName);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  stopBgm(): this {
    this.bgmSoundInstance?.destroy();
    this.bgmSoundInstance = undefined;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  submitUserPerformanceData(): this {
    this.userPerformanceData.submit();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  unpresentMenu(): this {
    this.buttonsMenu?.dispose();
    this.buttonsMenu = undefined;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  updateFromSettings(): this {
    this.contextLayers.updateFromSettings();
    this.buttonsMenu?.updateFromSettings();
    this.updateBgmFromSettings();
    this.pressIndicatorMgr.updateBgmFromSettings();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get userSettingsData(): RecursiveReadonly<SbUserSettingsData> {
    return (this.userSettings.data);
  }

  // ----------------------------------------------------------------------------------------------
  get videoContainer(): createjs.Container | undefined {
    return (CjsGetContainer(this.exportRoot.display, DisplayObjectNames.VideoContainer));
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private closeSettingsPanel(): void {
    this.restorePreviousSettings();
    this.settingsPanel?.unpresent();
    this.mainPanel?.updateFromSettings();
    this.updateFromSettings();

    // TODO: maybe find a way to save/restore state of scene when opening/closing
    // blocking UI panels
    // this.contentHandler.resume(SbPauseKinds.IsFromUi);
  }

  // ----------------------------------------------------------------------------------------------
  private continueAftarTutorial(): void {
    this.updateFromSettings();
    this.presentContent();

    // TODO: remove if na
    // createjs.Ticker.on(CjsEvents.TICK, this.onTickerTick, this);
  }

  // ----------------------------------------------------------------------------------------------
  private deleteProgressUsingQueryParam(storybookId: string): void {
    if (GetWindowQueryParamValue(SbQueryParams.DeleteProgress) !== null) {
      this.deleteProgress(storybookId);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private static ExitRedirect(): void {
    const exitRedirectUrl = GetWindowQueryParamValue(SbQueryParams.ExitRedirect);
    window.location.replace(exitRedirectUrl || window.location.href);
  }

  // ----------------------------------------------------------------------------------------------
  private getStartTimelineFrame(): number {
    const targetStartLabel = SbDebugData.StartLabel ? SbDebugData.StartLabel : SbFrameLabels.Start;
    let startFrameNumber: number;
    if (typeof (targetStartLabel) === 'string') {
      const startLabel = CjsGetMovieClipLabels(this.exportRoot.display)
        .find((frameLabel) => frameLabel.label === targetStartLabel);
      startFrameNumber = startLabel?.position ?? this.exportRoot.currentFrame + 1;
    } else {
      startFrameNumber = targetStartLabel;
    }

    console.info('start frame number:', startFrameNumber);
    return (startFrameNumber);
  }

  // ----------------------------------------------------------------------------------------------
  private async handleNavAction(direction: SbNavToDirection,
    navParams: SbNavToParams | undefined): Promise<void> {
    this.mainPanel?.unpresentNavUi();

    await this.playNavButtonSound();

    if (navParams) {
      this.contentHandler.navTo(direction, navParams);
    } else {
      this.contentHandler.resume(SbPauseKinds.IsFromNav);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initDevicesPanel(): this {
    const container = CjsGetMovieClip(this.exportRoot.display, DisplayObjectNames.DevicesPanel);
    if (!container) {
      console.warn('no devices panel found with name:', DisplayObjectNames.DevicesPanel);
      return (this);
    }

    this.devicesPanel = new SbDevicesPanel(container, this)
      .addListener(SbUiComponentEvents.CLOSE, this.onDevicesPanelClose, this)
      .setVisible(false);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private initMainPanel(): this {
    const container = CjsGetMovieClip(this.exportRoot.display, DisplayObjectNames.MainPanel);
    if (!container) {
      console.warn('no main panel found with name:', DisplayObjectNames.MainPanel);
      return (this);
    }

    this.mainPanel = new SbMainPanel(container, this)
      .addListener(SbMainPanelEvents.NEXT, this.onMainPanelNextPressed, this)
      .addListener(SbMainPanelEvents.PREV, this.onMainPanelPrevPressed, this)
      .addListener(SbMainPanelEvents.RESTART, this.onMainPanelRestartPressed, this)
      .addListener(SbMainPanelEvents.SETTINGS, this.onMainPanelSettingsPressed, this)
      .setVisible(true)
      .unpresentNavUi();

    // TODO
    this.mainPanel.progressBar?.setVisible(false);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private initPasscodePanel(): this {
    const container = CjsGetMovieClip(this.exportRoot.display, DisplayObjectNames.PasscodePanel);
    if (!container) {
      console.warn('no passcode panel found with name:', DisplayObjectNames.PasscodePanel);
      return (this);
    }

    this.passcodePanel = new SbPasscodePanel(container, this)
      .addListener(SbUiComponentEvents.CLOSE, this.onPasscodePanelClose, this)
      .addListener(SbPasscodePanelEvents.SUBMIT, this.onPasscodePanelSubmit, this)
      .setVisible(false);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private initSceneHandler(): void {
    const HandlerCtor = this.config.sceneHandler;
    if (HandlerCtor) {
      this.sceneHandler = new HandlerCtor(this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initSettingsPanel(): this {
    const container = CjsGetMovieClip(this.exportRoot.display, DisplayObjectNames.SettingsPanel);
    if (!container) {
      console.warn('no settings panel found with name:', DisplayObjectNames.SettingsPanel);
      return (this);
    }

    this.settingsPanel = new SbSettingsPanel(container, this)
      .addListener(SbUiComponentEvents.CLOSE, this.onSettingsPanelClose, this)
      .addListener(SbSettingsPanelEvents.EXIT, this.onSettingsPanelExit, this)
      .addListener(SbSettingsPanelEvents.SAVE, this.onSettingsPanelSave, this)
      .setVisible(false);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private initTiggieIntro(): this {
    const container = CjsGetMovieClip(this.exportRoot.display, DisplayObjectNames.TiggieIntro);
    if (!container) {
      console.warn('no tiggie intro display found with name:', DisplayObjectNames.TiggieIntro);
      return (this);
    }

    this.tiggieIntro = new SbTiggieIntro(container, this)
      .addListener(SbUiComponentEvents.CLOSE, this.onTiggieIntroClose, this)
      .setVisible(false);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private onButtonsMenuOptionPressed(_buttonName: string, isCorrect: boolean,
    menu: SbButtonsMenu): void {
    if (isCorrect && menu.quizData) {
      this.userPerformanceData.addQuizData(menu.quizData);
      console.info('user performance data:', this.userPerformanceData);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onDevicesPanelClose(): void {
    if (this.devicesPanel) {
      this.devicesPanel.unpresent().dispose();
      this.devicesPanel = undefined;
    }

    if (!this.presentTutorial()) {
      this.presentContent();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onMainPanelNextPressed(): void {
    this.handleNavAction(SbNavToDirections.Next, this.navParams);
  }

  // ----------------------------------------------------------------------------------------------
  private onMainPanelPrevPressed(): void {
    this.handleNavAction(SbNavToDirections.Prev, this.navParams);
  }

  // ----------------------------------------------------------------------------------------------
  private onMainPanelRestartPressed(): void {
    this.handleNavAction(SbNavToDirections.Restart, this.navParams);
  }

  // ----------------------------------------------------------------------------------------------
  private onMainPanelSettingsPressed(): void {
    // TODO
    // this.contentHandler.pause(SbPauseKinds.IsFromUi);

    this.mainPanel?.setVisible(false);

    if (SbDesignData.AreSettingsProtected) {
      this.passcodePanel?.present();
    } else {
      this.presentSettingsPanel();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onPasscodePanelClose(): void {
    this.passcodePanel?.unpresent();
    this.mainPanel?.setVisible(true);

    // TODO
    // this.contentHandler.resume(SbPauseKinds.IsFromUi);
  }

  // ----------------------------------------------------------------------------------------------
  private onPasscodePanelSubmit(isCorrect: boolean): void {
    if (isCorrect) {
      this.mainPanel?.setVisible(false);
      this.passcodePanel?.setVisible(false);
      this.presentSettingsPanel();
    } else {
      this.passcodePanel?.clearAnswer();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onSettingsPanelClose(): void {
    this.closeSettingsPanel();
  }

  // ----------------------------------------------------------------------------------------------
  private onSettingsPanelExit(): void {
    this.createMessagePanel('Are you sure you want to exit the activity?',
      this.lib.animateClasses.MessagePanelYNDisplay)
      .then((id) => {
        if (id === SbMessagePanelButtonIds.Yes) {
          StorybookEngine.ExitRedirect();
        }
      });
  }

  // ----------------------------------------------------------------------------------------------
  private async onSettingsPanelSave(settingsPanel: SbSettingsPanel): Promise<void> {
    this.userSettings.apply(settingsPanel.settingsData);
    this.previousSettingsData = undefined;
    await this.saveSettings(SbSaveSettingsCommonConfig());
    this.closeSettingsPanel();
  }

  // ----------------------------------------------------------------------------------------------
  // TODO
  /*
  private onStartButtonPressed(): void {
    if (!this.presentTutorial()) {
      this.presentContent();
    }
  }
  */

  // ----------------------------------------------------------------------------------------------
  private onTiggieIntroClose(): void {
    this.tiggieIntro?.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  private async playNavButtonSound(): Promise<void> {
    return (
      new Promise<void>((resolve) => {
        if (this.config.shouldWaitForNavSounds) {
          if (!this.playSound({
            audioId: this.config.navButtonsSoundKey,
            completeCallback: () => {
              resolve();
            },
          })) {
            resolve();
          }
        } else {
          this.playSound(this.config.navButtonsSoundKey);
          resolve();
        }
      })
    );
  }

  // ----------------------------------------------------------------------------------------------
  private presentContent(): void {
    const { storybookId } = this.engineConfig;
    const storybookState = storybookId
      ? this.userSettings.data.storybooksStates[storybookId]
      : undefined;

    if (storybookState?.progresFrameOrLabel === undefined) {
      this.presentContent2();
      this.contentHandler.start();
    } else {
      this.createMessagePanel(
        'Would you like to resume form where you left off?',
        this.lib.animateClasses.MessagePanelYNDisplay,
        {
          buttonLabels: {
            [SbMessagePanelButtonIds.Yes]: 'Yes, Resume',
            [SbMessagePanelButtonIds.No]: 'No, Restart',
          },
        },
      ).then((id) => {
        let startFrame: number | string;
        if (id === SbMessagePanelButtonIds.Yes) {
          startFrame = storybookState.progresFrameOrLabel;
          if (typeof (startFrame) === 'number') {
            startFrame += 1;
          }
        } else {
          if (storybookId) {
            this.deleteProgress(storybookId);
          }

          startFrame = 0;
        }

        this.presentContent2(startFrame);
      });
    }
  }

  // ----------------------------------------------------------------------------------------------
  private presentContent2(startFrameOrLabel?: number | string): void {
    if (!SbDebugData.ShouldSkipIntro && this.tiggieIntro) {
      this.tiggieIntro.present();
      this.tiggieIntro.once(SbUiComponentEvents.CLOSE, () => {
        this.tiggieIntro?.dispose();
        this.tiggieIntro = undefined;
        this.presentContent3(startFrameOrLabel);
      });
    } else {
      this.presentContent3(startFrameOrLabel);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private presentContent3(startFrameOrLabel?: number | string): void {
    if (this.mainPanel) {
      this.mainPanel.settingsButtonVisible = true;
    }

    this.contentHandler.start(startFrameOrLabel);
    this.contextLayers.start();
    this.startSceneHandler();

    this.userPerformanceData.start();
  }

  // ----------------------------------------------------------------------------------------------
  private presentDevicesPanel(): void {
    if (this.devicesPanel) {
      if (this.mainPanel) {
        this.mainPanel.settingsButtonVisible = false;
      }

      this.devicesPanel.present();
    } else if (!this.presentTutorial()) {
      this.presentContent();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private presentSettingsPanel(): void {
    this.previousSettingsData = CloneObject(this.userSettings.data);
    this.settingsPanel?.present(this.userSettings.data);
  }

  // ----------------------------------------------------------------------------------------------
  private presentTutorial(): boolean {
    if (!this.mainPanel || !this.shouldShowTutorial() || !this.mainPanel.presentTutorial()) {
      return (false);
    }

    this.mainPanel.once(SbMainPanelEvents.TUTORIAL_COMPLETE, async (isCompleted: boolean) => {
      if (isCompleted) {
        this.userSettings.data.hasCompletedTutorial = true;
        await this.saveSettings();
      }

      this.mainPanel?.unpresentTutorial();
      this.continueAftarTutorial();
    });

    return (true);
  }

  // ----------------------------------------------------------------------------------------------
  private restorePreviousSettings(): void {
    if (this.previousSettingsData) {
      this.userSettings.apply(this.previousSettingsData);
      this.previousSettingsData = undefined;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private shouldShowTutorial(): boolean {
    return (!this.userSettingsData.hasCompletedTutorial);
  }

  // ----------------------------------------------------------------------------------------------
  private startSceneHandler(): void {
    this.sceneHandler?.run();
  }

  // ----------------------------------------------------------------------------------------------
  private updateBgmFromSettings(): void {
    if (this.bgmSoundInstance) {
      this.bgmSoundInstance.volume = this.userSettingsData.audio.bgm.isEnabled
        ? this.userSettings.data.audio.bgm.volume
        : 0;
    }
  }
}
