import { CjsIsMovieCip } from '../../../utils/createjs/objects/ObjectUtils';
import IsValidPositiveNumber from '../../../utils/number/IsValidPositiveNumber';
import Timer from '../../../utils/timers/Timer';

export default class SbPressIndicator {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly indicator: createjs.DisplayObject;

  private timer: Timer | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(indicator: createjs.DisplayObject, delayInSecs?: number) {
    this.indicator = indicator;

    if (IsValidPositiveNumber(delayInSecs)) {
      this.start(delayInSecs);
    }
  }

  // ----------------------------------------------------------------------------------------------
  start(delayMs: number): this {
    this.hide();
    this.startTimer(delayMs);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  stop(): this {
    this.hide();
    this.stopTimer();
    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private hide(): void {
    this.indicator.visible = false;
    if (CjsIsMovieCip(this.indicator)) {
      this.indicator.stop();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private show(): void {
    this.indicator.visible = true;
    if (CjsIsMovieCip(this.indicator)) {
      this.indicator.gotoAndPlay(0);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private startTimer(delayMs: number): void {
    this.stopTimer();
    this.timer = Timer.Create({
      callback: () => {
        this.show();
      },

      delayMs,
    });
  }

  // ----------------------------------------------------------------------------------------------
  private stopTimer(): void {
    this.timer?.stop();
    this.timer = undefined;
  }
}
