/* eslint-disable no-use-before-define */
import SbScene from '../../../../engine/storybookScene/SbScene';
import SbSetSceneSpeakerButton from '../../../../engine/storybookScene/utils/SbSceneSpeakerButton';
import { SbButtonsMenuConfig } from '../../../../engine/ui/buttonsMenu/config/SbButtonsMenuConfig';
import McBoyWearShoes from '../shoes/McBoyWearShoes';

function presentCompletedMenu(scene: SbScene): void {
  scene.engine.presentNav({
    next: {
      callback: () => {
        McBoyWearShoes(scene);
      },
    },

    prev: {
      callback: () => {
        McBoyHelpWearSocksQuiz(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyHelpWearSocksQuiz(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function beginCorrect(scene: SbScene): void {
  scene.playTo(
    'boy_help_wear_socks_correct_end',
    'boy_help_wear_socks_correct_begin',
    async () => {
      await scene.playSyncVoice(
        'SBL-0001_Well-done-Great-job_48.0k_stereo.mp3',
        'boy_help_wear_socks_correct_voc_begin',
        'boy_help_wear_socks_correct_voc_end',
      );

      presentCompletedMenu(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function buildMenuConfig(areButtonsEnabled: boolean, scene: SbScene): SbButtonsMenuConfig {
  const isDisabled = !areButtonsEnabled;
  return ({
    buttons: {
      boySocksSlotButton0Display: {
        isCorrect: false,
        isDisabled,
      },

      boySocksSlotButton1Display: {
        isCorrect: false,
        isDisabled,
      },

      boySocksSlotButton2Display: {
        isCorrect: true,
        isDisabled,
        callback: (): void => {
          beginCorrect(scene);
        },
      },
    },

    defaultButtonName: 'boySocksMenuButton0Display',
    isQuiz: true,
  });
}

// ----------------------------------------------------------------------------------------------
function presentMenu(scene: SbScene, areButtonsEnabled: boolean): void {
  scene.engine.presentMenu(buildMenuConfig(areButtonsEnabled, scene));
}

// ----------------------------------------------------------------------------------------------
export default function McBoyHelpWearSocksQuiz(scene: SbScene): void {
  const labelPfx = 'boy_help_wear_socks_quiz';
  const startLabel = `${labelPfx}_start`;
  const endLabel = `${labelPfx}_end`;

  scene.stop(startLabel);
  presentMenu(scene, false);

  const quizPromptVoiceSoundId = 'SBL-0005_Can-you-help-him-wear-his-sock_48.0k_stereo.mp3';
  SbSetSceneSpeakerButton(scene, false, quizPromptVoiceSoundId);

  scene.playSyncVoice(
    quizPromptVoiceSoundId,
    startLabel,
    endLabel,
  ).then(() => {
    SbSetSceneSpeakerButton(scene, true, quizPromptVoiceSoundId);
    presentMenu(scene, true);
  });
}
