/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import Wait from '../../../../utils/timers/Wait';
import McBoySelectShirtQuiz from './McBoySelectShirtQuiz';

async function shirtTips(scene: SbScene): Promise<void> {
  scene.stop('boy_wear_shirt_tips_menu');
  await Wait(2000);
  await scene.playVoices('SBL-0001_shirts_tips');
  scene.engine.presentNav({
    next: {
      callback: () => {
        scene.playTo(
          'boy_wear_shirt_tips_menu_closed_end',
          'boy_wear_shirt_tips_menu_closed_begin',
          () => {
            McBoySelectShirtQuiz(scene);
          },
        );
      },
    },

    prev: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyWearShirt(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_wear_shirt_how_aac_i',
        soundId: 'SBL-0007_I',
      },
      {
        frameOrLabel: 'boy_wear_shirt_how_aac_wear',
        soundId: 'SBL-0008_Wear',
      },
      {
        frameOrLabel: 'boy_wear_shirt_how_aac_shirt',
        soundId: 'SBL-0005_tee-shirt',
      },
      {
        frameOrLabel: 'boy_wear_shirt_how_aac_body',
        soundId: 'SBL-0006_body',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          shirtTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearShirt(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0004_I-wear-my-tee-shirt-over-my-body',
    'boy_wear_shirt_how_start',
    'boy_wear_shirt_how_end',
  ).then(() => {
    playHowVoiceAac(scene);
  });
}

// ------------------------------------------------------------------------------------------------
function playWearShirtAnimation(scene: SbScene): void {
  scene.playTo(
    'boy_wear_shirt_animation_end',
    'boy_wear_shirt_animation_start',
    () => {
      scene.engine.presentNav({
        next: {
          callback: () => {
            playHowVoice(scene);
          },
        },

        prev: {
          callback: () => {
            McBoyWearShirt(scene);
          },
        },

        restart: {
          callback: () => {
            McBoyWearShirt(scene);
          },
        },
      });
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearShirtHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      boyWearShirtMenuButtonDisplay: {
        callback: () => {
          playWearShirtAnimation(scene);
        },
      },
    },

    defaultButtonName: 'boyWearShirtMenuButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McBoyWearShirt(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL0003_Iwearmyteeshirt_480k_stereo',
    'boy_wear_shirt_start',
    'boy_wear_shirt_end',
  ).then(() => {
    wearShirtHow(scene);
  });
}
