import GetValue from './GetValue';

/**
 * Returns the string value of a property key within the top level of an object, or returns a
 * default value if the key is not found.
 *
 * @function getStringValue
 *
 * @param source - The object to search.
 * @param key - The key for the property on source. Must exist at the top level of the
 * source object (no periods).
 * @param [defaultValue=''] - The default value to use if the key does not exist.
 *
 * @returns The value if found; otherwise, `defaultValue` (`''` if none provided).
 */
export default function GetStringValue(source: unknown, key: string, defaultValue = ''): string {
  return (GetValue(source, key, defaultValue) ?? defaultValue);
}
