import SbScene from '../../../engine/storybookScene/SbScene';
import McGirlSelected from './McGirlSelected';
import McGirlLetsRead from './McGirlLetsRead';
import SbAacSceneHelper from '../../../engine/storybookScene/SbAacSceneHelper';
import McIntro from '../McIntro';

export default function McGirlSelectedAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_selected_aac_choose',
        soundId: 'SBL-0035_choose_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'girl_selected_aac_girl',
        soundId: 'SBL-0036_girl_48.0k_stereo.mp3',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McGirlLetsRead(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlSelected(scene);
        },
      },

      restart: {
        callback: () => {
          McIntro(scene);
        },
      },
    });
  });
}
