import CjsSetVisible from '../../../../utils/createjs/actions/CjsSetVisible';
import CjsGetButton from '../../../../utils/createjs/objects/CjsGetButton';
import CjsGetMovieClip from '../../../../utils/createjs/objects/CjsGetMovieClip';
import CjsGetText from '../../../../utils/createjs/objects/CjsGetText';
import CjsSetText from '../../../../utils/createjs/objects/CjsSetText';
import CjsButton from '../../../../utils/createjs/objects/buttons/CjsButton';
import CjsDisplayObject from '../../../../utils/createjs/objects/display/CjsDisplayObject';
import SbHelpfulTipsPanelEvents from './SbHelpfulTipsPanelEvents';

const DisplayObjectNames = {
  Bubble: 'bubble_display',
  Button: 'buttonDisplay',
  TextMessage: 'txtMessage',
} as const;

export default class SbHelpfulTipsPanel extends CjsDisplayObject<createjs.Container> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly button: CjsButton | undefined;

  private readonly bubble: createjs.MovieClip | undefined;

  private readonly txtMessage: createjs.Text | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(display: createjs.Container) {
    super(display);

    this.button = CjsGetButton(this.display, DisplayObjectNames.Button, this.onButtonPressed,
      undefined, this);

    this.bubble = CjsGetMovieClip(this.display, DisplayObjectNames.Bubble);
    this.txtMessage = CjsGetText(this.display, DisplayObjectNames.TextMessage, '');
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();
    this.button?.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  present(message = ''): this {
    this.visible = true;

    CjsSetVisible([this.bubble, this.txtMessage], message.length > 0);
    CjsSetText(this.txtMessage, message);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  unpresent(): this {
    this.visible = false;
    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onButtonPressed(): void {
    this.emit(SbHelpfulTipsPanelEvents.OPENED, this);
  }
}
