import Cookies from 'js-cookie';
import CloneArray from '../../../utils/collections/arrays/CloneArray';
import CloneObject from '../../../utils/object/CloneObject';
import IStorybookEngine from '../../IStorybookEngine';
import SbQuizData from './SbQuizData';

export default class SbUserPerformanceData {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  private readonly _quizzes: SbQuizData[];

  private sessionStartTime: number;

  private sessionDurationMs: number;

  private sbCookies: Cookies.CookiesStatic | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    this.engine = engine;
    this._quizzes = [];
    this.sessionStartTime = 0;
    this.sessionDurationMs = 0;
  }

  // ----------------------------------------------------------------------------------------------
  addQuizData(data: Readonly<SbQuizData>): void {
    this._quizzes.push(CloneObject(data));
  }

  // ----------------------------------------------------------------------------------------------
  get quizzes(): SbQuizData[] {
    return (CloneArray(this._quizzes));
  }

  // ----------------------------------------------------------------------------------------------
  removeAllQuizzes(): void {
    this._quizzes.length = 0;
  }

  // ----------------------------------------------------------------------------------------------
  start(): void {
    this.sessionStartTime = Date.now();
    this.sessionDurationMs = 0;
  }

  // ----------------------------------------------------------------------------------------------
  stop(): void {
    if (this.sessionStartTime === 0) {
      return;
    }

    const stopTime = Date.now();
    this.sessionDurationMs = Math.max(0, stopTime - this.sessionStartTime);
    this.sessionStartTime = 0;
  }

  // ----------------------------------------------------------------------------------------------
  submit(): void {
    this.stop();

    if (this.sessionDurationMs === 0) {
      console.warn('no session is currently being recorded. first start a session with the start method');
      return;
    }

    if (!this.sbCookies) {
      this.sbCookies = (Cookies.noConflict ? Cookies.noConflict() : Cookies).withConverter({
        read: (value) => value,
        write: (value) => value.replace(/%25/g, '%'),
      });
    }

    const totalTimeMin = this.calculateTotalTimeMin();
    this.sbCookies.set('Totaltime', totalTimeMin.toString(10));

    const totalSelection = this.calculateTotalSelection();
    this.sbCookies.set('TotalSelection', totalSelection.toString(10));

    const percentageCorrect = this.calculatePercentageCorrect();
    this.sbCookies.set('Percentage_correct',
      SbUserPerformanceData.FormatPercentage(percentageCorrect));

    console.info('total time (mins):', totalTimeMin);
    console.info('total selectionm:', totalSelection);
    console.info('average percentage correct:', percentageCorrect);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private calculatePercentageCorrect(): number {
    if (this._quizzes.length === 0) {
      return (0);
    }

    const percentagesSum = this._quizzes.reduce(
      (currentPercentagesSum, quiz) => currentPercentagesSum + quiz.percentageCorrect, 0,
    );

    const percentagesAverage = percentagesSum / this._quizzes.length;
    return (percentagesAverage);
  }

  // ----------------------------------------------------------------------------------------------
  private calculateTotalSelection(): number {
    return (this._quizzes.reduce(
      (currentTotalSelection, quiz) => currentTotalSelection + quiz.totalSelection, 0,
    ));
  }

  // ----------------------------------------------------------------------------------------------
  private calculateTotalTimeMin(): number {
    const durationInSecs = this.sessionDurationMs / 1000;
    const durationInMins = durationInSecs / 60;
    return (durationInMins);
  }

  // ----------------------------------------------------------------------------------------------
  private static FormatPercentage(value: number): string {
    const percentage = (value * 100).toFixed(2);
    return (`${percentage}%`);
  }
}
