import SbAacSceneHelper from '../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../engine/storybookScene/SbScene';
import McBoyHowWearClothes from './McBoyHowWearClothes';
import McBoyPresentClothes from './McBoyPresentClothes';

export default function McBoyMyClothesAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_my_clothes_aac_my',
        soundId: 'SBL-0001_My',
      },
      {
        frameOrLabel: 'boy_my_clothes_aac_clothes',
        soundId: 'SBL-0002_Clothes',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoyHowWearClothes(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyPresentClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyPresentClothes(scene);
        },
      },
    });
  });
}
