import CjsGetBitmapText from './CjsGetBitmapText';
import CjsGetText from './CjsGetText';

type CjsTextObject = createjs.BitmapText | createjs.Text;

function CjsGetTextObject(container: createjs.Container, name: string): CjsTextObject | undefined {
  return (CjsGetBitmapText(container, name) || CjsGetText(container, name));
}

export {
  CjsGetTextObject,
  CjsTextObject,
};
