/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoySelectPantsQuiz from './McBoySelectPantsQuiz';

// ------------------------------------------------------------------------------------------------
function close(scene: SbScene): void {
  scene.playTo(
    'boy_wear_pants_tips_closing_end',
    'boy_wear_pants_tips_closing_begin',
    () => {
      McBoySelectPantsQuiz(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
async function playVoc(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_pants_tips',
    'boy_wear_pants_tips_voc_begin',
    'boy_wear_pants_tips_voc_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        close(scene);
      },
    },

    prev: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyWearPants(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function pantsTips(scene: SbScene): void {
  scene.playTo(
    'boy_wear_pants_closing_end',
    'boy_wear_pants_closing_begin',
    () => {
      playVoc(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_wear_pants_how_aac_i',
        soundId: 'SBL-0007_I',
      },
      {
        frameOrLabel: 'boy_wear_pants_how_aac_wear',
        soundId: 'SBL-0008_Wear',
      },
      {
        frameOrLabel: 'boy_wear_pants_how_aac_pants',
        soundId: 'SBL-0009_pants',
      },
      {
        frameOrLabel: 'boy_wear_pants_how_aac_legs',
        soundId: 'SBL-0010_legs',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          pantsTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearPants(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
async function playHowVoice(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0002_I-wear-my-pants-over-my-legs',
    'boy_wear_pants_how_start',
    'boy_wear_pants_how_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    prev: {
      callback: () => {
        McBoyWearPants(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyWearPants(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'boy_wear_pants_animation_end',
    'boy_wear_pants_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      boyWearPantsButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'boyWearPantsButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default async function McBoyWearPants(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_I-wear-my-pants',
    'boy_wear_pants_start',
    'boy_wear_pants_end',
  );

  wearHow(scene);
}
