/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoySelectShoesQuiz from './McBoySelectShoesQuiz';

// ------------------------------------------------------------------------------------------------
function closeTips(scene: SbScene): void {
  scene.playTo(
    'boy_shoes_tips_closing_end',
    'boy_shoes_tips_closing_begin',
    () => {
      McBoySelectShoesQuiz(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
async function playTipsVoc(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_For-a-start-encourage-your-ki_48.0k_stereo.mp3',
    'boy_shoes_tips_voc_begin',
    'boy_shoes_tips_voc_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        closeTips(scene);
      },
    },

    prev: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyWearShoes(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function openTips(scene: SbScene): void {
  scene.playTo(
    'boy_shoes_open_tips_end',
    'boy_shoes_open_tips_begin',
    () => {
      playTipsVoc(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_wear_shoes_how_aac_i',
        soundId: 'SBL-0015_I',
      },
      {
        frameOrLabel: 'boy_wear_shoes_how_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'boy_wear_shoes_how_aac_shoes',
        soundId: 'SBL-0017_shoes',
      },
      {
        frameOrLabel: 'boy_wear_shoes_how_aac_socks',
        soundId: 'SBL-0018_socks',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          openTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearShoes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-shoes-over-my-socks',
    'boy_wear_shoes_how_start',
    'boy_wear_shoes_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyWearShoes(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearShoes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'boy_wear_shoes_animation_end',
    'boy_wear_shoes_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      boyWearShoesButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'boyWearShoesButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McBoyWearShoes(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-shoes',
    'boy_wear_shoes_start',
    'boy_wear_shoes_end',
  ).then(() => {
    wearHow(scene);
  });
}
