import GetNumberValue from '../object/GetNumberValue';
import TimerCallback from './TimerCallback';
import TimerConfig from './TimerConfig';

export default class Timer<ParamsType = unknown> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  callback: TimerCallback<ParamsType> | undefined;

  context: unknown;

  private delayMs: number;

  private repeatCount: number;

  private currentRepeatCount: number;

  private isRunning: boolean;

  private timerId: NodeJS.Timeout | null;

  private params: ParamsType | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(config?: TimerConfig<ParamsType>) {
    const _config = config ?? {} as TimerConfig<ParamsType>;
    this.delayMs = Math.floor(Math.max(0, GetNumberValue(_config, 'delayMs', 1000)));
    this.repeatCount = Math.floor(GetNumberValue(_config, 'repeat', 0));
    this.callback = _config.callback;
    this.context = _config.context;
    this.params = _config.params;

    this.onInterval = this.onInterval.bind(this);

    this.currentRepeatCount = 0;
    this.timerId = null;
    this.isRunning = false;
  }

  // ----------------------------------------------------------------------------------------------
  static Create<ParamsType = unknown>(config?: TimerConfig<ParamsType>): Timer<ParamsType> {
    const timer = new Timer(config);
    timer.start();
    return (timer);
  }

  // ----------------------------------------------------------------------------------------------
  static Dispose<ParamsType = unknown>(timer: Timer<ParamsType> | undefined): undefined {
    if (timer) {
      timer.stop();
    }

    return (undefined);
  }

  // ----------------------------------------------------------------------------------------------
  start(): void {
    if (this.isRunning) {
      return;
    }

    this.currentRepeatCount = 0;
    this.timerId = setInterval(this.onInterval, this.delayMs);
    this.isRunning = true;
  }

  // ----------------------------------------------------------------------------------------------
  stop(): void {
    if (this.timerId) {
      clearTimeout(this.timerId);
      this.timerId = null;
    }

    this.isRunning = false;
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onInterval(): void {
    if (this.callback) {
      this.callback.call(this.context, this.params, this);
    }

    if (this.repeatCount === 0) {
      this.stop();
    } else if (this.repeatCount > 0) {
      const currentRepeatCount = this.currentRepeatCount + 1;
      if (currentRepeatCount > this.repeatCount) {
        this.stop();
      } else {
        this.currentRepeatCount = currentRepeatCount;
      }
    }
  }
}
