import CjsGetButton from '../../../utils/createjs/objects/CjsGetButton';
import CjsButton from '../../../utils/createjs/objects/buttons/CjsButton';
import SbScene from '../SbScene';

const DefaultSpeakerButtonDisplay = 'speakerButtonDisplay';

let SoundVoiceId: string | undefined;
let SpeakerButton: CjsButton | undefined;
let Scene: SbScene | undefined;

// ------------------------------------------------------------------------------------------------
function onSpeakerButtonPressed(): void {
  if (Scene && SoundVoiceId) {
    Scene.playVoices(SoundVoiceId);
  }
}

// ------------------------------------------------------------------------------------------------
export default function SbSetSceneSpeakerButton(scene: SbScene, isEnabled: boolean,
  soundVoiceId?: string, buttonDisplayName = DefaultSpeakerButtonDisplay): void {
  if (SpeakerButton) {
    SpeakerButton.dispose();
  }

  SpeakerButton = CjsGetButton(scene.display, buttonDisplayName, onSpeakerButtonPressed);
  if (!SpeakerButton) {
    console.warn('unable to create speaker button for display with name:', buttonDisplayName);
    return;
  }

  Scene = scene;
  SpeakerButton.enabled = isEnabled;

  if (soundVoiceId) {
    SoundVoiceId = soundVoiceId;
  }
}
