/* eslint-disable no-use-before-define */
import SbScene from '../../../../engine/storybookScene/SbScene';
import SbSetSceneSpeakerButton from '../../../../engine/storybookScene/utils/SbSceneSpeakerButton';
import { SbButtonsMenuConfig } from '../../../../engine/ui/buttonsMenu/config/SbButtonsMenuConfig';
import McBoyThisHowWhereClothes from '../outro/McBoyThisHowWhereClothes';

// ------------------------------------------------------------------------------------------------
async function playCorrectVoc(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_Well-done-Great-job_48.0k_stereo.mp3',
    'boy_help_wear_glasses_quiz_correct_voc_begin',
    'boy_help_wear_glasses_quiz_correct_voc_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        McBoyThisHowWhereClothes(scene);
      },
    },

    prev: {
      callback: () => {
        McBoyHelpWearGlassesQuiz(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyHelpWearGlassesQuiz(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function beginCorrect(scene: SbScene): void {
  scene.playTo(
    'boy_help_wear_glasses_quiz_correct_begin',
    'boy_help_wear_glasses_quiz_correct_end',
    () => {
      playCorrectVoc(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function buildMenuConfig(areButtonsEnabled: boolean, scene: SbScene): SbButtonsMenuConfig {
  const isDisabled = !areButtonsEnabled;
  return ({
    buttons: {
      boyGlassesSlotButton0Display: {
        isCorrect: false,
        isDisabled,
      },

      boyGlassesSlotButton2Display: {
        isCorrect: false,
        isDisabled,
      },

      boyGlassesSlotButton1Display: {
        isCorrect: true,
        isDisabled,
        callback: (): void => {
          beginCorrect(scene);
        },
      },
    },

    defaultButtonName: 'boyGlassesSlotButton0Display',
    isQuiz: true,
  });
}

// ----------------------------------------------------------------------------------------------
function presentMenu(scene: SbScene, areButtonsEnabled: boolean): void {
  scene.engine.presentMenu(buildMenuConfig(areButtonsEnabled, scene));
}

// ----------------------------------------------------------------------------------------------
export default function McBoyHelpWearGlassesQuiz(scene: SbScene): void {
  const labelPfx = 'boy_help_wear_glasses_quiz';
  const startLabel = `${labelPfx}_start`;
  const endLabel = `${labelPfx}_end`;

  scene.stop(startLabel);
  presentMenu(scene, false);

  const quizPromptVoiceSoundId = 'SBL-0005_Can-you-help-him-wear-his-glas_48.0k_stereo.mp3';
  SbSetSceneSpeakerButton(scene, false, quizPromptVoiceSoundId);

  scene.playSyncVoice(
    quizPromptVoiceSoundId,
    startLabel,
    endLabel,
  ).then(() => {
    SbSetSceneSpeakerButton(scene, true, quizPromptVoiceSoundId);
    presentMenu(scene, true);
  });
}
