import CjsCreateCheckbox from '../../../../../utils/createjs/objects/buttons/CjsCreateCheckbox';
import CjsRadioGroup from '../../../../../utils/createjs/objects/buttons/CjsRadioGroup';
import CjsRadioGroupCallback from '../../../../../utils/createjs/objects/buttons/CjsRadioGroupCallback';
import ICjsCheckbox from '../../../../../utils/createjs/objects/buttons/ICjsCheckbox';
import { SbAssetLib } from '../../../../data/SbAssetLib';
import SbDesignData from '../../../../data/SbDesignData';
import SbUserStorybookSettingsData from '../../../../data/settings/storybook/SbUserStorybookSettingsData';
import SbUiComponent from '../../../SbUiComponent';
import SbRadioGroupEvents from '../../../events/SbRadioGroupEvents';
import SbCaptionHiliteColorCheckbox from '../../SbCaptionHiliteColorCheckbox';
import SbSettingsSubPanel from '../SbSettingsSubPanel';

const OffBinaryButtonId = 'off';
const OnBinaryButtonId = 'on';
type BinaryButtonId = typeof OffBinaryButtonId | typeof OnBinaryButtonId;

export default class SbSettingsStorybookSubPanel
  extends SbSettingsSubPanel<SbUserStorybookSettingsData> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly captionHiliteColorRadioGroup: CjsRadioGroup<number>;

  private readonly helpfulTipsRadioGroup: CjsRadioGroup;

  private readonly captionsRadioGroup: CjsRadioGroup;

  private readonly captionHilitesRadioGroup: CjsRadioGroup;

  private readonly aacRadioGroup: CjsRadioGroup;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(mainPanel: SbUiComponent, lib: Readonly<SbAssetLib>) {
    super(mainPanel, lib);

    this.helpfulTipsRadioGroup = this.initRadioGroup(
      'helpfulTipsOffButtonDisplay',
      'helpfulTipsOnButtonDisplay',
      this.onHelpfulTipsGroupSelected,
    );

    this.captionsRadioGroup = this.initRadioGroup(
      'captionsOffButtonDisplay',
      'captionsOnButtonDisplay',
      this.onCaptionsGroupSelected,
    );

    this.captionHilitesRadioGroup = this.initRadioGroup(
      'captionHilitesOffButtonDisplay',
      'captionHilitesOnButtonDisplay',
      this.onCaptionHilitesGroupSelected,
    );

    this.captionHiliteColorRadioGroup = this.initCaptionHiliteColorCheckboxes();

    this.aacRadioGroup = this.initRadioGroup(
      'aacOffButtonDisplay',
      'aacOnButtonDisplay',
      this.onAacGroupSelected,
    );

    this.updateSettingsUiFromData();
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected updateSettingsUiFromData(): void {
    if (this.settings) {
      this.updateRadioGroup(this.aacRadioGroup, this.settings.aacSymbolsEnabled)
        .updateRadioGroup(this.captionsRadioGroup, this.settings.captionsEnabled)
        .updateRadioGroup(this.helpfulTipsRadioGroup, this.settings.helpfulTipsEnabled)
        .updateCaptionHilitesRadioGroup()
        .updateHiliteColorRadioGroup();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private updateCaptionHilitesRadioGroup(): this {
    return (this
      .updateRadioGroup(this.captionHilitesRadioGroup,
        this.settings?.captionHilitesEnabled === true)
      .updateCaptionHilitesRadioGroupEnabledState());
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private initCaptionHiliteColorCheckbox(
    displayName: string,
  ): SbCaptionHiliteColorCheckbox | undefined {
    const checkbox = CjsCreateCheckbox(this.mainPanel.container, displayName, false, undefined,
      undefined, this.lib.animateClasses.RadioButtonHitBoxDisplay);

    return (checkbox ? new SbCaptionHiliteColorCheckbox(checkbox) : undefined);
  }

  // ----------------------------------------------------------------------------------------------
  private initCaptionHiliteColorCheckboxes(): CjsRadioGroup<number> {
    const groupData: [number, ICjsCheckbox][] = [];
    let index = 0;
    let checkbox = this.initCaptionHiliteColorCheckbox(`hiliteColor${index}CheckboxDisplay`);
    while (checkbox) {
      const hiliteColor = SbDesignData.CaptionHilites.Colors[index];
      if (hiliteColor) {
        checkbox.setColor(hiliteColor);
      }

      checkbox.checkbox.shouldUseDisabledState = false;
      groupData.push([index, checkbox.checkbox]);
      index += 1;
      checkbox = this.initCaptionHiliteColorCheckbox(`hiliteColor${index}CheckboxDisplay`);
    }

    const group = new CjsRadioGroup(groupData);
    group.addListener(SbRadioGroupEvents.SELECTED, this.onCaptionHiliteColorCheckboxPressed, this);

    return (group);
  }

  // ----------------------------------------------------------------------------------------------
  private initRadioGroup(offButtonDisplayName: string,
    onButtonDisplayName: string, callback: CjsRadioGroupCallback<BinaryButtonId>): CjsRadioGroup {
    const offCheckbox = this.initCaptionHiliteColorCheckbox(offButtonDisplayName);
    const onCheckbox = this.initCaptionHiliteColorCheckbox(onButtonDisplayName);
    const groupData: [BinaryButtonId, ICjsCheckbox][] = [];
    if (offCheckbox) {
      groupData.push([OffBinaryButtonId, offCheckbox.checkbox]);
    }

    if (onCheckbox) {
      groupData.push([OnBinaryButtonId, onCheckbox.checkbox]);
    }

    const group = new CjsRadioGroup(groupData);
    group.addListener(SbRadioGroupEvents.SELECTED, callback, this);
    group.shouldUseDisabledStates(false);
    return (group);
  }

  // ----------------------------------------------------------------------------------------------
  private onAacGroupSelected(id: BinaryButtonId): void {
    this.setSettingProp(this.settings, 'aacSymbolsEnabled', id === OnBinaryButtonId);
  }

  // ----------------------------------------------------------------------------------------------
  private onCaptionHiliteColorCheckboxPressed(id: number): void {
    this.setSettingProp(this.settings, 'captionHiliteColorIndex', id);
  }

  // ----------------------------------------------------------------------------------------------
  private onCaptionHilitesGroupSelected(id: BinaryButtonId): void {
    this.setSettingProp(this.settings, 'captionHilitesEnabled', id === OnBinaryButtonId);

    const isEnabled = this.settings?.captionHilitesEnabled === true;
    this.captionHiliteColorRadioGroup.enabled = isEnabled;
    this.captionHiliteColorRadioGroup.shouldUseDisabledStates(!isEnabled);
  }

  // ----------------------------------------------------------------------------------------------
  private onCaptionsGroupSelected(id: BinaryButtonId): void {
    const isEnabled = id === OnBinaryButtonId;
    this.setSettingProp(this.settings, 'captionsEnabled', isEnabled);

    this.updateCaptionHilitesRadioGroupEnabledState();
    this.updateCaptionHiliteColorRadioGroupEnabledState();

    if (!isEnabled) {
      this.updateRadioGroup(this.captionHilitesRadioGroup, false);
      this.setSettingProp(this.settings, 'captionHilitesEnabled', false);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onHelpfulTipsGroupSelected(id: BinaryButtonId): void {
    this.setSettingProp(this.settings, 'helpfulTipsEnabled', id === OnBinaryButtonId);
  }

  // ----------------------------------------------------------------------------------------------
  private updateCaptionHiliteColorRadioGroupEnabledState(): void {
    const isEnabled = this.settings !== undefined
      && this.settings.captionsEnabled && this.settings.captionHilitesEnabled;
    this.captionHiliteColorRadioGroup.enabled = isEnabled;
    this.captionHiliteColorRadioGroup.shouldUseDisabledStates(!isEnabled);
  }

  // ----------------------------------------------------------------------------------------------
  private updateCaptionHilitesRadioGroupEnabledState(): this {
    const isEnabled = this.settings?.captionsEnabled === true;
    this.captionHilitesRadioGroup.enabled = isEnabled;
    this.captionHilitesRadioGroup.shouldUseDisabledStates(!isEnabled);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateHiliteColorRadioGroup(): this {
    if (this.settings?.captionHiliteColorIndex !== undefined) {
      this.captionHiliteColorRadioGroup.setButtonOn(this.settings.captionHiliteColorIndex);
    }

    this.updateCaptionHiliteColorRadioGroupEnabledState();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateRadioGroup(group: CjsRadioGroup, isEnabled: boolean): this {
    const buttonId = isEnabled ? OnBinaryButtonId : OffBinaryButtonId;
    group.setButtonOn(buttonId);
    return (this);
  }
}
