import IStorybookEngine from '../../IStorybookEngine';
import SbSaveSettingsCommonConfig from '../../data/SbSaveSettingsCommonConfig';
import SbUiComponent from '../SbUiComponent';
import SbSettingsDevicesSubPanel from '../settings/subPanels/devices/SbSettingsDevicesSubPanel';

export default class SbDevicesPanel extends SbUiComponent {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private subPanel: SbSettingsDevicesSubPanel;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: createjs.MovieClip, engine: IStorybookEngine) {
    super(container, engine);

    this.subPanel = new SbSettingsDevicesSubPanel(this, engine.lib);
    this.subPanel.setSettings(engine.userSettingsData.device);
    this.subPanel.updateFromSettings();
  }

  // ----------------------------------------------------------------------------------------------
  present(): this {
    this.visible = true;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  unpresent(): this {
    this.visible = false;
    return (this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected override async onClosePressed(): Promise<void> {
    if (this.subPanel.areSettingsDirty) {
      await this.engine.saveSettings(SbSaveSettingsCommonConfig());
    }

    super.onClosePressed();
  }

  // ==============================================================================================
  // private
  // ==============================================================================================
}
