/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoyHelpWearGlassesQuiz from '../glasses/McBoyHelpWearGlassesQuiz';
import McBoyHowYouWhereClothes from './McBoyHowYouWhereClothes';

function howIWearClothesAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_this_how_where_clothes_aac_how',
        soundId: 'SBL-0027_How',
      },
      {
        frameOrLabel: 'boy_this_how_where_clothes_aac_i',
        soundId: 'SBL-0019_I',
      },
      {
        frameOrLabel: 'boy_this_how_where_clothes_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'boy_this_how_where_clothes_aac_clothes',
        soundId: 'SBL-0030_clothes',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoyHowYouWhereClothes(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyThisHowWhereClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyThisHowWhereClothes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
export default function McBoyThisHowWhereClothes(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_This-is-how-I-wear-my-clothes_48.0k_stereo.mp3',
    'boy_this_how_where_clothes_start',
    'boy_this_how_where_clothes_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          howIWearClothesAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyHelpWearGlassesQuiz(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyHelpWearGlassesQuiz(scene);
        },
      },
    });
  });
}
