import { CjsEvents } from '../../utils/createjs/events/CjsEvents';
import CjsDisplayObject from '../../utils/createjs/objects/display/CjsDisplayObject';
import CjsResolveFrame from '../../utils/createjs/utils/CjsResolveFrame';
import Clamp from '../../utils/math/Clamp';
import IStorybookEngine from '../IStorybookEngine';
import SbPlaySoundCompleteCallback from '../audio/SbPlaySoundCompleteCallback';
import SbPlaySoundConfig from '../audio/SbPlaySoundConfig';
import { SbUserAudioSettingsDataKeys } from '../data/settings/audio/SbUserAudioSettingsDataKeys';
import SbResolveSceneDisplay from './utils/SbResolveSceneDisplay';
import SbSceneDisplay from './utils/SbSceneDisplay';
import SbSceneFrameCallbackData, { SbSceneFrameCallback } from './utils/SbSceneFrameCallbackData';

type SyncedSoundData = {
  endFrame: number;
  sound: createjs.AbstractSoundInstance;
  resolve: () => void | PromiseLike<void>;
  startFrame: number;
};

export default class SbScene extends CjsDisplayObject<SbSceneDisplay> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  private readonly frameCallbacks: SbSceneFrameCallbackData[];

  private readonly isContainerForDisplay: boolean;

  private lastVisitedFrame: number;

  private syncedSoundData: SyncedSoundData | undefined;

  private playToTween: createjs.Tween | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine, displayOrCtorName?: createjs.MovieClip | string,
    isContainerForDisplay = false) {
    super(SbResolveSceneDisplay(engine, displayOrCtorName));

    this.engine = engine;

    this.isContainerForDisplay = isContainerForDisplay;

    this.movieClip.scene = this;
    this.movieClip.engine = engine;
    this.movieClip.loop = false;

    this.frameCallbacks = [];

    this.stop();

    this.lastVisitedFrame = -1;

    this.onDisplayTimelineChange = this.onDisplayTimelineChange.bind(this);
    this.onSyncedSoundComplete = this.onSyncedSoundComplete.bind(this);
    this.onSyncedSoundTick = this.onSyncedSoundTick.bind(this);

    this.timeline.addEventListener(CjsEvents.CHANGE, this.onDisplayTimelineChange);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    this.removeAllFrameCallbacks();
    this.stop();
    delete (this.movieClip as unknown as Record<'scene', SbScene | undefined>).scene;
    this.display.parent?.removeChild(this.display);
    this.timeline.removeEventListener(CjsEvents.CHANGE, this.onDisplayTimelineChange);
    super.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  addFrameCallback<T = unknown>(frameNumberOrName: string | number,
    callback: SbSceneFrameCallback<T>,
    context?: unknown, isOnce = false, params: T | undefined = undefined): this {
    const frame = this.resolveFrame(frameNumberOrName);
    if (frame === null) {
      console.warn('unable to resolve in scene timeline, frame label of value:',
        frameNumberOrName);
      return (this);
    }

    const fc: SbSceneFrameCallbackData<T> = {
      callback,
      context,
      frame,
      isOnce,
      params,
    };

    this.frameCallbacks[frame] = fc as SbSceneFrameCallbackData;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get currentFrame(): number {
    return (this.movieClip.currentFrame);
  }

  // ----------------------------------------------------------------------------------------------
  set currentFrame(value: number) {
    this.stopInner(value);
  }

  // ----------------------------------------------------------------------------------------------
  getChild<T extends createjs.DisplayObject = createjs.DisplayObject>(
    indexOrName: number | string,
  ): T | undefined {
    return (typeof (indexOrName) === 'number'
      ? this.getChildAt(indexOrName)
      : this.getChildByName(indexOrName));
  }

  // ----------------------------------------------------------------------------------------------
  getChildAt<T extends createjs.DisplayObject = createjs.DisplayObject>(
    index: number,
  ): T | undefined {
    return ((this.display.getChildAt(index) as T) ?? undefined);
  }

  // ----------------------------------------------------------------------------------------------
  getChildByName<T extends createjs.DisplayObject = createjs.DisplayObject>(
    name: string,
  ): T | undefined {
    return ((this.display.getChildByName(name) as T) ?? undefined);
  }

  // ----------------------------------------------------------------------------------------------
  getChildMovieClip<T extends createjs.MovieClip = createjs.MovieClip>(
    indexOrName: number | string,
  ): T | undefined {
    const child = this.getChild(indexOrName);
    return (child instanceof createjs.MovieClip ? child as T : undefined);
  }

  // ----------------------------------------------------------------------------------------------
  isFrameValid(frameOrLabel: number | string): boolean {
    return (this.resolveFrame(frameOrLabel) !== null);
  }

  // ----------------------------------------------------------------------------------------------
  get movieClip(): SbSceneDisplay {
    const { display } = this;
    return (this.isContainerForDisplay
      ? display.getChildAt(0) as SbSceneDisplay
      : display);
  }

  // ----------------------------------------------------------------------------------------------
  nextFrame(): this {
    this.movieClip.gotoAndStop(this.movieClip.currentFrame + 1);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  play(startFrameOrLabel?: number | string): this {
    if (startFrameOrLabel !== undefined) {
      const frame = this.resolveFrame(startFrameOrLabel);
      if (frame !== null && frame !== this.movieClip.currentFrame) {
        this.movieClip.gotoAndPlay(frame);
      } else {
        this.movieClip.play();
      }
    } else {
      this.movieClip.play();
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  playOnce<ParamsType = unknown>(callback: SbSceneFrameCallback<ParamsType>,
    context?: unknown, startFrameOrLabel?: number | string, params?: ParamsType): this {
    this.addFrameCallback(
      this.movieClip.totalFrames - 1, () => {
        this.stop();
        callback.call(context, this, params);
      },

      context,
      true,
      params,
    );

    return (this.play(startFrameOrLabel));
  }

  // ----------------------------------------------------------------------------------------------
  playSound(config: SbPlaySoundConfig): createjs.AbstractSoundInstance | undefined;

  playSound(
    audioId: string,
    volume?: number,
    loop?: boolean | number,
    completeCallback?: SbPlaySoundCompleteCallback,
    callbackContext?: unknown,
  ): createjs.AbstractSoundInstance | undefined;

  playSound(
    audioIdOrConfig: string | SbPlaySoundConfig,
    volume: number | undefined = 1.0,
    loop: boolean | number | undefined = false,
    completeCallback: SbPlaySoundCompleteCallback | undefined = undefined,
    callbackContext: unknown = undefined,
  ): createjs.AbstractSoundInstance | undefined {
    return (this.engine.playSound(audioIdOrConfig, volume, loop, completeCallback,
      callbackContext));
  }

  // ----------------------------------------------------------------------------------------------
  /**
   * Caution: Due to frame-skipping to keep up, there is currently no guarantee that frame
   * callbacks will be called when using this method, unless the callback is added to
   * the last frame (on `endFrameOrLabel`).
   */
  playSyncVoice(audioId: string, startFrameOrLabel: number | string,
    endFrameOrLabel: number | string): Promise<void> {
    return new Promise((resolve) => {
      const startFrame = this.resolveFrame(startFrameOrLabel);
      if (startFrame === null) {
        resolve();
        return;
      }

      const endFrame = this.resolveFrame(endFrameOrLabel);
      if (endFrame === null) {
        resolve();
        return;
      }

      const sound = this.engine.playSound(audioId,
        this.engine.resolveSettingsVolume(SbUserAudioSettingsDataKeys.Voice));

      if (!sound) {
        resolve();
        return;
      }

      if (this.syncedSoundData) {
        this.syncedSoundData.sound.stop();
        this.syncedSoundData.sound.destroy();
      }

      this.syncedSoundData = {
        endFrame,
        resolve,
        sound,
        startFrame,
      };

      this.stop(this.syncedSoundData.startFrame);

      sound.addEventListener(CjsEvents.COMPLETE, this.onSyncedSoundComplete);
      createjs.Ticker.addEventListener(CjsEvents.TICK, this.onSyncedSoundTick);
    });
  }

  // ----------------------------------------------------------------------------------------------
  /**
   * Caution: Due to frame-skipping to keep up, there is currently no guarantee that frame
   * callbacks will be called when using this method, unless the callback is added to
   * the last frame (on `endFrameOrLabel`). Otherwise, use `completeCallback` instead.
   */
  playTo<ParamsType = unknown>(
    endFrameOrLabel: number | string,
    startFrameOrLabel?: number | string,
    completeCallback?: SbSceneFrameCallback<ParamsType>,
    context?: unknown,
    params?: ParamsType,
  ): this {
    const endFrame = this.resolveFrame(endFrameOrLabel);
    if (endFrame === null) {
      return (this);
    }

    if (startFrameOrLabel !== undefined) {
      const startFrame = this.resolveFrame(startFrameOrLabel);
      if (startFrame === null) {
        return (this);
      }

      this.stop(startFrameOrLabel);
    }

    if (this.playToTween) {
      this.stopPlayToTween();
    }

    this.playToTween = createjs.Tween
      .get(
        this,
        {
          onComplete: () => {
            completeCallback?.call(context, this, params);
          },

          useTicks: true,
        },
      )
      .to(
        {
          currentFrame: endFrame,
        },
        Math.abs(endFrame - this.currentFrame),
      );

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  /**
   * Caution: Due to frame-skipping to keep up, there is currently no guarantee that frame
   * callbacks will be called when using this method, unless the callback is added to
   * the last frame (on `endFrameOrLabel`). Otherwise, use `completeCallback` instead.
   */
  playToEnd<ParamsType = unknown>(
    endFrameOrLabel: number | string,
    completeCallback?: SbSceneFrameCallback<ParamsType>,
    context?: unknown,
    params?: ParamsType,
  ): this {
    return (this.playTo(endFrameOrLabel, undefined, completeCallback, context, params));
  }

  // ----------------------------------------------------------------------------------------------
  playVoices(soundIds: string | string[], volume?: number): Promise<void> {
    return (this.engine.playVoice(soundIds, volume));
  }

  // ----------------------------------------------------------------------------------------------
  removeAllFrameCallbacks(): this {
    this.frameCallbacks.length = 0;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  removeFrameCallback(frameNumberOrName: number | string): this {
    const frame = this.resolveFrame(frameNumberOrName);
    if (frame !== null) {
      delete this.frameCallbacks[frame];
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  resolveFrame(frameOrLabel?: number | string): number | null {
    return (CjsResolveFrame(this.display, frameOrLabel));
  }

  // ----------------------------------------------------------------------------------------------
  get stage(): createjs.Stage {
    return (this.display.stage);
  }

  // ----------------------------------------------------------------------------------------------
  stop(frameOrLabel?: number | string): this {
    // TODO
    // console.log('stop', this.movieClip.currentFrame, this.movieClip.totalFrames);

    this.stopPlayToTween();
    this.stopInner(frameOrLabel);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get timeline(): createjs.Timeline {
    return (this.movieClip.timeline);
  }

  // ----------------------------------------------------------------------------------------------
  get totalFrames(): number {
    return (this.movieClip.totalFrames);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  /** be sure to call base method if overridding */
  protected onDisplayTimelineChange(): void {
    const position = Math.round(this.timeline.position);
    if (this.lastVisitedFrame === position) {
      return;
    }

    if (position >= this.movieClip.totalFrames) {
      if (!this.movieClip.loop) {
        this.stop(this.movieClip.totalFrames - 1);
      }
      return;
    }

    this.lastVisitedFrame = position;

    const callbackData = this.frameCallbacks[position];
    if (!callbackData) {
      return;
    }

    if (callbackData.isOnce && callbackData.frame !== undefined) {
      this.removeFrameCallback(callbackData.frame);
    }

    if (callbackData.callback) {
      callbackData.callback.call(callbackData.context, this, callbackData.params);
    }
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onSyncedSoundComplete(): void {
    createjs.Ticker.removeEventListener(CjsEvents.TICK, this.onSyncedSoundTick);
    const { syncedSoundData } = this;
    if (!syncedSoundData) {
      return;
    }

    this.syncedSoundData = undefined;

    if (this.currentFrame !== syncedSoundData.endFrame) {
      this.stop(syncedSoundData.endFrame);
    }

    syncedSoundData.sound.destroy();
    syncedSoundData.resolve();
  }

  // ----------------------------------------------------------------------------------------------
  private onSyncedSoundTick(): void {
    if (!this.syncedSoundData || this.syncedSoundData.sound.duration <= 0) {
      createjs.Ticker.removeEventListener(CjsEvents.TICK, this.onSyncedSoundTick);
      return;
    }

    const syncDuration = this.syncedSoundData.endFrame - this.syncedSoundData.startFrame;

    // kludge: sometimes, progress goes past 1.0, when this should never happen
    const progress = Clamp(
      this.syncedSoundData.sound.position / this.syncedSoundData.sound.duration,
      0,
      1.0,
    );

    const currentFrame = Math.floor(this.syncedSoundData.startFrame + syncDuration * progress);
    this.stop(currentFrame);
  }

  // ----------------------------------------------------------------------------------------------
  private stopInner(frameOrLabel?: number | string): void {
    if (frameOrLabel !== undefined) {
      const frame = this.resolveFrame(frameOrLabel);
      if (frame !== null && frame !== this.movieClip.currentFrame) {
        this.movieClip.gotoAndStop(frame);
      } else {
        this.movieClip.stop();
      }
    } else {
      this.movieClip.stop();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private stopPlayToTween(): void {
    if (this.playToTween) {
      createjs.Tween.removeTweens(this);
      this.playToTween = undefined;
    }
  }
}
