function ParseBoolean(source: unknown, defaultValue = false): boolean {
  switch (typeof (source)) {
    case 'boolean':
      return (source);

    case 'number':
      return (source !== 0);

    case 'string':
      switch (source.toLocaleLowerCase()) {
        case 'true':
          return (true);

        case 'false':
        case '':
          return (false);

        default: {
          const numberValue = Number.parseFloat(source);
          if (Number.isNaN(numberValue)) {
            return (defaultValue);
          }

          return (numberValue !== 0);
        }
      }

    default:
      return (defaultValue);
  }
}

export default ParseBoolean;
