import CjsGetMovieClip from '../../../utils/createjs/objects/CjsGetMovieClip';
import CjsSlider from '../../../utils/createjs/objects/sliders/CjsSlider';

export default class SbProgressBar extends CjsSlider {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly flag: createjs.MovieClip | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(display: createjs.MovieClip, value = 0) {
    super(display, value);

    this.flag = CjsGetMovieClip(this.display, 'flag');
    this.shouldUseHandleBoundsForConstraint = false;
  }
}
