import SbAacSceneHelper from '../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../engine/storybookScene/SbScene';
import McBoyHowWearClothes from './McBoyHowWearClothes';
import McBoyPresentClothes from './McBoyPresentClothes';
import McBoyWearShirt from './shirt/McBoyWearShirt';

export default function McBoyHowWearClothesAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_how_wear_clothes_aac_how',
        soundId: 'SBL-0027_How_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'boy_how_wear_clothes_aac_i',
        soundId: 'SBL-0028_I',
      },
      {
        frameOrLabel: 'boy_how_wear_clothes_aac_wear',
        soundId: 'SBL-0029_wear',
      },
      {
        frameOrLabel: 'boy_how_wear_clothes_aac_clothes',
        soundId: 'SBL-0002_Clothes',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoyWearShirt(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyHowWearClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyPresentClothes(scene);
        },
      },
    });
  });
}
