import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoyHelpHimWearClothesQuiz from './McBoyHelpHimWearClothesQuiz';
import McBoyThisHowWhereClothes from './McBoyThisHowWhereClothes';

function howYouWearClothesAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_how_you_where_clothes_aac_how',
        soundId: 'SBL-0027_How',
      },
      {
        frameOrLabel: 'boy_how_you_where_clothes_aac_you',
        soundId: 'SBL-0032_you_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'boy_how_you_where_clothes_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'boy_how_you_where_clothes_aac_clothes',
        soundId: 'SBL-0030_clothes',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoyHelpHimWearClothesQuiz(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyThisHowWhereClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyThisHowWhereClothes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
export default function McBoyHowYouWhereClothes(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_How-do-you-wear-your-clothes_48.0k_stereo.mp3',
    'boy_how_you_where_clothes_start',
    'boy_how_you_where_clothes_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          howYouWearClothesAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyThisHowWhereClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyThisHowWhereClothes(scene);
        },
      },
    });
  });
}
