import CjsGetDisplayObject from './CjsGetDisplayObject';

export default function CjsGetMovieClip<T extends createjs.MovieClip = createjs.MovieClip>(
  container: createjs.Container,
  name: string,
): T | undefined {
  const clip = CjsGetDisplayObject(container, name);
  return (clip instanceof createjs.MovieClip ? clip as T : undefined);
}
