import SbScene from '../storybookScene/SbScene';

type PauseConfig = {
  [P in SbPauseKind as Uncapitalize<P>]: boolean;
};

const { ContentVideoId } = SbDesignData;

export default class SbContentHandler extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  // TODO: remove if na
  // startFrame: number;

  private readonly engine: IStorybookEngine;

  private readonly pauseConfig: PauseConfig;

  private readonly videoMath: SbVideoMath;

  private contentVideoLastTime: number;

  private contentVideo: SbContentVideo | undefined;

  private videoTimeProps: SbVideoMathProps | undefined;

  // TODO: remove if na
  // private testTimer: Timer | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    super();

    this.engine = engine;
    this.pauseConfig = {
      isFromNav: false,
      isFromUi: false,
    };

    this.contentVideoLastTime = -1;

    // remove if na
    // this.startFrame = 0;
    this.videoMath = new SbVideoMath(engine);

    // TODO: remove if na
    // this.onTickerTick = this.onTickerTick.bind(this);
  }

  // ----------------------------------------------------------------------------------------------
  get videoElement(): HTMLVideoElement | undefined {
    return (this.contentVideo?.videoElement);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    if (this.contentVideo) {
      this.contentVideo.dispose();
      this.contentVideo = undefined;
    } else {
      this.exportRoot.stop();
    }
  }

  // ----------------------------------------------------------------------------------------------
  loadContentVideo(videoUrl: string): this {
    if (this.contentVideo) {
      return (this);
    }

    const queue = new createjs.LoadQueue();
    queue.on(CjsEvents.FILE_LOAD, (e: unknown) => {
      const event = e as createjs.Event;
      const item = event.item as CjsLoadItem;
      if (item.id === ContentVideoId) {
        this.setContentVideo(event.result);
      }
    }, this);

    queue.on(CjsEvents.COMPLETE, () => {
      queue.destroy();
      this.emit(SbContentHandlerEvents.VIDEO_LOAD_COMPLETE, this);
    }, this, true);

    queue.loadFile({
      id: ContentVideoId,
      src: videoUrl,
      type: 'video',
    }, true);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  navTo(direction: SbNavToDirection, navToParams: SbNavToParams): void {
    const navParam = navToParams[direction];

    // TODO: remove if na
    // const targetFrameOrLabel = navToParams[direction];
    // if (targetFrameOrLabel === undefined) {

    if (navParam === undefined) {
      this.resume(SbPauseKinds.IsFromNav);
      return;
    }

    let _navToParam: SbNavToParam;
    if (SbIsNavToParam(navParam)) {
      _navToParam = navParam;
    } else {
      _navToParam = {
        action: SbNavActions.Play,
        frame: navParam,
      };
    }

    if (_navToParam.callback !== undefined) {
      _navToParam.callback.call(_navToParam.context);
      return;
    }

    // TODO: remove if na
    const targetFrameOrLabel = _navToParam.frame;
    if (targetFrameOrLabel === undefined) {
      this.resume(SbPauseKinds.IsFromNav);
      return;
    }

    // TODO: remove if na
    /*
    switch (direction) {
      case SbNavToDirections.Prev:
      case SbNavToDirections.Restart: {
        const timelineFrame = this.exportRoot.timeline.resolve(targetFrameOrLabel) + 1;
        if (this.contentVideo) {
          this.navToJumpVideo(timelineFrame, _navToParam.action);
        } else {
          this.navToJumpRoot(timelineFrame, _navToParam.action);
        }

        break;
      }

      case SbNavToDirections.Next:
      default:
        this.resume(SbPauseKinds.IsFromNav);
        break;
    }
    */
    // TODO: remove if na
    const timelineFrame = this.exportRoot.timeline.resolve(targetFrameOrLabel);
    if (this.contentVideo && _navToParam.action) {
      this.navToJumpVideo(timelineFrame, _navToParam.action);
    } else {
      this.navToJumpRoot(timelineFrame, _navToParam);
    }
  }

  // ----------------------------------------------------------------------------------------------
  pause(pauseKind: SbPauseKind): this {
    this.pauseTarget(pauseKind);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  resume(pauseKind: SbPauseKind, startFrameOrLabel?: number | string,
    endFrameOrLabel?: number | string): this {
    this.resumeTarget(pauseKind, startFrameOrLabel, endFrameOrLabel);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setContentVideo(video: HTMLVideoElement): this {
    if (this.contentVideo) {
      console.info('A content video has already been set.');
      return (this);
    }

    this.contentVideo = new SbContentVideo(this.engine, video);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  start(timelineFrame?: number | string): this {
    const startTimelineFrame = timelineFrame ?? this.startTimelineFrame;

    if (this.contentVideo?.videoElement) {
      this.startWithVideo(startTimelineFrame);
    } else {
      this.startWithTimeline(startTimelineFrame);
    }

    this.engine.updateFromSettings();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  update(): void {
    if (!this.contentVideo?.videoElement) {
      return;
    }

    this.videoTimeProps = this.videoMath.resolveVideoTime(this.videoTimeProps);
    /*
    const timelineFrame = this.exportRoot.currentFrame - this.startTimelineFrane;
    const videoTimeSecs = this.contentVideo.videoElement.currentTime;
    const videoFrame = Math.ceil(videoTimeSecs * this.engine.config.videoFps);
    */

    // TODO
    // console.log(videoFrame, timelineFrame, this.exportRoot.currentFrame);

    // FIXME: still needed for video? if so, need to clean-up before re-using
    /*
    const {
      timelineFrame,
      videoFrame,
      videoTimeSecs,
    } = this.videoTimeProps;

    if (videoFrame - timelineFrame >= 5) {
      const catchUpTimeInSecs = (videoFrame - timelineFrame) / this.engine.config.videoFps;
      this.exportRoot.paused = false;
      this.exportRoot.advance(catchUpTimeInSecs * 1000);
      this.exportRoot.paused = true;

      // TODO
      // console.log('skip');
    } else if (!this.contentVideo.videoElement.paused) {
      const videoTimeDelta = this.contentVideoLastTime === -1
        ? videoTimeSecs
        : videoTimeSecs - this.contentVideoLastTime;

      // TODO
      // console.log(videoTimeDelta);
      if (videoTimeDelta > 0) {
        this.exportRoot.paused = false;
        this.exportRoot.advance(videoTimeDelta * 1000);
        this.exportRoot.paused = true;
      }
    }

    this.contentVideoLastTime = videoTimeSecs;
    */
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  /*
  private convertTimelineFrameToVideoTime(timelineFrame: number | string): number {
    const videoFrame = Math.max(
      0,
      this.resolveTimelineFrame(timelineFrame) - this.startTimelineFrane,
    );

    const videoTimeInSecs = videoFrame / this.engine.config.videoFps;
    return (videoTimeInSecs);
  }
  */

  // ----------------------------------------------------------------------------------------------
  private get exportRoot(): SbScene {
    return (this.engine.exportRoot);
  }

  // ----------------------------------------------------------------------------------------------
  private navToJumpRoot(startFrameOrLabel: number, param: SbNavToParam): void {
    // TODO
    console.log('navToJumpRoot:', startFrameOrLabel, this.exportRoot.currentFrame, 'param:',
      param);

    if (param.action === SbNavActions.Play) {
      this.resume(SbPauseKinds.IsFromNav, startFrameOrLabel, param.endFrame);
    } else {
      this.exportRoot.stop(startFrameOrLabel);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private navToJumpVideo(timelineFrame: number, action: SbNavAction): void {
    this.exportRoot.stop(timelineFrame);

    if (action === SbNavActions.Play) {
      if (this.contentVideo?.videoElement) {
        this.contentVideo.seekToFrame(timelineFrame);
        this.contentVideoLastTime = this.contentVideo.videoElement.currentTime;
        this.resume(SbPauseKinds.IsFromNav);
      }
    } else if (this.contentVideo?.videoElement) {
      this.contentVideo.seekToFrame(timelineFrame);
      this.contentVideoLastTime = this.contentVideo.videoElement.currentTime;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private pauseTarget(pauseKind: SbPauseKind): void {
    this.pauseConfig[pauseKind] = true;
    if (this.contentVideo) {
      this.contentVideo.stop();
    } else {
      // TODO
      console.log('pause target', this.exportRoot.currentFrame,
        this.exportRoot.display.currentLabel);
      this.exportRoot.stop();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private async resumeTarget(pauseKind: SbPauseKind,
    startFrameOrLabel?: number | string,
    endFrameOrLabel?: number | string): Promise<void> {
    this.pauseConfig[pauseKind] = false;
    if (this.pauseConfig.isFromNav || this.pauseConfig.isFromUi) {
      return;
    }

    if (this.contentVideo) {
      if (startFrameOrLabel !== undefined) {
        this.exportRoot.stop(startFrameOrLabel);
      }

      this.contentVideo.play();
    } else {
      // TODO
      console.log('resume target', this.exportRoot.currentFrame,
        this.exportRoot.display.currentLabel,
        startFrameOrLabel !== undefined ? `starting from: ${startFrameOrLabel}` : '');

      if (startFrameOrLabel !== undefined) {
        if (endFrameOrLabel !== undefined) {
          this.exportRoot.playTo(endFrameOrLabel, startFrameOrLabel);
        } else {
          this.exportRoot.play(startFrameOrLabel);
        }
      } else {
        this.exportRoot.play();
      }

      // TODO: remove if na
      /*
      createjs.Ticker.removeEventListener(CjsEvents.TICK, this.onTickerTick);
      createjs.Ticker.addEventListener(CjsEvents.TICK, this.onTickerTick);

      this.testTimer?.stop();
      this.testTimer = new Timer(1000);
      console.log('test timer start');
      this.testTimer.start().then(() => {
        console.log('test timer stop');
        createjs.Ticker.removeEventListener(CjsEvents.TICK, this.onTickerTick);
        this.testTimer = undefined;
      });
      */
    }
  }

  // ----------------------------------------------------------------------------------------------
  // TODO: remove if na
  /*
  private onTickerTick(_eventObj: unknown): void {
    if (this.pauseConfig.isFromNav || this.pauseConfig.isFromUi) {
      return;
    }

    if (!this.exportRoot.paused) {
      return;
    }

    console.info('force play kludge');
    this.testTimer?.stop();
    this.testTimer = undefined;
    this.exportRoot.play();
  }
  */

  // ----------------------------------------------------------------------------------------------
  private get startTimelineFrame(): number {
    return (this.engine.startTimelineFrame);
  }

  // ----------------------------------------------------------------------------------------------
  private startWithTimeline(_startTimelineFrame: string | number): void {
    const { config } = this.engine;
    if (config.startCallback) {
      config.startCallback.call(config.startCallbackContext);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private startWithVideo(startTimelineFrame: string | number): void {
    if (!this.contentVideo?.videoElement) {
      return;
    }

    this.exportRoot.stop(startTimelineFrame);
    this.contentVideo.videoElement.currentTime = this.videoMath.convertTimelineFrameToVideoTime(
      startTimelineFrame,
    );

    this.contentVideo.play();
    this.contentVideo.setVisible(true);
  }
}
