import ICjsMcSymbolPrototype from './ICjsMcSymbolPrototype';

export default function CjsGetBounds(
  displayObject: ICjsMcSymbolPrototype,
  shouldTryNominalBounds = true,
): createjs.Rectangle | undefined {
  let bounds: createjs.Rectangle | undefined = displayObject.getBounds();
  if (shouldTryNominalBounds && (!bounds || bounds.isEmpty())) {
    bounds = displayObject.nominalBounds ?? undefined;
  }

  return (bounds);
}
