const SbMessagePanelEvents = {
  /**
   * Dispatched when the message panel has been closed.
   *
   * ```
   * function onMessagePanelClose(panel: SbMessagePanel): void;
   * ```
   */
  CLOSE: 'close',

  /**
   * Dispatched when a button on the message panel is pressed.
   *
   * ```
   * function onMessagePanelComplete(id: SbMessagePanelButtonId, panel: SbMessagePanel): void;
   * ```
   */
  COMPLETE: 'complete',
} as const;

export default SbMessagePanelEvents;
