import CjsDisplayObject from '../../../utils/createjs/objects/display/CjsDisplayObject';
import CjsGetContainer from '../../../utils/createjs/objects/CjsGetContainer';
import CjsGetText from '../../../utils/createjs/objects/CjsGetText';
import { SbMessagePanelButtonId, SbMessagePanelButtonIds, SbMessagePanelButtonIdsArray } from './SbMessagePanelButtonIds';
import CjsGetButton from '../../../utils/createjs/objects/CjsGetButton';
import ICjsBaseButton from '../../../utils/createjs/objects/buttons/ICjsBaseButton';
import IMovieClipDisplayCtor from '../../../utils/adobeAnimate/IMovieClipDisplayCtor';
import ISize from '../../../utils/geom/ISize';
import { CjsEvents } from '../../../utils/createjs/events/CjsEvents';
import CjsDragManager from '../../../utils/createjs/utils/dragManager/CjsDragManager';
import SbMessagePanelEvents from './SbMessagePanelEvents';
import SbDefaults from '../../data/defaults/SbDefaults';
import Timer from '../../../utils/timers/Timer';
import { SbMessagePanelCallback, SbMessagePanelRejectReasons } from './SbMessagePanelResults';
import CjsGetDisplayObject from '../../../utils/createjs/objects/CjsGetDisplayObject';
import SbMessagePanelAutoCloseConfig from './configs/SbMessagePanelAutoCloseConfig';
import { CjsGetTextMetrics } from '../../../utils/createjs/utils/CjsTextMetrics';
import SbRawMessagePanelConfig, { SbMessagePanelConfig, SbResolveMessagePanelConfig } from './configs/SbMessagePanelConfig';
import CjsButton from '../../../utils/createjs/objects/buttons/CjsButton';
import SbMessagePanelSingleton from './SbMessagePanelSingleton';

const DisplayObjectNames = {
  BackDisplay: 'back',
  MessageText: 'txtMessage',
  TextAreaNode: 'textAreaNode',
} as const;

export default class SbMessagePanel extends CjsDisplayObject<createjs.MovieClip> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  shouldCloseAfterSelection: boolean;

  private readonly buttonIdsByButton: Map<ICjsBaseButton, SbMessagePanelButtonId>;

  private readonly config: SbMessagePanelConfig;

  private stage: createjs.Stage;

  private dimBack: createjs.Shape;

  private isOpen: boolean;

  private dragManager: CjsDragManager | undefined;

  private _autoCloseDelayConfig: Required<SbMessagePanelAutoCloseConfig> | undefined;

  private autoCloseTimer: Timer | undefined;

  private textMessage: createjs.Text | undefined;

  private back: createjs.Container | undefined;

  private textAreaNode: createjs.Rectangle | undefined;

  private promisedResolve: SbMessagePanelCallback | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  get autoCloseDelayConfig(): SbMessagePanelAutoCloseConfig | undefined {
    return (this._autoCloseDelayConfig);
  }

  // ----------------------------------------------------------------------------------------------
  set autoCloseDelayConfig(value: SbMessagePanelAutoCloseConfig | undefined) {
    this._autoCloseDelayConfig = SbMessagePanel.BuildAutoCloseDelayConfig(value);
  }

  // ----------------------------------------------------------------------------------------------
  static Create(
    stage: createjs.Stage,
    message: string,
    MessagePanelDisplay: IMovieClipDisplayCtor,
    config?: SbRawMessagePanelConfig,
  ): Promise<SbMessagePanelButtonId> {
    if (SbMessagePanelSingleton.Instance) {
      return (new Promise((_, reject) => {
        reject(SbMessagePanelRejectReasons.AlreadyOpen);
      }));
    }

    const panelDisplay = new MessagePanelDisplay();
    const panel = new SbMessagePanel(stage, panelDisplay, message, config);
    const { canvasSize } = panel;
    if (canvasSize) {
      panel.setPosition(
        canvasSize.width / 2,
        canvasSize.height / 2,
      );
    }

    return (panel.open());
  }

  // ----------------------------------------------------------------------------------------------
  close(id: SbMessagePanelButtonId = SbMessagePanelButtonIds.Close): void {
    if (!this.isOpen) {
      return;
    }

    this.isOpen = false;

    if (this.autoCloseTimer) {
      this.autoCloseTimer.stop();
      this.autoCloseTimer = undefined;
    }

    this.stage.removeChild(this.dimBack, this.display);
    this.dispatchPromsedResolve(id);

    SbMessagePanelSingleton.Clear(this);
    this.emit(SbMessagePanelEvents.CLOSE, this);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();

    this.close();

    this.dragManager?.dispose();
    this.dragManager = undefined;

    this.dimBack.removeAllEventListeners();
    this.disposeButtons();
  }

  // ----------------------------------------------------------------------------------------------
  async open(): Promise<SbMessagePanelButtonId> {
    return (new Promise((resolve, reject) => {
      if (this.isOpen) {
        reject(SbMessagePanelRejectReasons.AlreadyOpen);
        return;
      }

      this.isOpen = true;
      this.promisedResolve = resolve;
      this.stage.addChild(this.dimBack, this.display);

      if (this._autoCloseDelayConfig) {
        this.autoCloseTimer = Timer.Create({
          callback: () => {
            this.close(this._autoCloseDelayConfig?.buttonId ?? SbMessagePanelButtonIds.Close);
          },

          delayMs: this._autoCloseDelayConfig.delayMs,
        });
      }
    }));
  }

  // ----------------------------------------------------------------------------------------------
  setAutoCloseDelay(config: SbMessagePanelAutoCloseConfig | true | null = null): this {
    this._autoCloseDelayConfig = SbMessagePanel.BuildAutoCloseDelayConfig(config ?? undefined);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setText(message: string): this {
    if (this.textMessage) {
      this.textMessage.text = message;
    }

    this.alignTextVerticlally();
    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private constructor(stage: createjs.Stage, display: createjs.MovieClip, message?: string,
    config?: SbRawMessagePanelConfig) {
    super(display);

    SbMessagePanelSingleton.Set(this);

    this.config = SbResolveMessagePanelConfig(config);

    this.stage = stage;
    this.dimBack = this.createDimBack();
    this.textMessage = CjsGetText(this.display, DisplayObjectNames.MessageText, message);
    this.back = CjsGetContainer(this.display, DisplayObjectNames.BackDisplay);
    this.buttonIdsByButton = this.initButtons();
    this.textAreaNode = this.initTextAreaNode();

    if (this.back) {
      this.dragManager = new CjsDragManager(this.display, this.back);
    }

    this._autoCloseDelayConfig = SbMessagePanel.BuildAutoCloseDelayConfig(this.config.autoClose);
    this.shouldCloseAfterSelection = true;
    this.isOpen = false;

    this.alignTextVerticlally();
  }

  // ----------------------------------------------------------------------------------------------
  private alignTextVerticlally(): void {
    const { textAreaNode, textMessage } = this;
    if (textMessage && textAreaNode) {
      const metrics = CjsGetTextMetrics(textMessage);
      const calculatedHeight = textMessage.getMeasuredLineHeight() * metrics.lines.length;
      textMessage.y = textAreaNode.y + (textAreaNode.height - calculatedHeight) / 2;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private get canvas(): HTMLCanvasElement | undefined {
    const canvasOrId = this.stage.canvas;
    return (canvasOrId instanceof HTMLCanvasElement
      ? canvasOrId
      : document.getElementById(canvasOrId as string) as HTMLCanvasElement);
  }

  // ----------------------------------------------------------------------------------------------
  private get canvasSize(): ISize | undefined {
    const { canvas } = this;
    if (!canvas) {
      return (undefined);
    }

    return ({
      width: canvas.width / this.stage.scaleX,
      height: canvas.height / this.stage.scaleY,
    });
  }

  // ----------------------------------------------------------------------------------------------
  private createDimBack(): createjs.Shape {
    const shape = new createjs.Shape();
    shape.on(CjsEvents.CLICK, () => {});
    this.renderDimBack(shape);
    return (shape);
  }

  // ----------------------------------------------------------------------------------------------
  private dispatchPromsedResolve(id: SbMessagePanelButtonId | undefined): void {
    if (this.promisedResolve && id) {
      this.promisedResolve(id);
      this.promisedResolve = undefined;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private disposeButtons(): void {
    this.buttonIdsByButton.forEach((_, button) => {
      button.dispose();
    });

    this.buttonIdsByButton.clear();
  }

  // ----------------------------------------------------------------------------------------------
  private static BuildAutoCloseDelayConfig(
    autoCloseDelayConfig: true | SbMessagePanelAutoCloseConfig | undefined,
  ): Required<SbMessagePanelAutoCloseConfig> | undefined {
    if (!autoCloseDelayConfig) {
      return (undefined);
    }

    if (autoCloseDelayConfig === true) {
      return ({
        buttonId: SbDefaults.MessagePanel.AutoClose.ButtonId,
        delayMs: SbDefaults.MessagePanel.AutoClose.DelayMs,
      });
    }

    return ({
      buttonId: autoCloseDelayConfig.buttonId ?? SbDefaults.MessagePanel.AutoClose.ButtonId,
      delayMs: autoCloseDelayConfig.delayMs,
    });
  }

  // ----------------------------------------------------------------------------------------------
  private initButton(buttonId: SbMessagePanelButtonId, labelText?: string): CjsButton | undefined {
    const button = CjsGetButton(this.display,
      `${buttonId}ButtonDisplay`, this.onButtonPressed, undefined, this);

    if (button && labelText) {
      button.buttonLabelText = labelText;
    }

    return (button);
  }

  // ----------------------------------------------------------------------------------------------
  private initButtons(): Map<ICjsBaseButton, SbMessagePanelButtonId> {
    const buttons = new Map<ICjsBaseButton, SbMessagePanelButtonId>();
    SbMessagePanelButtonIdsArray.forEach((buttonId) => {
      const button = this.initButton(buttonId, this.config.buttonLabels[buttonId]);
      if (button) {
        buttons.set(button, buttonId);
      }
    });

    return (buttons);
  }

  // ----------------------------------------------------------------------------------------------
  private initTextAreaNode(): createjs.Rectangle | undefined {
    const node = CjsGetDisplayObject(this.display, DisplayObjectNames.TextAreaNode);
    if (!node) {
      return (undefined);
    }

    const bounds = node.getTransformedBounds().clone();
    node.visible = false;
    return (bounds);
  }

  // ----------------------------------------------------------------------------------------------
  private onButtonPressed(button: ICjsBaseButton): void {
    const id = this.buttonIdsByButton.get(button);
    this.emit(SbMessagePanelEvents.COMPLETE, id, this);

    this.dispatchPromsedResolve(id);

    if (this.shouldCloseAfterSelection) {
      this.dispose();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private renderDimBack(shape: createjs.Shape): void {
    const { canvasSize } = this;
    if (!canvasSize) {
      return;
    }

    shape.alpha = this.config.dimBack.alpha;
    shape.graphics.clear()
      .beginFill(this.config.dimBack.color)
      .drawRect(0, 0, canvasSize.width, canvasSize.height)
      .endFill();
  }
}
