import GetBooleanValue from '../../../../utils/object/GetBooleanValue';
import GetNumberValue from '../../../../utils/object/GetNumberValue';
import GetStringValue from '../../../../utils/object/GetStringValue';
import GetValue from '../../../../utils/object/GetValue';
import GetWindowQueryParamValue from '../../../../utils/web/window/GetWindowQueryParamValue';
import { SbAssetLib } from '../../../data/SbAssetLib';
import { SbQueryParams } from '../../../data/SbQueryParams';
import SbDefaults from '../../../data/defaults/SbDefaults';
import SbEngineConfig from '../SbEngineConfig';
import { SbResolveEngineButtonsMenuConfig } from './SbEngineButtonsMenuConfig';

const DefaultEngineConfig = SbDefaults.EngineConfig;

// ------------------------------------------------------------------------------------------------
function resolveStorybookId(configIn: SbEngineConfig | undefined): string {
  const id = GetStringValue(configIn, 'storybookId');
  return (id || (GetWindowQueryParamValue(SbQueryParams.StorybookId) ?? ''));
}

// ------------------------------------------------------------------------------------------------
function resolveVideoFps(configIn: SbEngineConfig | undefined,
  lib?: Readonly<SbAssetLib>): number {
  const videoFps = GetNumberValue(configIn, 'videoFps', lib?.header.properties.fps);
  return (videoFps > 0 ? videoFps : DefaultEngineConfig.videoFps);
}

// ------------------------------------------------------------------------------------------------
function SbResolveEngineConfig(configIn?: SbEngineConfig,
  lib?: Readonly<SbAssetLib>, configOut?: SbEngineConfig): SbEngineConfig {
  configOut ??= {};
  configOut.buttonsMenu = SbResolveEngineButtonsMenuConfig(configIn?.buttonsMenu);

  configOut.contentVideoUrl = GetStringValue(configIn, 'contentVideoUrl',
    DefaultEngineConfig.contentVideoUrl);

  configOut.exitRedirectUrl = GetStringValue(configIn, 'exitRedirectUrl',
    DefaultEngineConfig.exitRedirectUrl);

  configOut.navButtonsSoundKey = GetStringValue(configIn, 'navButtonsSoundKey',
    DefaultEngineConfig.navButtonsSoundKey);

  configOut.pressIndicatorDelay = GetNumberValue(configIn, 'pressIndicatorDelay',
    DefaultEngineConfig.pressIndicatorDelay);

  configOut.sceneHandler = GetValue(configIn, 'sceneHandler',
    DefaultEngineConfig.sceneHandler);

  configOut.shouldUseVideoBuffer = GetBooleanValue(configIn, 'shouldUseVideoBuffer',
    DefaultEngineConfig.shouldUseVideoBuffer);

  configOut.shouldWaitForNavSounds = GetBooleanValue(configIn, 'shouldWaitForNavSounds',
    DefaultEngineConfig.shouldWaitForNavSounds);

  configOut.singleDeviceDelayMs = GetNumberValue(configIn, 'singleDeviceDelayMs',
    DefaultEngineConfig.singleDeviceDelayMs);

  configOut.startCallback = configIn?.startCallback ?? DefaultEngineConfig.startCallback;
  configOut.startCallbackContext = configIn?.startCallbackContext
    ?? DefaultEngineConfig.startCallbackContext;

  configOut.storybookId = resolveStorybookId(configIn);
  configOut.videoFps = resolveVideoFps(configIn, lib);
  return (configOut);
}

export default SbResolveEngineConfig;
