import ValidateResourceNames from '../../utils/adobeAnimate/ValidateResourceName';
import { CjsEvents } from '../../utils/createjs/events/CjsEvents';
import CjsEventsHandler from '../../utils/createjs/events/CjsEventsHandler';
import SbPlaySoundCompleteCallback from './SbPlaySoundCompleteCallback';
import SbPlaySoundConfig from './SbPlaySoundConfig';

function resolveLoopParam(loop: number | boolean | undefined): number {
  switch (typeof (loop)) {
    case 'boolean':
      return (loop ? -1 : 0);

    case 'number':
      return (loop);

    default:
      return (0);
  }
}

// ------------------------------------------------------------------------------------------------
function SbPlaySound(config: SbPlaySoundConfig): createjs.AbstractSoundInstance | undefined;
function SbPlaySound(
  audioId: string,
  volume?: number,
  loop?: boolean | number,
  completeCallback?: SbPlaySoundCompleteCallback,
  callbackContext?: unknown,
): createjs.AbstractSoundInstance | undefined;

function SbPlaySound(
  audioIdOrConfig: string | SbPlaySoundConfig,
  volume: number | undefined = 1.0,
  loop: boolean | number | undefined = false,
  completeCallback: SbPlaySoundCompleteCallback | undefined = undefined,
  callbackContext: unknown = undefined,
): createjs.AbstractSoundInstance | undefined {
  let config: SbPlaySoundConfig;
  if (typeof (audioIdOrConfig) === 'string') {
    config = {
      audioId: audioIdOrConfig,
      callbackContext,
      completeCallback,
      loop,
      volume,
    };
  } else {
    config = audioIdOrConfig;
  }

  const resolvedAudioId = ValidateResourceNames(config.audioId);
  const soundInstance = createjs.Sound.play(resolvedAudioId);

  if (soundInstance.playState === createjs.Sound.PLAY_FAILED) {
    console.warn(`failed to play sound: ${config.audioId}. resolved audio id: ${resolvedAudioId}`);
    soundInstance.destroy();
    return (undefined);
  }

  if (config.completeCallback) {
    const eventsHandler = new CjsEventsHandler(soundInstance);
    eventsHandler.once(CjsEvents.COMPLETE, config.completeCallback, config.callbackContext);
  }

  soundInstance.loop = resolveLoopParam(config.loop);
  soundInstance.volume = config.volume ?? 1.0;
  return (soundInstance);
}

export default SbPlaySound;
