/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McGirlSelectSocksQuiz from './McGirlSelectSocksQuiz';

async function socksTips(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_For-a-start-you-can-let-your_48.0k_stereo.mp3',
    'girl_wear_socks_tips1_start',
    'girl_wear_socks_tips1_end',
  );

  await scene.playSyncVoice(
    'SBL-0002_You-can-start-by-teaching-them_48.0k_stereo.mp3',
    'girl_wear_socks_tips2_start',
    'girl_wear_socks_tips2_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        scene.playToEnd(
          'girl_wear_socks_tips_menu_closed',
          () => {
            McGirlSelectSocksQuiz(scene);
          },
        );
      },
    },

    prev: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    restart: {
      callback: () => {
        McGirlWearSocks(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_wear_socks_how_aac_i',
        soundId: 'SBL-0007_I',
      },
      {
        frameOrLabel: 'girl_wear_socks_how_aac_wear',
        soundId: 'SBL-0012_Wear',
      },
      {
        frameOrLabel: 'girl_wear_socks_how_aac_socks',
        soundId: 'SBL-0013_Socks',
      },
      {
        frameOrLabel: 'girl_wear_socks_how_aac_feet',
        soundId: 'SBL-0014_Feet',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          socksTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearSocks(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-socks-on-my-feet',
    'girl_wear_socks_how_start',
    'girl_wear_socks_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlWearSocks(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearSocks(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'girl_wear_socks_animation_end',
    'girl_wear_socks_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      girlWearSocksButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'girlWearSocksButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McGirlWearSocks(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-socks',
    'girl_wear_socks_start',
    'girl_wear_socks_end',
  ).then(() => {
    wearHow(scene);
  });
}
