import IncludesIs from '../../../../utils/collections/arrays/IncludesIs';
import IsPlainObject from '../../../../utils/object/IsPlainObject';
import SbDefaults from '../../../data/defaults/SbDefaults';
import { SbNavToDirection, SbNavToDirectionsArray } from './SbNavToDirections';
import SbNavToParams from './SbNavToParams';
import SbIsPresentNavConfig from './SbIsPresentNavConfig';

type SbPresentNavConfig = {
  defaultSelectedNavButtonId?: SbNavToDirection;
  navButtonsToHide?: SbNavToDirection[];
  params?: SbNavToParams;
};

// ------------------------------------------------------------------------------------------------
function isNavToParams(data: unknown): data is SbNavToParams {
  return (IsPlainObject(data)
    && Object.keys(data).every((key) => IncludesIs(key, SbNavToDirectionsArray)));
}

// ------------------------------------------------------------------------------------------------
function SbResolvePresentNavConfig(config: unknown): SbPresentNavConfig {
  let out: SbPresentNavConfig;

  if (SbIsPresentNavConfig(config)) {
    out = config;
  } else if (isNavToParams(config)) {
    out = {
      params: config,
    };
  } else {
    out = {};
  }

  out.defaultSelectedNavButtonId ??= SbDefaults.SelectedNavButtonId;
  return (out);
}

export {
  SbPresentNavConfig,
  SbResolvePresentNavConfig,
};
