import SbAacSceneHelper from '../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../engine/storybookScene/SbScene';
import McGirlHowWearClothes from './McGirlHowWearClothes';
import McGirlPresentClothes from './McGirlPresentClothes';
import McGirlWearShirt from './shirt/McGirlWearShirt';

export default function McGirlHowWearClothesAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_how_wear_clothes_aac_how',
        soundId: 'SBL-0027_How_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'girl_how_wear_clothes_aac_i',
        soundId: 'SBL-0028_I',
      },
      {
        frameOrLabel: 'girl_how_wear_clothes_aac_wear',
        soundId: 'SBL-0029_wear',
      },
      {
        frameOrLabel: 'girl_how_wear_clothes_aac_clothes',
        soundId: 'SBL-0002_Clothes',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McGirlWearShirt(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlHowWearClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlPresentClothes(scene);
        },
      },
    });
  });
}
