import CjsApplyColor from '../../../utils/display/CjsApplyColor';
import RecursiveReadonly from '../../../utils/object/RecursiveReadonly';
import SbDefaults from '../../data/defaults/SbDefaults';
import SbDesignData from '../../data/SbDesignData';
import SbUserStorybookSettingsData from '../../data/settings/storybook/SbUserStorybookSettingsData';

function UpdateCaptionHilitesFromSettings(captionHilitesDisplay: createjs.MovieClip,
  storybookSettings: RecursiveReadonly<SbUserStorybookSettingsData>): void {
  captionHilitesDisplay.visible = storybookSettings.captionHilitesEnabled;
  if (captionHilitesDisplay.visible) {
    const { Colors } = SbDesignData.CaptionHilites;
    const color = Colors[storybookSettings.captionHiliteColorIndex]
      ?? Colors[SbDefaults.Settings.storybook.captionHiliteColorIndex]
      ?? Colors[0];

    CjsApplyColor(captionHilitesDisplay, color);
  }
}

export default UpdateCaptionHilitesFromSettings;
