import IsValidNumber from '../number/IsValidNumber';
import GetValue from './GetValue';

/**
 * Returns the number value of a property key within the top level of an object, or returns a
 * default value if the key is not found.
 *
 * @param source - The object to search.
 * @param key - The key for the property on source. Must exist at the top level of the
 * source object (no periods).
 * @param [defaultValue=0] - The default value to use if the key does not exist.
 *
 * @returns The value if found; otherwise, `defaultValue` (`0` if none provided).
 */
export default function GetNumberValue(source: unknown, key: string, defaultValue = 0): number {
  const value = GetValue(source, key, defaultValue);
  return (IsValidNumber(value) ? value : defaultValue);
}
