import SbScene from '../../../engine/storybookScene/SbScene';
import McBoySelectedAac from './McBoySelectedAac';
import McChooseCharacter from '../McChooseCharacter';
import McIntro from '../McIntro';

export default function McBoySelected(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL0001_YouhavechosenBoy_480k_stereo',
    'boy_selected_start',
    'boy_selected_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoySelectedAac(scene);
        },
      },

      prev: {
        callback: () => {
          McChooseCharacter(scene);
        },
      },

      restart: {
        callback: () => {
          McIntro(scene);
        },
      },
    });
  });
}
