const CjsButtonLabels = {
  Disabled: 'disabled',
  Down: 'down',
  Hit: 'hit',
  Out: 'out',
  Over: 'over',
  Selected: 'selected',
} as const;

type CjsButtonLabel = typeof CjsButtonLabels[keyof typeof CjsButtonLabels];

const CjsButtonLabelsArray = Object.values(CjsButtonLabels) as ReadonlyArray<CjsButtonLabel>;

export {
  CjsButtonLabel,
  CjsButtonLabels,
  CjsButtonLabelsArray,
};
