import HasOwnProperty from './HasOwnProperty';

/**
 * Returns the value of a property key within the top level of an object, or returns a
 * default value if the key is not found. This is a generic function; for primitive types, it's
 * recommended to use one of the specific `getXvalue` functions instead.
 *
 * @param source - The object to search.
 * @param key - The key for the property on source. Must exist at the top level of the
 * source object (no periods).
 * @param [defaultValue] - The default value to use if the key does not exist.
 *
 * @returns The value if found; otherwise, `defaultValue`.
 */
export default function GetValue<T>(source: unknown, key: string, defaultValue: T): T {
  if (!source) {
    return (defaultValue);
  }

  const sourceType = typeof (source);
  if (sourceType === 'number' || sourceType === 'string' || sourceType === 'boolean') {
    return (defaultValue);
  }

  const sourceRecord = <Record<string, T>>source;
  if (HasOwnProperty(sourceRecord, key) && sourceRecord[key] !== undefined) {
    return (sourceRecord[key] as T);
  }

  return (defaultValue);
}
