import SbUserSettingsData from '../SbUserSettingsData';
import SbUserSettingsDataParser from './parsers/SbUserSettingsDataParser';

export const SbUserSettingsStorageKey = 'settings';

export default class SbUserSettingsStorage {
  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  static Read(): SbUserSettingsData | undefined {
    let rawData: unknown;
    try {
      rawData = localStorage.getItem(SbUserSettingsStorageKey);
    } catch (error) {
      console.warn(error);
      return (undefined);
    }

    const parserData = SbUserSettingsDataParser(rawData);
    if (!parserData.wasSourceMirrored) {
      if (SbUserSettingsStorage.Write(parserData.target)) {
        console.info('updated incoming settings data');
      } else {
        console.warn('unable to update incoming settings data');
      }
    }

    return (parserData.target);
  }

  // ----------------------------------------------------------------------------------------------
  static Write(settings: SbUserSettingsData): boolean {
    try {
      const settingsString = JSON.stringify(settings);
      localStorage.setItem(SbUserSettingsStorageKey, settingsString);
    } catch (error) {
      console.warn(error);
      return (false);
    }

    return (true);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private constructor() {
    throw (new Error('instances of this class may not be created'));
  }
}
