/* eslint-disable no-use-before-define */
import SbScene from '../../../../engine/storybookScene/SbScene';
import SbSetSceneSpeakerButton from '../../../../engine/storybookScene/utils/SbSceneSpeakerButton';
import { SbButtonsMenuConfig } from '../../../../engine/ui/buttonsMenu/config/SbButtonsMenuConfig';
import McBoyHelpWearCapQuiz from './McBoyHelpWearCapQuiz';

function presentCompletedMenu(scene: SbScene): void {
  scene.engine.presentNav({
    next: {
      callback: () => {
        McBoyHelpWearCapQuiz(scene);
      },
    },

    prev: {
      callback: () => {
        McBoySelectCapQuiz(scene);
      },
    },

    restart: {
      callback: () => {
        McBoySelectCapQuiz(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function buildMenuConfig(areButtonsEnabled: boolean, scene: SbScene): SbButtonsMenuConfig {
  const isDisabled = !areButtonsEnabled;
  return ({
    buttons: {
      boyCapMenuButton1Display: {
        isCorrect: false,
        isDisabled,
      },

      boyCapMenuButton2Display: {
        isCorrect: false,
        isDisabled,
      },

      boyCapMenuButton0Display: {
        isCorrect: true,
        isDisabled,
        callback: (): void => {
          scene.playSyncVoice(
            'SBL-0002_Thats-right-You-have-chosen_cap2_48.0k_stereo.mp3',
            'boy_select_cap_quiz_correct_start',
            'boy_select_cap_quiz_correct_end',
          ).then(() => {
            presentCompletedMenu(scene);
          });
        },
      },
    },

    defaultButtonName: 'boyCapMenuButton0Display',
    isQuiz: true,
  });
}

// ----------------------------------------------------------------------------------------------
function presentMenu(scene: SbScene, areButtonsEnabled: boolean): void {
  scene.engine.presentMenu(buildMenuConfig(areButtonsEnabled, scene));
}

// ----------------------------------------------------------------------------------------------
export default function McBoySelectCapQuiz(scene: SbScene): void {
  const labelPfx = 'boy_menu_select_cap_quiz_voc';
  const startLabel = `${labelPfx}_begin`;
  const endLabel = `${labelPfx}_end`;

  scene.stop(startLabel);
  presentMenu(scene, false);

  const quizPromptVoiceSoundId = 'SBL-0001_Can-you-select-the-cap';
  SbSetSceneSpeakerButton(scene, false, quizPromptVoiceSoundId);

  scene.playSyncVoice(
    quizPromptVoiceSoundId,
    startLabel,
    endLabel,
  ).then(() => {
    SbSetSceneSpeakerButton(scene, true);
    presentMenu(scene, true);
  });
}
