import IMovieClipDisplayCtor from '../../../adobeAnimate/IMovieClipDisplayCtor';
import CjsGetContainer from '../CjsGetContainer';
import CjsSlider from './CjsSlider';
import CjsSliderCallback from './CjsSliderCallback';
import CjsSliderEvents from './CjsSliderEvents';

function CjsCreateSlider<
  SliderDisplayType extends createjs.Container = createjs.Container
>(container: createjs.Container, displayName: string,
  sliderUpdateCallback?: CjsSliderCallback | undefined, context?: unknown, value?: number,
  handleHitAreaOrAnLibClipCtor?: createjs.DisplayObject
  | IMovieClipDisplayCtor): CjsSlider<SliderDisplayType> | undefined {
  const sliderContainer = CjsGetContainer<SliderDisplayType>(container, displayName);
  if (!sliderContainer) {
    return (undefined);
  }

  const slider = new CjsSlider<SliderDisplayType>(sliderContainer, value,
    handleHitAreaOrAnLibClipCtor);

  if (sliderUpdateCallback) {
    slider.on(CjsSliderEvents.CHANGED, sliderUpdateCallback, context);
  }

  return (slider);
}

export default CjsCreateSlider;
