import SbScene from '../../../engine/storybookScene/SbScene';
import McBoySelected from './McBoySelected';
import McIntro from '../McIntro';
import McBoyLetsRead from './McBoyLetsRead';
import SbAacSceneHelper from '../../../engine/storybookScene/SbAacSceneHelper';

export default function McBoySelectedAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_selected_aac_choose',
        soundId: 'SBL-0035_choose_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'boy_selected_aac_boy',
        soundId: 'SBL-0037_boy_48.0k_stereo.mp3',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoyLetsRead(scene);
        },
      },

      prev: {
        callback: () => {
          McBoySelected(scene);
        },
      },

      restart: {
        callback: () => {
          McIntro(scene);
        },
      },
    });
  });
}
