import EventEmitter from 'eventemitter3';
import CjsDragManagerEvents from './CjsDragManagerEvents';
import CjsEventsHandler from '../../events/CjsEventsHandler';
import { CjsEvents } from '../../events/CjsEvents';
import IPoint from '../../../geom/IPoint';
import Clamp from '../../../math/Clamp';

export default class CjsDragManager<
  DisplayType extends createjs.DisplayObject = createjs.DisplayObject
> extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly displayTarget: DisplayType;

  dragBounds: createjs.Rectangle | null;

  private readonly cjsEventsHandler: CjsEventsHandler;

  private readonly inputTarget: createjs.DisplayObject;

  private dragOffset: IPoint;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(displayTarget: DisplayType, inputTarget?: createjs.DisplayObject) {
    super();

    this.displayTarget = displayTarget;
    this.inputTarget = inputTarget ?? this.displayTarget;

    this.cjsEventsHandler = new CjsEventsHandler(this.inputTarget)
      .on(CjsEvents.MOUSE_DOWN, this.onDisplayMouseDown, this)
      .on(CjsEvents.PRESS_MOVE, this.onDisplayPressMove, this)
      .on(CjsEvents.PRESS_UP, this.onDisplayPressUp, this);

    this.dragOffset = {
      x: 0,
      y: 0,
    };

    this.dragBounds = null;
  }

  // ----------------------------------------------------------------------------------------------
  clearDragBounds(): this {
    this.dragBounds = null;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.removeAllListeners();
    this.cjsEventsHandler.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  setDragBounds(sourceBounds: createjs.Rectangle): this {
    if (this.dragBounds) {
      this.dragBounds.copy(sourceBounds);
    } else {
      this.dragBounds = sourceBounds.clone();
    }

    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onDisplayMouseDown(event: createjs.MouseEvent): void {
    this.dragOffset.x = event.localX;
    this.dragOffset.y = event.localY;
    this.emit(CjsDragManagerEvents.START, event, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onDisplayPressMove(event: createjs.MouseEvent): void {
    let x = this.displayTarget.x + event.localX - this.dragOffset.x;
    let y = this.displayTarget.y + event.localY - this.dragOffset.y;
    if (this.dragBounds) {
      x = Clamp(x, this.dragBounds.x, this.dragBounds.x + this.dragBounds.width);
      y = Clamp(y, this.dragBounds.y, this.dragBounds.y + this.dragBounds.height);
    }

    this.displayTarget.x = x;
    this.displayTarget.y = y;
    this.emit(CjsDragManagerEvents.DRAG, event, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onDisplayPressUp(event: createjs.MouseEvent): void {
    this.dragOffset.x = 0;
    this.dragOffset.y = 0;
    this.emit(CjsDragManagerEvents.STOP, event, this);
  }
}
