import SbScene from '../../../../engine/storybookScene/SbScene';
import McGirlHelpHerWearClothesQuiz from './McGirlHelpHerWearClothesQuiz';
import McGirlThisHowWhereClothes from './McGirlThisHowWhereClothes';

// ------------------------------------------------------------------------------------------------
function complete(scene: SbScene): void {
  scene.stop('girl_complete');
  scene.engine.submitUserPerformanceData();
}

// ------------------------------------------------------------------------------------------------
async function playOutroTipsVoc(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_Parents-you-may-want-to-ask-t_48.0k_stereo.mp3',
    'girl_outro_tips_voc_begin',
    'girl_outro_tips_voc_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        complete(scene);
      },
    },

    prev: {
      callback: () => {
        McGirlHelpHerWearClothesQuiz(scene);
      },
    },

    restart: {
      callback: () => {
        McGirlThisHowWhereClothes(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
export default function McBoyOutro(scene: SbScene): void {
  scene.playTo(
    'girl_outro_tips_end',
    'girl_outro_tips_begin',
    () => {
      playOutroTipsVoc(scene);
    },
  );
}
