import EventEmitter from 'eventemitter3';
import { GlobalDomEvents } from '../../../utils/web/dom/GlobalDomEvents';
import { KeyCodes } from '../../../utils/web/keys/KeyCodes';
import SbInputDeviceHandlerEvents from './SbInputDeviceHandlerEvents';
import IStorybookEngine from '../../IStorybookEngine';
import { SbInputDevices } from '../../data/SbInputDevices';
import Timer from '../../../utils/timers/Timer';
import SbDefaults from '../../data/defaults/SbDefaults';

export default class SbInputDeviceHandler extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  private singleSwitchTimer: Timer | undefined;

  private _isEnabled: boolean;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    super();

    this.engine = engine;
    this.onDocumentKeyUp = this.onDocumentKeyUp.bind(this);
    this._isEnabled = false;
    this.updateFromSettings();
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.isEnabled = false;
    this.removeAllListeners();
  }

  // ----------------------------------------------------------------------------------------------
  isDualSwitchEnabled(): boolean {
    return (this.engine.userSettingsData.device.inputDevice === SbInputDevices.DualSwitch);
  }

  // ----------------------------------------------------------------------------------------------
  get isEnabled(): boolean {
    return (this._isEnabled);
  }

  // ----------------------------------------------------------------------------------------------
  set isEnabled(value: boolean) {
    this.setEnabled(value);
  }

  // ----------------------------------------------------------------------------------------------
  isSingleSwitchEnabled(): boolean {
    return (this.engine.userSettingsData.device.inputDevice === SbInputDevices.SingleSwitch);
  }

  // ----------------------------------------------------------------------------------------------
  setEnabled(isEnabled: boolean): this {
    if (isEnabled === this._isEnabled) {
      return (this);
    }

    this._isEnabled = isEnabled;

    if (this._isEnabled) {
      document.addEventListener(GlobalDomEvents.KeyUp, this.onDocumentKeyUp);
    } else {
      document.removeEventListener(GlobalDomEvents.KeyUp, this.onDocumentKeyUp);
    }

    this.handleSingleSwitch();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  updateFromSettings(): boolean {
    const newEnabled = this.isSingleSwitchEnabled() || this.isDualSwitchEnabled();
    if (this.isEnabled !== newEnabled) {
      this.setEnabled(newEnabled);
    } else {
      this.handleSingleSwitch();
    }

    return (this.isEnabled);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private handleSingleSwitch(): boolean {
    if (this.isEnabled && this.isSingleSwitchEnabled()) {
      this.handleSingleSwitchEnable();
      return (true);
    }

    this.handleSingleSwitchDisable();
    return (false);
  }

  // ----------------------------------------------------------------------------------------------
  private handleSingleSwitchDisable(): void {
    this.singleSwitchTimer?.stop();
    this.singleSwitchTimer = undefined;
  }

  // ----------------------------------------------------------------------------------------------
  private handleSingleSwitchEnable(): void {
    if (this.singleSwitchTimer) {
      return;
    }

    this.singleSwitchTimer = Timer.Create({
      callback: () => {
        this.emit(SbInputDeviceHandlerEvents.NEXT, this);
      },

      delayMs: SbDefaults.EngineConfig.singleDeviceDelayMs,
      repeat: -1,
    });
  }

  // ----------------------------------------------------------------------------------------------
  private onDocumentKeyUp(event: KeyboardEvent): void {
    if (event.code === KeyCodes.Space) {
      this.emit(SbInputDeviceHandlerEvents.NEXT, this);
    } else if (event.code === KeyCodes.Enter) {
      this.emit(SbInputDeviceHandlerEvents.SELECT, this);
    }
  }
}
