import SbScene from '../../../engine/storybookScene/SbScene';
import McBoyHowWearClothesAac from './McBoyHowWearClothesAac';
import McBoyMyClothesAac from './McBoyMyClothesAac';
import McBoyPresentClothes from './McBoyPresentClothes';

export default function McBoyHowWearClothes(scene: SbScene): void {
  scene.stop('boy_how_wear_clothes');
  scene.playVoices('SBL-0002_How-do-I-wear-my-clothes')
    .then(() => {
      scene.engine.presentNav({
        next: {
          callback: () => {
            McBoyHowWearClothesAac(scene);
          },
        },

        prev: {
          callback: () => {
            McBoyMyClothesAac(scene);
          },
        },

        restart: {
          callback: () => {
            McBoyPresentClothes(scene);
          },
        },
      });
    });
}
