import IMovieClipDisplayCtor from '../../../utils/adobeAnimate/IMovieClipDisplayCtor';
import IStorybookEngine from '../../IStorybookEngine';

function SbResolveSceneDisplay(engine: IStorybookEngine,
  displayOrCtorName?: createjs.MovieClip | string): createjs.MovieClip {
  if (displayOrCtorName instanceof createjs.MovieClip) {
    return (displayOrCtorName);
  }

  if (!displayOrCtorName) {
    return (new createjs.MovieClip());
  }

  const displayClasses = engine.lib.animateClasses as unknown as
    Record<string, IMovieClipDisplayCtor>;
  const DisplayClassCtor = displayClasses ? displayClasses[displayOrCtorName] : undefined;
  return (DisplayClassCtor ? new DisplayClassCtor() : new createjs.MovieClip());
}

export default SbResolveSceneDisplay;
