/* eslint-disable no-use-before-define */
import SbScene from '../../engine/storybookScene/SbScene';
import McChooseCharacter from './McChooseCharacter';
import McIntro from './McIntro';

// ------------------------------------------------------------------------------------------------
export default function McPrep(scene: SbScene): void {
  scene.stop('prep');
  scene.playVoices(
    [
      'SBL-0001_For-a-more-immersive-experienc_48.0k_stereo.mp3',
      'SBL-0002_shirt-pants-socks-shoes-cap-an_48.0k_stereo.mp3',
    ],
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McChooseCharacter(scene);
        },
      },

      prev: {
        callback: () => {
          McIntro(scene);
        },
      },

      restart: {
        callback: () => {
          McIntro(scene);
        },
      },
    });
  });
}
