import SbUiComponent from '../SbUiComponent';
import CjsCompoundButton from '../../../utils/createjs/objects/buttons/CjsCompoundButton';
import SbPasscodePanelEvents from './SbPasscodePanelEvents';
import ChallengeData from './challengeExpression/ChallengeData';
import GenerateChallenge from './challengeExpression/ChallengeExpression';
import ICjsCompoundButton from '../../../utils/createjs/objects/buttons/ICjsCompoundButton';
import IStorybookEngine from '../../IStorybookEngine';

export default class SbPasscodePanel extends SbUiComponent {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly numberButtons: ICjsCompoundButton[];

  private readonly txtChallenge: createjs.Text | undefined;

  private readonly txtChallengeAnswer: createjs.Text | undefined;

  private clearButton: CjsCompoundButton | undefined;

  private submitButton: CjsCompoundButton | undefined;

  private feedbackTween: createjs.Tween | undefined;

  private challengeData: ChallengeData | undefined;

  private origTextColor: string;

  private answer: number;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: createjs.MovieClip, engine: IStorybookEngine) {
    super(container, engine);

    this.numberButtons = this.initNumberButtons();
    this.submitButton = this.initSubmitButton();
    this.clearButton = this.initClearButton();

    this.txtChallenge = this.initChallengeText('txtChallenge');

    this.txtChallengeAnswer = this.initChallengeText('txtChallengeAnswer');
    this.origTextColor = this.txtChallengeAnswer?.color ?? '#000';

    this.answer = 0;
  }

  // ----------------------------------------------------------------------------------------------
  clearAnswer(): this {
    this.answer = 0;
    this.updateAfterResponse();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  present(): this {
    this.refreshChallenge();
    this.submitButton?.setEnabled(true);
    this.visible = true;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  unpresent(): this {
    this.disposeFeedbackTween();
    this.submitButton?.setEnabled(false);
    this.visible = false;
    return (this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected override onInit(): void {
    super.onInit(this.engine.lib.animateClasses.CloseXButtonHitBoxDisplay);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private addNumberOntoAnswer(value: number): void {
    let numberAsTest = (this.answer + value.toString(10));
    numberAsTest = numberAsTest.substring(numberAsTest.length - 1);
    this.answer = Number.parseInt(numberAsTest, 10);
  }

  // ----------------------------------------------------------------------------------------------
  private disposeFeedbackTween(): void {
    if (this.feedbackTween) {
      createjs.Tween.removeTweens(this.feedbackTween.target);
      this.feedbackTween = undefined;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private enableNumberButtons(isEnabled: boolean): void {
    this.numberButtons.forEach((button) => {
      button.enabled = isEnabled;
    });
  }

  // ----------------------------------------------------------------------------------------------
  private feedbackIncorrect(): void {
    this.flashAnswerText('#f00');
    this.feedbackTween?.call(() => {
      this.submitButton?.setEnabled(true);
      this.emit(SbPasscodePanelEvents.SUBMIT, false, this);
    });
  }

  // ----------------------------------------------------------------------------------------------
  private feedbackCorrect(): void {
    this.flashAnswerText('#0f0');
    this.feedbackTween?.call(() => {
      this.emit(SbPasscodePanelEvents.SUBMIT, true, this);
    });
  }

  // ----------------------------------------------------------------------------------------------
  private flashAnswerText(cssColor: string): void {
    if (!this.txtChallengeAnswer) {
      return;
    }

    this.disposeFeedbackTween();

    const text = this.txtChallengeAnswer;
    this.feedbackTween = createjs.Tween.get(text);
    for (let index = 0; index < 7; index += 1) {
      this.feedbackTween
        .call(() => {
          text.color = cssColor;
        })
        .wait(60)
        .call(() => {
          text.color = this.origTextColor;
        })
        .wait(60);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initChallengeText(displayName: string): createjs.Text | undefined {
    const textObject = this.initText<createjs.Text>(displayName);
    if (textObject) {
      textObject.visible = false;
    }

    return (textObject);
  }

  // ----------------------------------------------------------------------------------------------
  private initClearButton(): CjsCompoundButton | undefined {
    return (this.initCompoundButton('passcodeButtonClear', this.onClearPressed));
  }

  // ----------------------------------------------------------------------------------------------
  private initNumberButtons(): ICjsCompoundButton[] {
    const buttons: ICjsCompoundButton[] = [];
    let index = 1;
    let button = this.initCompoundButton(`passcodeButton${index}`, this.onNumberButtonPressed,
      undefined, this.engine.lib.animateClasses.PasscodeButtonBackDisplay);

    while (button) {
      buttons.push(button);
      index += 1;
      button = this.initCompoundButton(`passcodeButton${index}`, this.onNumberButtonPressed);
    }

    return (buttons);
  }

  // ----------------------------------------------------------------------------------------------
  private initSubmitButton(): CjsCompoundButton | undefined {
    return (this.initCompoundButton('passcodeButtonSubmit', this.onSubmitPressed));
  }

  // ----------------------------------------------------------------------------------------------
  private onClearPressed(): void {
    this.clearAnswer();
  }

  // ----------------------------------------------------------------------------------------------
  private onNumberButtonPressed(button: ICjsCompoundButton): void {
    const buttonIndex = this.numberButtons.indexOf(button);
    if (buttonIndex > -1) {
      this.addNumberOntoAnswer(buttonIndex + 1);
      this.updateAfterResponse();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onSubmitPressed(): void {
    this.submitButton?.setEnabled(false);
    const isCorrect = this.challengeData !== undefined && this.answer === this.challengeData.value;
    if (isCorrect) {
      this.feedbackCorrect();
    } else {
      this.feedbackIncorrect();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private refreshChallenge(): void {
    this.challengeData = GenerateChallenge();
    if (this.txtChallenge) {
      this.txtChallenge.text = `${this.challengeData.expression}?`;
      this.txtChallenge.visible = true;
    }

    this.answer = 0;
    this.updateAfterResponse();
  }

  // ----------------------------------------------------------------------------------------------
  private updateAfterResponse(): void {
    if (this.txtChallengeAnswer) {
      this.txtChallengeAnswer.text = this.answer !== 0 ? this.answer.toString(10) : '';
      this.txtChallengeAnswer.visible = true;
    }

    this.enableNumberButtons(this.answer > -100 && this.answer < 100);
  }
}
