import CjsGetMovieClip from '../../../utils/createjs/objects/CjsGetMovieClip';
import CjsButton from '../../../utils/createjs/objects/buttons/CjsButton';
import CjsCheckbox from '../../../utils/createjs/objects/buttons/CjsCheckbox';
import SbDesignData from '../../data/SbDesignData';

type FillObject = {
  bounds: createjs.Rectangle,
  display: createjs.Shape,
};

export default class SbCaptionHiliteColorCheckbox {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly checkbox: CjsCheckbox;

  private readonly onFill: FillObject | undefined;

  private readonly offFill: FillObject | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(checkbox: CjsCheckbox) {
    this.checkbox = checkbox;
    this.onFill = SbCaptionHiliteColorCheckbox.GetFillDisplay(this.checkbox.onButton);
    this.offFill = SbCaptionHiliteColorCheckbox.GetFillDisplay(this.checkbox.offButton);
  }

  // ----------------------------------------------------------------------------------------------
  setColor(color: string): this {
    SbCaptionHiliteColorCheckbox.ApplyColorToFill(this.onFill, color);
    SbCaptionHiliteColorCheckbox.ApplyColorToFill(this.offFill, color);
    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private static ApplyColorToFill(fillObject: FillObject | undefined, color: string): void {
    if (!fillObject) {
      return;
    }

    const DesignStroke = SbDesignData.CaptionHilites.Stroke;
    fillObject.display.graphics
      .clear()
      .setStrokeStyle(DesignStroke.Thickness)
      .beginStroke(DesignStroke.Color)
      .beginFill(color)
      .drawRect(0, 0, fillObject.bounds.width, fillObject.bounds.height);
  }

  // ----------------------------------------------------------------------------------------------
  private static GetFillDisplay(button: CjsButton | undefined): FillObject | undefined {
    if (!(button?.display instanceof createjs.MovieClip)) {
      return (undefined);
    }

    const fillDisplay = CjsGetMovieClip(button.display, 'fill_display');
    if (!fillDisplay) {
      return (undefined);
    }

    const node = CjsGetMovieClip(fillDisplay, 'fill_placeholder');
    if (!node) {
      return (undefined);
    }

    const bounds = node.getBounds();
    if (!bounds) {
      return (undefined);
    }

    fillDisplay.removeChild(node);

    const shape = new createjs.Shape();
    shape.regX = bounds.width / 2;
    shape.regY = bounds.height / 2;
    fillDisplay.addChild(shape);

    return ({
      bounds: bounds.clone(),
      display: shape,
    });
  }
}
