export function BuildAssetFilename(basePart: string, extensionWithoutDot: string,
  prefix = '', suffix = ''): string {
  return (`${prefix}${basePart}${suffix}.${extensionWithoutDot}`);
}

// ----------------------------------------------------------------------------------------------
export function BuildUrlPath(...folderNames: string[]): string {
  return (`${folderNames.join('/')}/`);
}

// ----------------------------------------------------------------------------------------------
/** @see https://www.petermorlion.com/iterating-a-typescript-enum/ */
export function GetEnumKeys<E extends object, K extends keyof E = keyof E>(enumObj: E): K[] {
  return (Object.keys(enumObj).filter((k) => Number.isNaN(+k)) as K[]);
}

// ------------------------------------------------------------------------------------------------
export function IsEnumMemberOf<Enum>(id: unknown, enums: Record<string, unknown>): id is Enum {
  return (Object.values(enums).includes(id));
}

// ------------------------------------------------------------------------------------------------
export function IsFiniteNumber(value: unknown): value is number {
  switch (typeof (value)) {
    case 'number':
      return (Number.isFinite(value));

    case 'string':
      return (IsFiniteNumber(Number.parseFloat(value)));

    default:
      return (false);
  }
}

// ------------------------------------------------------------------------------------------------
export function ToReadonlyValues<T>(...values: T[]): readonly T[] {
  return ([...values]);
}
