import CjsButton from '../../../utils/createjs/objects/buttons/CjsButton';
import Timer from '../../../utils/timers/Timer';
import { SbFrameLabels } from '../../data/SbAssetLib';
import SbDesignData from '../../data/SbDesignData';
import SbScene from '../../storybookScene/SbScene';
import SbUiComponent from '../SbUiComponent';
import SbMainPanel from '../main/SbMainPanel';
import SbTutorialManagerEvents from './SbTutorialManagerEvents';

const DisplayObjectNames = {
  NextButton: 'nextButtonDisplay',
} as const;

export default class SbTutorialManager extends SbUiComponent {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly scene: SbScene;

  private nextButton: CjsButton | undefined;

  private introDelayTimer: Timer | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(mainPanel: SbMainPanel, container: createjs.MovieClip) {
    super(container, mainPanel.engine);

    this.scene = new SbScene(this.engine, container);

    this.nextButton = this.initButton(DisplayObjectNames.NextButton, this.onNextButtonPressed,
      this.engine.lib.animateClasses.TutorialSkipNavButtonHitDisplay);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    this.nextButton?.dispose();
    this.disposeIntroDelayTimer();
    super.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  present(): void {
    this.visible = true;
    this.scene.stop(0);

    this.introDelayTimer = Timer.Create({
      delayMs: SbDesignData.TutorialIntroDelayMs,
      callback: () => {
        this.setNextPage();
      },
    });
  }

  // ----------------------------------------------------------------------------------------------
  unpresent(): void {
    this.visible = false;
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected override onInit(): void {
    super.onInit(this.engine.lib.animateClasses.TutorialSkipNavButtonHitDisplay);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private disposeIntroDelayTimer(): void {
    this.introDelayTimer = Timer.Dispose(this.introDelayTimer);
  }

  // ----------------------------------------------------------------------------------------------
  private onNextButtonPressed(): void {
    this.setNextPage();
  }

  // ----------------------------------------------------------------------------------------------
  private setNextPage(): void {
    this.disposeIntroDelayTimer();

    if (this.scene.currentFrame + 1 < this.scene.totalFrames) {
      this.scene.nextFrame();
    } else {
      this.emit(SbTutorialManagerEvents.COMPLETE, this);
    }
  }
}
