function SearchForMapKeyByValue<Key, Value>(
  map: ReadonlyMap<Key, Value>,
  valueToSearchFor: Value,
): Key | undefined {
  const mapIterator = map.entries();
  let current = mapIterator.next();

  while (!current.done) {
    const [key, value] = current.value;
    if (value === valueToSearchFor) {
      return (key);
    }

    current = mapIterator.next();
  }

  return (undefined);
}

export default SearchForMapKeyByValue;
