import IStorybookEngine from '../../IStorybookEngine';
import SbScene from '../../storybookScene/SbScene';
import SbVideoMathProps from './SbVideoMathProps';

export default class SbVideoMath {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    this.engine = engine;
  }

  // ----------------------------------------------------------------------------------------------
  convertTimelineFrameToVideoTime(timelineFrame: number | string): number {
    const videoFrame = Math.max(
      0,
      this.resolveTimelineFrame(timelineFrame) - this.startTimelineFrame,
    );

    const videoTimeInSecs = videoFrame / this.engine.config.videoFps;
    return (videoTimeInSecs);
  }

  // ----------------------------------------------------------------------------------------------
  resolveTimelineFrame(timelineFrame: number | string): number {
    return (typeof (timelineFrame) === 'number'
      ? timelineFrame
      : this.exportRoot.timeline.resolve(timelineFrame) ?? 0);
  }

  // ----------------------------------------------------------------------------------------------
  resolveVideoTime(out?: SbVideoMathProps): SbVideoMathProps {
    if (!out) {
      out = {
        timelineFrame: 0,
        videoFrame: 0,
        videoTimeSecs: 0,
      };
    }

    const { contentVideoElement } = this;
    if (contentVideoElement) {
      out.timelineFrame = this.exportRoot.currentFrame - this.startTimelineFrame;
      out.videoTimeSecs = contentVideoElement.currentTime;
      out.videoFrame = Math.ceil(out.videoTimeSecs * this.engine.config.videoFps);
    }

    return (out);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private get contentVideoElement(): HTMLVideoElement | undefined {
    return (this.engine.contentVideoElement);
  }

  // ----------------------------------------------------------------------------------------------
  private get exportRoot(): SbScene {
    return (this.engine.exportRoot);
  }

  // ----------------------------------------------------------------------------------------------
  private get startTimelineFrame(): number {
    return (this.engine.startTimelineFrame);
  }
}
