import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McGirlHelpHerWearClothesQuiz from './McGirlHelpHerWearClothesQuiz';
import McGirlThisHowWhereClothes from './McGirlThisHowWhereClothes';

function howYouWearClothesAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_how_you_where_clothes_aac_how',
        soundId: 'SBL-0027_How',
      },
      {
        frameOrLabel: 'girl_how_you_where_clothes_aac_you',
        soundId: 'SBL-0032_you_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'girl_how_you_where_clothes_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'girl_how_you_where_clothes_aac_clothes',
        soundId: 'SBL-0030_clothes',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McGirlHelpHerWearClothesQuiz(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlThisHowWhereClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlThisHowWhereClothes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
export default function McGirlHowYouWhereClothes(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_How-do-you-wear-your-clothes_48.0k_stereo.mp3',
    'girl_how_you_where_clothes_start',
    'girl_how_you_where_clothes_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          howYouWearClothesAac(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlThisHowWhereClothes(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlThisHowWhereClothes(scene);
        },
      },
    });
  });
}
