import SbScene from '../../../engine/storybookScene/SbScene';
import { SbButtonsMenuConfig } from '../../../engine/ui/buttonsMenu/config/SbButtonsMenuConfig';
import McBoyMyClothesAac from './McBoyMyClothesAac';

const FrameLabelSequencePrefixes = [
  'boy_present_clothes_shirt',
  'boy_present_clothes_pants',
  'boy_present_clothes_socks',
  'boy_present_clothes_hat',
  'boy_present_clothes_shoes',
  'boy_present_clothes_eyewear',
] as const;

type FrameLabel = typeof FrameLabelSequencePrefixes[number];

let ProgressIndex: number;

class McBoyPresentClothesHelper {
  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor() {
    throw (new Error('Instances of this class are not allowed.'));
  }

  // ----------------------------------------------------------------------------------------------
  static OpenWardrobe(scene: SbScene): void {
    McBoyPresentClothesHelper.PresentMenu(false, scene);
    scene.playSound({
      audioId: 'my_clothes_wardrobe_open.mp3',
      completeCallback: async () => {
        await scene.playSyncVoice(
          'SBL-0001_These-are-my-clothes',
          'boy_these_are_my_clothes_voc_start',
          'boy_these_are_my_clothes_voc_end',
        );

        McBoyPresentClothesHelper.PresentMenu(true, scene);
      },
    });
  }

  // ----------------------------------------------------------------------------------------------
  static Start(scene: SbScene): void {
    ProgressIndex = -1;

    scene.playTo(
      'boy_present_clothes_end',
      'boy_present_clothes_start',
      () => {
        McBoyPresentClothesHelper.OpenWardrobe(scene);
      },
    );
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private static BuildMenuConfig(areButtonsEnabled: boolean, scene: SbScene): SbButtonsMenuConfig {
    const isDisabled = !areButtonsEnabled;
    return ({
      buttons: {
        wardrobeButtonDisplay: {
          isDisabled,
          callback: (): void => {
            McBoyPresentClothesHelper.PresentNextItem(scene);
          },
        },
      },
    });
  }

  // ----------------------------------------------------------------------------------------------
  private static PresentItemAt(index: number, scene: SbScene): void {
    ProgressIndex = index;
    const label = FrameLabelSequencePrefixes[ProgressIndex];
    if (label) {
      McBoyPresentClothesHelper.PresentMenu(false, scene);
      const endLabel = `${label}_end`;
      const startLabel = `${label}_start`;
      scene.playTo(endLabel, startLabel, McBoyPresentClothesHelper.OnPresentItemComplete);
      scene.playSound('my_clothes_present_item.mp3');
    } else {
      McBoyPresentClothesHelper.Complete(scene);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private static PresentItemOf(label: FrameLabel, scene: SbScene): void {
    const index = FrameLabelSequencePrefixes.indexOf(label);
    McBoyPresentClothesHelper.PresentItemAt(index, scene);
  }

  // ----------------------------------------------------------------------------------------------
  private static Complete(scene: SbScene): void {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McBoyMyClothesAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyPresentClothesHelper.PresentItemOf('boy_present_clothes_shoes', scene);
        },
      },

      restart: {
        callback: () => {
          McBoyPresentClothesHelper.Start(scene);
        },
      },
    });
  }

  // ----------------------------------------------------------------------------------------------
  private static OnPresentItemComplete(scene: SbScene): void {
    const label = FrameLabelSequencePrefixes[ProgressIndex + 1];
    if (label) {
      McBoyPresentClothesHelper.PresentMenu(true, scene);
    } else {
      McBoyPresentClothesHelper.Complete(scene);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private static PresentMenu(areButtonsEnabled: boolean, scene: SbScene): void {
    scene.engine.presentMenu(McBoyPresentClothesHelper.BuildMenuConfig(areButtonsEnabled, scene));
    if (areButtonsEnabled) {
      scene.engine.startPressIndicator();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private static PresentNextItem(scene: SbScene): void {
    McBoyPresentClothesHelper.PresentItemAt(ProgressIndex + 1, scene);
  }
}

// ================================================================================================
export default function McBoyPresentClothes(scene: SbScene): void {
  McBoyPresentClothesHelper.Start(scene);
}
