import CjsRadioGroup from '../../../../../utils/createjs/objects/buttons/CjsRadioGroup';
import ICjsCheckbox from '../../../../../utils/createjs/objects/buttons/ICjsCheckbox';
import { SbAssetLib } from '../../../../data/SbAssetLib';
import { SbInputDevices, SbInputDevice } from '../../../../data/SbInputDevices';
import SbUserDeviceSettingsData from '../../../../data/settings/device/SbUserDeviceSettingsData';
import SbUiComponent from '../../../SbUiComponent';
import SbRadioGroupEvents from '../../../events/SbRadioGroupEvents';
import SbSettingsSubPanel from '../SbSettingsSubPanel';

const DisplayNamesMap = [
  [SbInputDevices.DualSwitch, 'dualSwitchCheckboxDisplay'],
  [SbInputDevices.EyeTracker, 'eyeTrackerCheckboxDisplay'],
  [SbInputDevices.SingleSwitch, 'singleSwitchCheckboxDisplay'],
  [SbInputDevices.TapMouse, 'tapMouseCheckboxDisplay'],
] as const;

export default class SbSettingsDevicesSubPanel
  extends SbSettingsSubPanel<SbUserDeviceSettingsData> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly devicesRadioGroup: CjsRadioGroup;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(mainPanel: SbUiComponent, lib: Readonly<SbAssetLib>) {
    super(mainPanel, lib);

    this.devicesRadioGroup = this.initDevicesRadioGroup();
  }

  // ----------------------------------------------------------------------------------------------
  updateFromSettings(): this {
    if (this.settings) {
      this.devicesRadioGroup.setButtonOn(this.settings.inputDevice);
    }

    return (this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected updateSettingsUiFromData(): void {
    this.updateFromSettings();
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private initDevicesRadioGroup(): CjsRadioGroup {
    const groupData: [SbInputDevice, ICjsCheckbox][] = [];
    DisplayNamesMap.forEach(([inputDevice, displayName]) => {
      const deviceCheckBox = this.mainPanel.initCheckbox(displayName);
      if (deviceCheckBox) {
        groupData.push([inputDevice, deviceCheckBox]);
      }
    });

    const group = new CjsRadioGroup(groupData);
    group.addListener(SbRadioGroupEvents.SELECTED, this.onDevicesRadioGroupSelected, this);
    return (group);
  }

  // ----------------------------------------------------------------------------------------------
  private onDevicesRadioGroupSelected(id: SbInputDevice): void {
    this.setSettingProp(this.settings, 'inputDevice', id);
  }
}
