import { IsFiniteNumber } from '../Utils';
import Clamp from '../math/Clamp';

function ParseNumber(source: unknown, defaultValue?: number, min?: number,
  max?: number): number {
  if (IsFiniteNumber(source)) {
    return (Clamp(source, min, max));
  }

  defaultValue = IsFiniteNumber(defaultValue) ? defaultValue : 0;
  if (source === undefined || source === null || typeof (source) !== 'string') {
    return (defaultValue);
  }

  const value = Number.parseFloat(source);
  return (Number.isNaN(value) ? defaultValue : Clamp(value, min, max));
}

export default ParseNumber;
