/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoySelectGlassesQuiz from './McBoySelectGlassesQuiz';

function closeHow(scene: SbScene): void {
  scene.playTo(
    'boy_wear_glasses_close_how_end',
    'boy_wear_glasses_close_how_start',
    () => {
      McBoySelectGlassesQuiz(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_wear_glasses_how_aac_i',
        soundId: 'SBL-0019_I',
      },
      {
        frameOrLabel: 'boy_wear_glasses_how_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'boy_wear_glasses_how_aac_glasses',
        soundId: 'SBL-0025_glasses_48.0k_stereo.mp3',
      },
      {
        frameOrLabel: 'boy_wear_glasses_how_aac_eyes',
        soundId: 'SBL-0026_eyes_48.0k_stereo.mp3',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          closeHow(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearGlasses(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-glasses-over-my-eyes',
    'boy_wear_glasses_how_start',
    'boy_wear_glasses_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyWearGlasses(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearGlasses(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'boy_wear_glasses_animation_end',
    'boy_wear_glasses_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      boyWearGlassesButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'boyWearGlassesButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McBoyWearGlasses(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-glasses_48.0k_stereo.mp3',
    'boy_wear_glasses_start',
    'boy_wear_glasses_end',
  ).then(() => {
    wearHow(scene);
  });
}
