import IMovieClipDisplayCtor from '../../../adobeAnimate/IMovieClipDisplayCtor';
import CjsGetMovieClip from '../CjsGetMovieClip';
import CjsButtonEvents from './CjsButtonEvents';
import CjsCheckbox from './CjsCheckbox';
import CjsCheckboxCallback from './CjsCheckboxCallback';

function CjsCreateCheckbox(container: createjs.Container, displayName: string, isOn?: boolean,
  callback?: CjsCheckboxCallback | undefined, context?: unknown,
  hitAreaOrAnLibClipCtor?: createjs.DisplayObject
  | IMovieClipDisplayCtor): CjsCheckbox | undefined {
  const checkboxDisplay = CjsGetMovieClip(container, displayName);
  if (!checkboxDisplay) {
    return (undefined);
  }

  const checkbox = new CjsCheckbox(checkboxDisplay, isOn, hitAreaOrAnLibClipCtor);
  if (callback) {
    checkbox.addListener(CjsButtonEvents.PRESSED, callback, context);
  }

  return (checkbox);
}

export default CjsCreateCheckbox;
