import EventEmitter from 'eventemitter3';
import ICjsCheckbox from './ICjsCheckbox';
import SbRadioGroupEvents from '../../../../engine/ui/events/SbRadioGroupEvents';
import CjsCheckbox from './CjsCheckbox';
import CjsButtonEvents from './CjsButtonEvents';

export default class CjsRadioGroup<
  ButtonIdType extends string | number = string
> extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly radioButtonIdsByButton: Map<ICjsCheckbox, ButtonIdType>;

  private _isEnabled: boolean;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(radioButtons?: [ButtonIdType, ICjsCheckbox][]) {
    super();

    this.radioButtonIdsByButton = new Map();

    this._isEnabled = true;

    radioButtons?.forEach(([id, checkbox]) => {
      this.add(id, checkbox);
    });
  }

  // ----------------------------------------------------------------------------------------------
  add(id: ButtonIdType, button: ICjsCheckbox): this {
    if (!this.radioButtonIdsByButton.has(button)) {
      button.addListener(CjsButtonEvents.PRESSED, this.onButtonPressed, this);
    }

    this.radioButtonIdsByButton.set(button, id);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.removeAllButtons();
    this.removeAllListeners();
  }

  // ----------------------------------------------------------------------------------------------
  get enabled(): boolean {
    return (this._isEnabled);
  }

  // ----------------------------------------------------------------------------------------------
  set enabled(value: boolean) {
    this._isEnabled = value;
    this.radioButtonIdsByButton.forEach((_id, checkbox) => {
      checkbox.enabled = value;
    });
  }

  // ----------------------------------------------------------------------------------------------
  removeAllButtons(): this {
    this.radioButtonIdsByButton.forEach((id) => {
      this.removeButton(id);
    });

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  removeButton(idOrButton: ButtonIdType | ICjsCheckbox): this {
    const button = this.resolveButton(idOrButton);
    if (button) {
      button.removeListener(CjsButtonEvents.PRESSED, this.onButtonPressed, this);
      this.radioButtonIdsByButton.delete(button);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setButtonOn(id: ButtonIdType): this {
    return (this.setButtonState(id, true));
  }

  // ----------------------------------------------------------------------------------------------
  setButtonState(id: ButtonIdType, isOn: boolean): this {
    const button = this.resolveButton(id);
    if (button) {
      button.setOn(isOn);
      this.update(button);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setEnabled(isEnabled: boolean): this {
    this.enabled = isEnabled;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  shouldUseDisabledStates(should: boolean): this {
    this.radioButtonIdsByButton.forEach((_id, checkbox) => {
      (checkbox as CjsCheckbox).shouldUseDisabledState = should;
    });

    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onButtonPressed(checkbox: ICjsCheckbox): void {
    const id = this.radioButtonIdsByButton.get(checkbox);
    if (id === undefined) {
      return;
    }

    this.update(checkbox);
    this.emit(SbRadioGroupEvents.SELECTED, id, checkbox, this);
  }

  // ----------------------------------------------------------------------------------------------
  private resolveButton(idOrButton: ButtonIdType | ICjsCheckbox): ICjsCheckbox | undefined {
    if (typeof (idOrButton) === 'string' || typeof (idOrButton) === 'number') {
      const entriesIterator = this.radioButtonIdsByButton.entries();

      let currentEntry = entriesIterator.next();
      while (!currentEntry.done) {
        const [button, id] = currentEntry.value;

        if (id === idOrButton) {
          return (button);
        }

        currentEntry = entriesIterator.next();
      }

      return (undefined);
    }

    return (this.radioButtonIdsByButton.has(idOrButton) ? idOrButton : undefined);
  }

  // ----------------------------------------------------------------------------------------------
  private setOnAndDisableOtherButtons(radioButtonToExclude: ICjsCheckbox): void {
    this.radioButtonIdsByButton.forEach((_id, button) => {
      if (button !== radioButtonToExclude) {
        button.setOn(false).setEnabled(this.enabled);
      }
    });
  }

  // ----------------------------------------------------------------------------------------------
  private update(radioButtonJustSetOn: ICjsCheckbox): void {
    if (radioButtonJustSetOn.isOn) {
      radioButtonJustSetOn.enabled = false;
      this.setOnAndDisableOtherButtons(radioButtonJustSetOn);
    }
  }
}
