const CjsSliderEvents = {
  /**
   * Dispatched when the value of a CjsSlider has changed.
   *
   * ```
   * function onSliderChanged(slider: ICjsSlider): void;
   * ```
   */
  CHANGED: 'changed',

  /**
   * Dispatched when the user has begun a dragging session by pressing the slider's handle
   * or body.
   *
   * ```
   * function onSliderDragStarted(slider: ICjsSlider): void;
   * ```
   */
  DRAG_STARTED: 'dragStarted',

  /**
   * Dispatched when the user has ended a dragging session by releasing the slider's handle
   * or body.
   *
   * ```
   * function onSliderDragStopped(slider: ICjsSlider): void;
   * ```
   */
  DRAG_STOPPED: 'dragStopped',
} as const;

export default CjsSliderEvents;
