import EventEmitter from 'eventemitter3';
import CjsGetButton from '../../utils/createjs/objects/CjsGetButton';
import CjsButton from '../../utils/createjs/objects/buttons/CjsButton';
import CjsButtonCallback from '../../utils/createjs/objects/buttons/CjsButtonCallback';
import SbUiComponentEvents from './events/SbUiComponentEvents';
import CjsCompoundButton from '../../utils/createjs/objects/buttons/CjsCompoundButton';
import CjsGetContainer from '../../utils/createjs/objects/CjsGetContainer';
import { CjsGetTextObject, CjsTextObject } from '../../utils/createjs/objects/CjsGetTextObject';
import IMovieClipDisplayCtor from '../../utils/adobeAnimate/IMovieClipDisplayCtor';
import CjsCheckbox from '../../utils/createjs/objects/buttons/CjsCheckbox';
import CjsCreateCheckbox from '../../utils/createjs/objects/buttons/CjsCreateCheckbox';
import CjsButtonEvents from '../../utils/createjs/objects/buttons/CjsButtonEvents';
import CjsCompoundButtonCallback from '../../utils/createjs/objects/buttons/CjsCompoundButtonCallback';
import IStorybookEngine from '../IStorybookEngine';
import CjsGetDisplayObject from '../../utils/createjs/objects/CjsGetDisplayObject';

const DisplayObjectNames = {
  CloseButton: 'closeButtonDisplay',
} as const;

export default abstract class SbUiComponent extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  readonly container: createjs.MovieClip;

  protected closeButton: CjsButton | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: createjs.MovieClip, engine: IStorybookEngine) {
    super();

    this.container = container;
    this.container.stop();
    this.engine = engine;
    this.onInit();
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.removeAllListeners();
    this.closeButton = CjsButton.Dispose(this.closeButton);
  }

  // ----------------------------------------------------------------------------------------------
  initButton(displayName: string, callback: CjsButtonCallback,
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
    container?: createjs.Container, context?: unknown): CjsButton | undefined {
    container = container ?? this.container;
    context = context === undefined ? this : context;
    const button = CjsGetButton(container, displayName, callback, hitAreaOrAnLibClipCtor, context);
    button?.on(CjsButtonEvents.PRESSED, this.onButtonPressed, this);
    return (button);
  }

  // ----------------------------------------------------------------------------------------------
  initCheckbox(displayName: string, callback?: CjsButtonCallback, isOn?: boolean,
    container?: createjs.Container, context?: unknown,
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject
    | IMovieClipDisplayCtor): CjsCheckbox | undefined {
    container = container ?? this.container;
    context = context === undefined ? this : context;
    const checkbbox = CjsCreateCheckbox(container, displayName, isOn, callback, context,
      hitAreaOrAnLibClipCtor);

    checkbbox?.on(CjsButtonEvents.PRESSED, this.onCheckboxPressed, this);
    return (checkbbox);
  }

  // ----------------------------------------------------------------------------------------------
  initCompoundButton(displayName: string,
    callback: CjsCompoundButtonCallback, container?: createjs.Container,
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
    context?: unknown): CjsCompoundButton | undefined {
    container = container ?? this.container;
    context = context === undefined ? this : context;
    const cpButtonContainer = CjsGetContainer(container, displayName);
    if (!cpButtonContainer) {
      return (undefined);
    }

    const cpButton = new CjsCompoundButton(cpButtonContainer, hitAreaOrAnLibClipCtor);
    cpButton.on(CjsButtonEvents.PRESSED, this.onButtonPressed, this);
    cpButton.on(CjsButtonEvents.PRESSED, callback, context);
    return (cpButton);
  }

  // ----------------------------------------------------------------------------------------------
  setVisible(isVisible: boolean): this {
    this.visible = isVisible;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get visible(): boolean {
    return (this.container.visible);
  }

  // ----------------------------------------------------------------------------------------------
  set visible(value: boolean) {
    this.container.visible = value;
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected clearText(textObject: CjsTextObject | undefined): this {
    return (this.setText(textObject, ''));
  }

  // ----------------------------------------------------------------------------------------------
  protected initCloseButton(
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  ): CjsButton | undefined {
    const closeButton = this.initButton(DisplayObjectNames.CloseButton, this.onClosePressed,
      hitAreaOrAnLibClipCtor);
    return (closeButton);
  }

  // ----------------------------------------------------------------------------------------------
  protected initDisplayObject<T extends createjs.DisplayObject = createjs.DisplayObject>(
    displayName: string,
    container?: createjs.Container,
  ): T | undefined {
    container = container ?? this.container;
    return (CjsGetDisplayObject(container, displayName));
  }

  // ----------------------------------------------------------------------------------------------
  protected initMovieClip<T extends createjs.MovieClip = createjs.MovieClip>(
    displayName: string,
    container?: createjs.Container,
  ): T | undefined {
    return (this.initDisplayObject<T>(displayName, container));
  }

  // ----------------------------------------------------------------------------------------------
  protected initText<T extends CjsTextObject = CjsTextObject>(displayName: string,
    container?: createjs.Container): T | undefined {
    container = container ?? this.container;
    return (CjsGetTextObject(container, displayName) as T);
  }

  // ----------------------------------------------------------------------------------------------
  protected onClosePressed(): void {
    this.emit(SbUiComponentEvents.CLOSE, this);
  }

  // ----------------------------------------------------------------------------------------------
  protected onInit(
    closeButtonHitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  ): void {
    this.closeButton = this.initCloseButton(closeButtonHitAreaOrAnLibClipCtor);
  }

  // ----------------------------------------------------------------------------------------------
  protected setText(textObject: CjsTextObject | undefined, text: number | string): this {
    if (textObject) {
      textObject.text = typeof (text) === 'number' ? text.toString(10) : text;
    }

    return (this);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private onButtonPressed(): void {
    this.engine.playSound(this.engine.lib.audioIds.RadioHeavyButtonPress);
  }

  // ----------------------------------------------------------------------------------------------
  private onCheckboxPressed(): void {
    this.engine.playSound(this.engine.lib.audioIds.RadioLightButtonPress);
  }
}
