import SbScene from '../../engine/storybookScene/SbScene';
import McPrep from './McPrep';

// ------------------------------------------------------------------------------------------------
function closeIntro(scene: SbScene): void {
  scene.playTo(
    'intro_closing_end',
    'intro_closing_begin',
    () => {
      McPrep(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function presentIntroMenu(scene: SbScene): void {
  scene.engine.presentNav({
    navButtonsToHide: ['prev', 'restart'],
    params: {
      next: {
        callback: () => {
          closeIntro(scene);
        },
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
export default function McIntro(scene: SbScene): void {
  scene.playTo(
    'start_end',
    'start',
    () => {
      presentIntroMenu(scene);
    },
  );
}
