/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McGirlSelectShirtQuiz from './McGirlSelectShirtQuiz';

function shirtTips(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_It-is-good-to-start-by-teaching-shirts',
    'girl_wear_shirt_tips_start',
    'girl_wear_shirt_tips_end',
  ).then(() => {
    scene.stop('girl_wear_shirt_tips_end');
    scene.engine.presentNav({
      next: {
        callback: () => {
          scene.playToEnd(
            'girl_wear_shirt_tips_menu_closed',
            () => {
              McGirlSelectShirtQuiz(scene);
            },
          );
        },
      },

      prev: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearShirt(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_wear_shirt_how_aac_i',
        soundId: 'SBL-0007_I',
      },
      {
        frameOrLabel: 'girl_wear_shirt_how_aac_wear',
        soundId: 'SBL-0008_Wear',
      },
      {
        frameOrLabel: 'girl_wear_shirt_how_aac_shirt',
        soundId: 'SBL-0005_tee-shirt',
      },
      {
        frameOrLabel: 'girl_wear_shirt_how_aac_body',
        soundId: 'SBL-0006_body',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          shirtTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearShirt(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0004_I-wear-my-tee-shirt-over-my-body',
    'girl_wear_shirt_how_start',
    'girl_wear_shirt_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlWearShirt(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearShirt(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearShirtAnimation(scene: SbScene): void {
  scene.playTo(
    'girl_wear_shirt_animation_end',
    'girl_wear_shirt_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearShirtHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      girlWearShirtMenuButtonDisplay: {
        callback: () => {
          playWearShirtAnimation(scene);
        },
      },
    },

    defaultButtonName: 'girlWearShirtMenuButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default async function McGirlWearShirt(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL0003_Iwearmyteeshirt_480k_stereo',
    'girl_wear_shirt_start',
    'girl_wear_shirt_end',
  );

  wearShirtHow(scene);
}
