import IStorybookEngine from '../../IStorybookEngine';
import SbScene from '../SbScene';

export default abstract class SbBaseSceneHandler {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    this.engine = engine;
  }

  // ----------------------------------------------------------------------------------------------
  abstract run(...params: unknown[]): void;

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected get exportRoot(): SbScene {
    return (this.engine.exportRoot);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================
}
