import IsPlainObject from '../../../../utils/object/IsPlainObject';
import { SbPresentNavConfig } from './SbPresentNavConfig';

function SbIsPresentNavConfig(data: unknown): data is SbPresentNavConfig {
  return (IsPlainObject(data)
    && ('defaultSelectedNavButtonId' in data
      || 'navButtonsToHide' in data
      || 'params' in data));
}

export default SbIsPresentNavConfig;
