import IncludesIs from '../../../../../utils/collections/arrays/IncludesIs';
import CloneObject from '../../../../../utils/object/CloneObject';
import IsPlainObject from '../../../../../utils/object/IsPlainObject';
import SbDefaults from '../../../defaults/SbDefaults';
import { SbInputDevice, SbInputDevicesArray } from '../../../SbInputDevices';
import SbUserDeviceSettingsData from '../../device/SbUserDeviceSettingsData';

function parseInputDevice(source: unknown, defaultData: SbInputDevice): SbInputDevice {
  return (IncludesIs(source, SbInputDevicesArray) ? source : defaultData);
}

// ------------------------------------------------------------------------------------------------
function SbParseDeviceSettings(source: unknown): SbUserDeviceSettingsData {
  const target = CloneObject(SbDefaults.Settings.device);
  if (!IsPlainObject(source)) {
    return (target);
  }

  target.inputDevice = parseInputDevice(source.inputDevice, target.inputDevice);
  return (target);
}

export default SbParseDeviceSettings;
