import IMovieClipDisplayCtor from '../../../utils/adobeAnimate/IMovieClipDisplayCtor';
import SearchForMapKeyByValue from '../../../utils/collections/maps/SearchForMapKeyByValue';
import CjsSetVisible from '../../../utils/createjs/actions/CjsSetVisible';
import CjsGetMovieClip from '../../../utils/createjs/objects/CjsGetMovieClip';
import CjsButton from '../../../utils/createjs/objects/buttons/CjsButton';
import CjsButtonCallback from '../../../utils/createjs/objects/buttons/CjsButtonCallback';
import IStorybookEngine from '../../IStorybookEngine';
import { ISbAnCompLibClasses } from '../../data/SbAssetLib';
import { SbNavToDirection, SbNavToDirections } from './nav/SbNavToDirections';
import SbUiComponent from '../SbUiComponent';
import SbUiComponentEvents from '../events/SbUiComponentEvents';
import SbInputDeviceHandler from '../inputDeviceHandler/SbInputDeviceHandler';
import SbInputDeviceHandlerEvents from '../inputDeviceHandler/SbInputDeviceHandlerEvents';
import SbProgressBar from '../progressBar/SbProgressBar';
import SbTutorialManager from '../tutorial/SbTutorialManager';
import SbTutorialManagerEvents from '../tutorial/SbTutorialManagerEvents';
import SbMainPanelDisplayObjectNames from './SbMainPanelDisplayObjectNames';
import SbMainPanelEvents from './SbMainPanelEvents';
import SbHelpfulTipsPanel from './helpfulTipsPanel/SbHelpfulTipsPanel';
import SbHelpfulTipsPanelEvents from './helpfulTipsPanel/SbHelpfulTipsPanelEvents';
import { SbPresentNavConfig } from './nav/SbPresentNavConfig';
import { CjsEvents } from '../../../utils/createjs/events/CjsEvents';

const NavButtonDirections: readonly SbNavToDirection[] = [
  SbNavToDirections.Next,
  SbNavToDirections.Prev,
  SbNavToDirections.Restart,
];

type NavButtonsByDirection = Map<SbNavToDirection, CjsButton>;

export default class SbMainPanel extends SbUiComponent {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly progressBar: SbProgressBar | undefined;

  private readonly navButtonsByDirection: NavButtonsByDirection;

  private readonly helpfulTipsPanel: SbHelpfulTipsPanel | undefined;

  private readonly navButtonsInOrder: CjsButton[];

  private readonly inputDeviceHandler: SbInputDeviceHandler;

  private readonly settingsButton: CjsButton | undefined;

  private selectedButtonIndex: number;

  private isNavUiActive: boolean;

  private navStartFrame: number;

  private ticks: number;

  private tutorialManager: SbTutorialManager | undefined;

  private navConfig: SbPresentNavConfig | undefined;

  private buttonEvent: string | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: createjs.MovieClip, engine: IStorybookEngine) {
    super(container, engine);

    this.progressBar = this.initProgressBar();

    this.navButtonsByDirection = this.initNavButtons(engine.lib.animateClasses);

    this.navButtonsInOrder = NavButtonDirections
      .map((direction) => this.navButtonsByDirection.get(direction))
      .filter((button): button is CjsButton => !!button);

    this.inputDeviceHandler = this.initInputDeviceHandler();

    this.selectedButtonIndex = -1;

    this.settingsButton = this.initButton(SbMainPanelDisplayObjectNames.SettingsButton,
      this.onSettingsPressed, engine.lib.animateClasses.SettingsButtonDisplay);

    this.helpfulTipsPanel = this.initHelpfulTipsPanel();

    this.isNavUiActive = false;
    this.navStartFrame = -1;
    this.ticks = -1;

    this.onTickPessKludge = this.onTickPessKludge.bind(this);
    this.onExportRootTimelineChangeNav = this.onExportRootTimelineChangeNav.bind(this);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();

    this.uninitExportRootTimeline();
    this.inputDeviceHandler.dispose();
    this.progressBar?.dispose();
    this.helpfulTipsPanel?.dispose();
    this.settingsButton?.dispose();

    this.navButtonsByDirection.forEach((navButton) => {
      navButton?.dispose();
    });
  }

  // ----------------------------------------------------------------------------------------------
  presentNavUi(config?: SbPresentNavConfig): this {
    if (this.isNavUiActive) {
      return (this);
    }

    this.navStartFrame = this.engine.exportRoot.currentFrame;

    this.isNavUiActive = true;

    CjsSetVisible(this.navButtonsInOrder, true);

    this.navConfig = config;
    config?.navButtonsToHide?.forEach((directionToHide) => {
      this.navButtonsByDirection.get(directionToHide)?.setVisible(false);
    });

    this.unselectCurrentNavButton();
    this.setDefaultSelectedButton(config?.defaultSelectedNavButtonId ?? null);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  presentTutorial(): boolean {
    if (!this.tutorialManager) {
      const display = CjsGetMovieClip(this.container,
        SbMainPanelDisplayObjectNames.TutorialPanels);

      if (!display) {
        console.warn('No tutorial panels display object on main panel was found with name:',
          SbMainPanelDisplayObjectNames.TutorialPanels);
        return (false);
      }

      this.tutorialManager = new SbTutorialManager(this, display)
        .once(SbUiComponentEvents.CLOSE, this.onTutorialManagerClose, this)
        .once(SbTutorialManagerEvents.COMPLETE, this.onTutorialManagerComplete, this);
    }

    this.tutorialManager.present();
    this.unpresentNavUi();
    this.visible = true;
    this.helpfulTipsPanel?.setVisible(false);
    this.settingsButton?.setVisible(false);
    this.progressBar?.setVisible(false);
    return (true);
  }

  // ----------------------------------------------------------------------------------------------
  set settingsButtonVisible(value: boolean) {
    this.settingsButton?.setVisible(value);
  }

  // ----------------------------------------------------------------------------------------------
  override setVisible(isVisible: boolean): this {
    const didVisibilityChange = (isVisible !== this.visible);
    const out = super.setVisible(isVisible);
    if (didVisibilityChange) {
      this.update();
    }

    return (out);
  }

  // ----------------------------------------------------------------------------------------------
  unpresentNavUi(): this {
    this.inputDeviceHandler.isEnabled = false;

    if (this.isNavUiActive) {
      this.isNavUiActive = false;
      CjsSetVisible(this.navButtonsInOrder, false);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  unpresentTutorial(): this {
    this.tutorialManager?.unpresent();
    this.settingsButton?.setVisible(true);

    // TODO
    // this.progressBar?.setVisible(true);

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  updateFromSettings(): void {
    this.visible = true;
    this.inputDeviceHandler.updateFromSettings();
    this.setDefaultSelectedButton(this.navConfig?.defaultSelectedNavButtonId ?? null);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private confirmSelectedNavButton(): void {
    const id = this.selectedNavButtonEvent;
    if (id) {
      this.emit(id, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initHelpfulTipsPanel(): SbHelpfulTipsPanel | undefined {
    const display = CjsGetMovieClip(this.container, SbMainPanelDisplayObjectNames.HelpfulTipsPanel);
    if (!display) {
      console.warn('No helpful tips display object on main panel was found with name:',
        SbMainPanelDisplayObjectNames.HelpfulTipsPanel);

      return (undefined);
    }

    return (new SbHelpfulTipsPanel(display)
      .setVisible(false)
      .on(SbHelpfulTipsPanelEvents.OPENED, this.onHelpfulTipsOpened, this));
  }

  // ----------------------------------------------------------------------------------------------
  private initInputDeviceHandler(): SbInputDeviceHandler {
    return (new SbInputDeviceHandler(this.engine)
      .on(SbInputDeviceHandlerEvents.NEXT, this.onInputDeviceNext, this)
      .on(SbInputDeviceHandlerEvents.SELECT, this.onInputDeviceSelect, this));
  }

  // ----------------------------------------------------------------------------------------------
  private initNavButton(
    displayName: string,
    callback: CjsButtonCallback,
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  ): CjsButton | undefined {
    const button = this.initButton(displayName, callback,
      hitAreaOrAnLibClipCtor)?.setVisible(false);

    if (!button) {
      console.warn('No nav button on main panel was found with name:', displayName);
    }

    return (button);
  }

  // ----------------------------------------------------------------------------------------------
  private initNavButtons(lib: Readonly<ISbAnCompLibClasses>): NavButtonsByDirection {
    const configs = [
      [
        SbNavToDirections.Next,
        this.initNavButton(SbMainPanelDisplayObjectNames.NextButton, this.onNextPressed,
          lib.NextButtonDisplay),
      ],
      [
        SbNavToDirections.Prev,
        this.initNavButton(SbMainPanelDisplayObjectNames.PrevButton, this.onPrevPressed,
          lib.PrevtButtonDisplay),
      ],
      [
        SbNavToDirections.Restart,
        this.initNavButton(SbMainPanelDisplayObjectNames.RestartButton, this.onRestartPressed,
          lib.RestartButtonDisplay),
      ],
    ] as const;

    const navButtons = new Map<SbNavToDirection, CjsButton>();
    configs.forEach((config) => {
      const [key, button] = config;
      if (button) {
        navButtons.set(key, button);
      }
    });

    return (navButtons);
  }

  // ----------------------------------------------------------------------------------------------
  private initProgressBar(): SbProgressBar | undefined {
    const display = CjsGetMovieClip(this.container, SbMainPanelDisplayObjectNames.ProgressBar);
    return (display ? new SbProgressBar(display) : undefined);
  }

  // ----------------------------------------------------------------------------------------------
  private onExportRootTimelineChangeNav(): void {
    const { currentFrame } = this.engine.exportRoot;
    if (this.navStartFrame > -1 && currentFrame !== this.navStartFrame) {
      console.log(`nav adjusted timeline from ${currentFrame} to ${this.navStartFrame}`);
      const frame = this.navStartFrame;
      this.navStartFrame = -1;
      this.engine.exportRoot.stop(frame);
    }
  }

  // ----------------------------------------------------------------------------------------------
  // eslint-disable-next-line class-methods-use-this
  private onHelpfulTipsOpened(): void {
    // TODO
  }

  // ----------------------------------------------------------------------------------------------
  private onInputDeviceNext(): void {
    this.selectNextNavButton();
  }

  // ----------------------------------------------------------------------------------------------
  private onInputDeviceSelect(): void {
    this.confirmSelectedNavButton();
  }

  // ----------------------------------------------------------------------------------------------
  private onNextPressed(): void {
    this.emit(SbMainPanelEvents.NEXT, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onPrevPressed(): void {
    this.emit(SbMainPanelEvents.PREV, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onRestartPressed(): void {
    this.emit(SbMainPanelEvents.RESTART, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onSettingsPressed(): void {
    this.emit(SbMainPanelEvents.SETTINGS, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onTickPessKludge(): void {
    this.ticks += 1;
    if (this.ticks < 4) {
      return;
    }

    createjs.Ticker.removeEventListener(CjsEvents.TICK, this.onTickPessKludge);
    if (this.buttonEvent) {
      this.emit(this.buttonEvent, this);
      this.buttonEvent = undefined;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onTutorialManagerClose(): void {
    this.emit(SbMainPanelEvents.TUTORIAL_COMPLETE, false, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onTutorialManagerComplete(): void {
    this.emit(SbMainPanelEvents.TUTORIAL_COMPLETE, true, this);
  }

  // ----------------------------------------------------------------------------------------------
  private get selectedNavButton(): CjsButton | undefined {
    return (this.navButtonsInOrder[this.selectedButtonIndex]);
  }

  // ----------------------------------------------------------------------------------------------
  private get selectedNavButtonEvent(): string | undefined {
    const direction = SearchForMapKeyByValue(this.navButtonsByDirection, this.selectedNavButton);
    switch (direction) {
      case SbNavToDirections.Prev:
        return (SbMainPanelEvents.PREV);

      case SbNavToDirections.Next:
        return (SbMainPanelEvents.NEXT);

      case SbNavToDirections.Restart:
        return (SbMainPanelEvents.RESTART);

      default:
        return (undefined);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private selectNextNavButton(): void {
    this.selectNavButtonAt((this.selectedButtonIndex + 1) % this.navButtonsInOrder.length);
  }

  // ----------------------------------------------------------------------------------------------
  private selectNavButtonAt(index: number): void {
    this.navButtonsInOrder[this.selectedButtonIndex]?.setSelected(false);
    this.selectedButtonIndex = index;
    this.navButtonsInOrder[this.selectedButtonIndex]?.setSelected(true);
  }

  // ----------------------------------------------------------------------------------------------
  private setDefaultSelectedButton(defaultSelectedNavButtonId: SbNavToDirection | null): void {
    const shouldSelectButton = this.inputDeviceHandler.updateFromSettings()
      && defaultSelectedNavButtonId
      && (this.inputDeviceHandler.isSingleSwitchEnabled()
      || this.inputDeviceHandler.isDualSwitchEnabled);

    if (shouldSelectButton) {
      this.setSelectedNavButton(defaultSelectedNavButtonId);
    } else {
      this.unselectCurrentNavButton();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private setSelectedNavButton(direction: SbNavToDirection): void {
    const index = NavButtonDirections.indexOf(direction);
    this.selectNavButtonAt(index);
  }

  // ----------------------------------------------------------------------------------------------
  private uninitExportRootTimeline(): void {
    this.navStartFrame = -1;
    this.engine.exportRoot.timeline.removeEventListener(CjsEvents.CHANGE,
      this.onExportRootTimelineChangeNav);
  }

  // ----------------------------------------------------------------------------------------------
  private unselectCurrentNavButton(): void {
    this.selectNavButtonAt(-1);
  }

  // ----------------------------------------------------------------------------------------------
  private update(): void {
    if (!this.tutorialManager) {
      this.updateHelpfupTipsPanel();
    }
  }

  // ----------------------------------------------------------------------------------------------
  // eslint-disable-next-line class-methods-use-this
  private updateHelpfupTipsPanel(): void {
    // TODO
    /*
    if (this.engine.userSettingsData.storybook.helpfulTipsEnabled) {
      this.helpfulTipsPanel?.present();
    } else {
      this.helpfulTipsPanel?.unpresent();
    }
    */
  }
}
