import CloneObject from '../../object/CloneObject';
import IsObject from '../../object/IsObject';

function CloneArray<T>(source: T[]): T[] {
  const out: T[] = [];
  source.forEach((value) => {
    if (IsObject(value)) {
      out.push(CloneObject(value) as T);
    } else if (Array.isArray(value)) {
      out.push(CloneArray(value) as T);
    } else {
      out.push(value);
    }
  });

  return (out);
}

export default CloneArray;
