import CjsLabelPositionsByName from '../objects/CjsLabelPositionsByName';
import CjsDisplayObject from '../objects/display/CjsDisplayObject';
import CjsGetMovieClipLabels from './CjsGetMovieClipLabels';

function CjsGetMovieClipLabelsByName(
  displayObject: CjsDisplayObject<createjs.MovieClip> | createjs.MovieClip,
): CjsLabelPositionsByName {
  displayObject = displayObject instanceof createjs.MovieClip
    ? displayObject
    : displayObject.display;

  const sourceLabels = CjsGetMovieClipLabels(displayObject);
  const out = {} as CjsLabelPositionsByName;
  sourceLabels.forEach((sourceLabel) => {
    out[sourceLabel.label] = sourceLabel.position;
  });

  return (out);
}

export default CjsGetMovieClipLabelsByName;
