import SbScene from '../../engine/storybookScene/SbScene';
import { SbButtonsMenuConfig } from '../../engine/ui/buttonsMenu/config/SbButtonsMenuConfig';
import McBoySelected from './boy/McBoySelected';
import McGirlSelected from './girl/McGirlSelected';

function buildMenuConfig(areButtonsEnabled: boolean, scene: SbScene): SbButtonsMenuConfig {
  const isDisabled = !areButtonsEnabled;
  return ({
    buttons: {
      boyButtonDisplay: {
        isDisabled,
        callback: (): void => {
          scene.engine.playSound({
            audioId: 'my_clothes_correct.mp3',
            completeCallback: () => {
              McBoySelected(scene);
            },
          });
        },
      },

      girlButtonDisplay: {
        isDisabled,
        callback: (): void => {
          scene.engine.playSound({
            audioId: 'my_clothes_correct.mp3',
            completeCallback: () => {
              McGirlSelected(scene);
            },
          });
        },
      },
    },

    defaultButtonName: 'boyButtonDisplay',
  });
}

// ----------------------------------------------------------------------------------------------
function presentMenu(scene: SbScene, areButtonsEnabled: boolean): void {
  scene.engine.presentMenu(buildMenuConfig(areButtonsEnabled, scene));
}

// ----------------------------------------------------------------------------------------------
export default function McChooseCharacter(scene: SbScene): void {
  scene.stop('character_selection');
  presentMenu(scene, false);
  scene.playVoices('SBL0001_Firstchooseacharacter_480k_stereo')
    .then(() => {
      presentMenu(scene, true);
    });
}
