import CloneObject from '../../../utils/object/CloneObject';
import RecursiveReadonly from '../../../utils/object/RecursiveReadonly';
import SbDefaults from '../defaults/SbDefaults';
import SbUserSettingsData from './SbUserSettingsData';
import SbUserSettingsStorage from './storage/SbUserSettingsStorage';

export default class SbUserSettings {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private _data!: SbUserSettingsData;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor() {
    if (!this.readFromLocalStorage()) {
      this._data = CloneObject(SbDefaults.Settings);
    }
  }

  // ----------------------------------------------------------------------------------------------
  apply(source: RecursiveReadonly<SbUserSettingsData>): this {
    this._data = CloneObject(source);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get data(): SbUserSettingsData {
    return (this._data);
  }

  // ----------------------------------------------------------------------------------------------
  readFromLocalStorage(): boolean {
    const data = SbUserSettingsStorage.Read();
    if (data !== undefined) {
      this._data = data;
      return (true);
    }

    return (false);
  }

  // ----------------------------------------------------------------------------------------------
  reset(): this {
    this._data = CloneObject(SbDefaults.Settings);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  writeToLocalStorage(): boolean {
    return (SbUserSettingsStorage.Write(this._data));
  }
}
