import CloneObject from '../../../../../utils/object/CloneObject';
import IsPlainObject from '../../../../../utils/object/IsPlainObject';
import ParseBoolean from '../../../../../utils/parsers/ParseBoolean';
import ParseNumber from '../../../../../utils/parsers/ParseNumber';
import SbDefaults from '../../../defaults/SbDefaults';
import SbDesignData from '../../../SbDesignData';
import SbUserStorybookSettingsData from '../../storybook/SbUserStorybookSettingsData';

function SbParseStorybookSettings(source: unknown): SbUserStorybookSettingsData {
  const target = CloneObject(SbDefaults.Settings.storybook);
  if (!IsPlainObject(source)) {
    return (target);
  }

  target.aacSymbolsEnabled = ParseBoolean(source.aacSymbolsEnabled, target.aacSymbolsEnabled);
  target.captionHiliteColorIndex = ParseNumber(source.captionHiliteColorIndex,
    target.captionHiliteColorIndex, 0, SbDesignData.CaptionHilites.Colors.length - 1);

  target.captionHilitesEnabled = ParseBoolean(source.captionHilitesEnabled,
    target.captionHilitesEnabled);

  target.captionsEnabled = ParseBoolean(source.captionsEnabled, target.captionsEnabled);
  target.helpfulTipsEnabled = ParseBoolean(source.helpfulTipsEnabled, target.helpfulTipsEnabled);
  return (target);
}

export default SbParseStorybookSettings;
