/* eslint-disable no-bitwise */
import Clamp from '../../math/Clamp';

export default class RgbColor {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private _blueNormalized!: number;

  private _greenNormalized!: number;

  private _redNormalized!: number;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor();

  constructor(red: number, green: number, blue: number, areNormalized?: boolean);

  constructor(rgbColor: number);

  constructor(hexColor: string);

  constructor(redOrRgbHexColor?: number | string, green?: number, blue?: number,
    areNormalized = false) {
    if (redOrRgbHexColor === undefined) {
      this.redNormalized = 0.0;
      this.greenNormlized = 0.0;
      this.blueNormalized = 0.0;
    } else if (typeof (redOrRgbHexColor) === 'string') {
      this.hexStringToColor(redOrRgbHexColor);
    } else if (green === undefined || blue === undefined) {
      if (areNormalized) {
        this.redNormalized = redOrRgbHexColor;
        this.greenNormlized = green ?? 0.0;
        this.blueNormalized = blue ?? 0.0;
      } else {
        this.red = (redOrRgbHexColor & 0xff0000) >> 16;
        this.green = (redOrRgbHexColor & 0xff00) >> 8;
        this.blue = (redOrRgbHexColor & 0xff);
      }
    } else if (areNormalized) {
      this.redNormalized = redOrRgbHexColor;
      this.greenNormlized = green;
      this.blueNormalized = blue;
    } else {
      this.red = redOrRgbHexColor;
      this.green = green;
      this.blue = blue;
    }
  }

  // ----------------------------------------------------------------------------------------------
  get blue(): number {
    return (RgbColor.GetColorComponent(this._blueNormalized));
  }

  // ----------------------------------------------------------------------------------------------
  set blue(value: number) {
    this._blueNormalized = RgbColor.SetColorComponent(value);
  }

  // ----------------------------------------------------------------------------------------------
  get blueNormalized(): number {
    return (this._blueNormalized);
  }

  // ----------------------------------------------------------------------------------------------
  set blueNormalized(value: number) {
    this._blueNormalized = Clamp(value, 0.0, 1.0);
  }

  // ----------------------------------------------------------------------------------------------
  static Create(): RgbColor;

  static Create(rgbColor: number): RgbColor;

  static Create(red: number, green?: number, blue?: number, areNormalized?: boolean): RgbColor;

  static Create(hexColor: string): RgbColor;

  static Create(redOrRgbHexColor?: number | string, green?: number, blue?: number,
    areNormalized = false): RgbColor {
    if (redOrRgbHexColor === undefined) {
      return (new RgbColor());
    }

    if (typeof (redOrRgbHexColor) === 'string') {
      return (new RgbColor(redOrRgbHexColor));
    }

    if (green !== undefined && blue !== undefined) {
      return (new RgbColor(redOrRgbHexColor, green, blue, areNormalized));
    }

    return (new RgbColor(redOrRgbHexColor));
  }

  // ----------------------------------------------------------------------------------------------
  static GetColorComponent(normalizedColorComponent: number): number {
    normalizedColorComponent = Clamp(normalizedColorComponent, 0.0, 1.0);
    return (normalizedColorComponent === 1.0 ? 255 : normalizedColorComponent * 256);
  }

  // ----------------------------------------------------------------------------------------------
  get green(): number {
    return (RgbColor.GetColorComponent(this._greenNormalized));
  }

  // ----------------------------------------------------------------------------------------------
  set green(value: number) {
    this._greenNormalized = RgbColor.SetColorComponent(value);
  }

  // ----------------------------------------------------------------------------------------------
  get greenNormlized(): number {
    return (this._greenNormalized);
  }

  // ----------------------------------------------------------------------------------------------
  set greenNormlized(value: number) {
    this._greenNormalized = Clamp(value, 0.0, 1.0);
  }

  // ----------------------------------------------------------------------------------------------
  /**
   * Converts a hex string color to its numerical counterpart. RGB is surrportd; no alpha channel.
   * @param hex {string} - The RGB format string to convert. It can begin with an optional
   * `0x` or `#`.
   * @returns {number} - The converted number, or -1 if the hex string could not be converted.
   */
  hexStringToColor(hex: string): number {
    if (!hex) {
      return (-1);
    }

    hex = hex.replace(/^(?:#|0x)?([a-f\d])([a-f\d])([a-f\d])$/i,
      (m: string, r: string, g: string, b: string) => (r + r + g + g + b + b));

    const result = (/^(?:#|0x)?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i).exec(hex);
    if (!result) {
      return (-1);
    }

    const [, rs, gs, bs] = result;
    this.red = rs ? Number.parseInt(rs, 16) : 0;
    this.green = gs ? Number.parseInt(gs, 16) : 0;
    this.blue = bs ? Number.parseInt(bs, 16) : 0;

    return (this.value);
  }

  // ----------------------------------------------------------------------------------------------
  numericalToColor(color: number): number {
    this.red = (color & 0xff0000) >> 16;
    this.green = (color & 0xff00) >> 8;
    this.blue = color & 0xff;
    return (this.value);
  }

  // ----------------------------------------------------------------------------------------------
  setColor(color: number | string): this {
    if (typeof color === 'string') {
      this.hexStringToColor(color);
    } else {
      this.numericalToColor(color);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get red(): number {
    return (RgbColor.GetColorComponent(this._redNormalized));
  }

  // ----------------------------------------------------------------------------------------------
  set red(value: number) {
    this._redNormalized = RgbColor.SetColorComponent(value);
  }

  // ----------------------------------------------------------------------------------------------
  get redNormalized(): number {
    return (this._redNormalized);
  }

  // ----------------------------------------------------------------------------------------------
  set redNormalized(value: number) {
    this._redNormalized = Clamp(value, 0.0, 1.0);
  }

  // ----------------------------------------------------------------------------------------------
  static SetColorComponent(colorComponent: number): number {
    colorComponent = Clamp(colorComponent, 0, 255);
    return (colorComponent === 255 ? 1.0 : colorComponent / 256);
  }

  // ----------------------------------------------------------------------------------------------
  get value(): number {
    return (
      (this.red << 16)
      | (this.green << 8)
      | this.blue
    );
  }
}
