import RecursiveConcrete from '../../../../utils/object/RecursiveConcrete';
import SbMessagePanelAutoCloseConfig from './SbMessagePanelAutoCloseConfig';
import SbMessagePanelButtonLabelsConfig from './SbMessagePanelButtonLabelsConfig';
import SbMessagePanelDimBackConfig, { SbResolveMessagePanelDimBackConfig } from './SbMessagePanelDimBackConfig';

export type SbRawMessagePanelConfig = {
  autoClose?: SbMessagePanelAutoCloseConfig | true;
  buttonLabels?: SbMessagePanelButtonLabelsConfig;
  dimBack?: SbMessagePanelDimBackConfig;
};

export type SbMessagePanelConfig = Omit<SbRawMessagePanelConfig, 'buttonLabels' | 'dimBack'> & {
  buttonLabels: SbMessagePanelButtonLabelsConfig;
  dimBack: RecursiveConcrete<SbMessagePanelDimBackConfig>;
};

export function SbResolveMessagePanelConfig(
  config?: SbRawMessagePanelConfig,
): SbMessagePanelConfig {
  const _config = (config ?? {}) as SbMessagePanelConfig;
  _config.buttonLabels ??= {};
  _config.dimBack = SbResolveMessagePanelDimBackConfig(_config.dimBack);
  return (_config);
}

export default SbRawMessagePanelConfig;
