import SbScene from '../../../engine/storybookScene/SbScene';
import McGirlHowWearClothesAac from './McGirlHowWearClothesAac';
import McGirlMyClothesAac from './McGirlMyClothesAac';
import McGirlPresentClothes from './McGirlPresentClothes';

export default function McGirlHowWearClothes(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_How-do-I-wear-my-clothes2',
    'girl_how_wear_clothes_begin',
    'girl_how_wear_clothes_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McGirlHowWearClothesAac(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlMyClothesAac(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlPresentClothes(scene);
        },
      },
    });
  });
}
