import IMovieClipDisplayCtor from '../../../adobeAnimate/IMovieClipDisplayCtor';
import { CjsButtonLabels } from './CjsButtonLabels';
import ICjsButton from './ICjsButton';
import { CjsEvents } from '../../events/CjsEvents';
import CjsButtonEvents from './CjsButtonEvents';
import CjsDisplayObject from '../display/CjsDisplayObject';
import CjsDefaultButtonDisplayType from './CjsDefaultButtonDisplayType';
import { CjsGetTextObject, CjsTextObject } from '../CjsGetTextObject';
import CjsSetText from '../CjsSetText';
import CjsButtonHelper from './CjsButtonHelper';

export const CjsButtonDisplayObjectNames = {
  LabelText: 'txtLabel',
} as const;

export default class CjsButton<
  ButtonDisplayType extends CjsDefaultButtonDisplayType = CjsDefaultButtonDisplayType
> extends CjsDisplayObject<ButtonDisplayType> implements ICjsButton<ButtonDisplayType> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly helper: CjsButtonHelper;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(display: ButtonDisplayType,
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor) {
    super(display);

    display.stop();
    this.helper = this.createButtonHelper(hitAreaOrAnLibClipCtor);
    this.cjsEventsHandler.on(CjsEvents.CLICK, this.onDisplayClick, this);
  }

  // ----------------------------------------------------------------------------------------------
  get buttonLabel(): CjsTextObject | undefined {
    return (CjsGetTextObject(this.display, CjsButtonDisplayObjectNames.LabelText));
  }

  // ----------------------------------------------------------------------------------------------
  get buttonLabelText(): string {
    return (this.getButtonLabelText());
  }

  // ----------------------------------------------------------------------------------------------
  set buttonLabelText(value: string) {
    this.setButtonLabelText(value);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();
    this.helper.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  getButtonLabelText(): string {
    return (this.buttonLabel?.text ?? '');
  }

  // ----------------------------------------------------------------------------------------------
  get isDown(): boolean {
    return (this.helper.isDown);
  }

  // ----------------------------------------------------------------------------------------------
  get isOver(): boolean {
    return (this.helper.isOver);
  }

  // ----------------------------------------------------------------------------------------------
  override setEnabled(isEnabled: boolean): this {
    super.setEnabled(isEnabled);
    this.helper.enabled = isEnabled;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setButtonLabelText(label: string): this {
    const txtLabel = CjsGetTextObject(this.display, CjsButtonDisplayObjectNames.LabelText);
    CjsSetText(txtLabel, label);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setSelected(isSelected: boolean): this {
    this.helper.setSelected(isSelected);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setShouldUseDisabledState(shouldUseDisabledState: boolean): this {
    this.helper.shouldUseDisabledState = shouldUseDisabledState;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  override setVisible(isVisible: boolean): this {
    super.setVisible(isVisible);
    if (isVisible !== this.visible) {
      this.helper.onTargetVisibilityChanged();
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get shouldUseDisabledState(): boolean {
    return (this.helper.shouldUseDisabledState);
  }

  // ----------------------------------------------------------------------------------------------
  set shouldUseDisabledState(value: boolean) {
    this.setShouldUseDisabledState(value);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private createButtonHelper(
    HitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  ): CjsButtonHelper {
    let hitArea: createjs.DisplayObject | undefined;
    if (HitAreaOrAnLibClipCtor) {
      hitArea = HitAreaOrAnLibClipCtor instanceof createjs.DisplayObject
        ? HitAreaOrAnLibClipCtor
        : new HitAreaOrAnLibClipCtor();
    }

    const helper = new CjsButtonHelper(this.display, CjsButtonLabels.Out, CjsButtonLabels.Over,
      CjsButtonLabels.Down, hitArea, CjsButtonLabels.Hit);

    return (helper);
  }

  // ----------------------------------------------------------------------------------------------
  private onDisplayClick(_event: createjs.MouseEvent): void {
    this.emit(CjsButtonEvents.PRESSED, this);
  }
}
