import SbScene from '../../../engine/storybookScene/SbScene';
import McChooseCharacter from '../McChooseCharacter';
import McIntro from '../McIntro';
import McGirlSelectedAac from './McGirlSelectedAac';

export default function McGirlSelected(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_You-have-chosen-Girl_48.0k_stereo.mp3',
    'girl_selected_start',
    'girl_selected_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          McGirlSelectedAac(scene);
        },
      },

      prev: {
        callback: () => {
          McChooseCharacter(scene);
        },
      },

      restart: {
        callback: () => {
          McIntro(scene);
        },
      },
    });
  });
}
