import { SbAssetLib } from '../../../data/SbAssetLib';
import SbUserSettingsSubPanelData from './SbUserSettingsSubPanelData';
import SbUiComponent from '../../SbUiComponent';

export default abstract class SbSettingsSubPanel<
  SettingsDataType extends SbUserSettingsSubPanelData
> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  areSettingsDirty: boolean;

  protected readonly mainPanel: SbUiComponent;

  protected readonly lib: Readonly<SbAssetLib>;

  private _settings: SettingsDataType | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(mainPanel: SbUiComponent, lib: Readonly<SbAssetLib>) {
    this.mainPanel = mainPanel;
    this.lib = lib;
    this.areSettingsDirty = false;
  }

  // ----------------------------------------------------------------------------------------------
  get settings(): SettingsDataType | undefined {
    return (this._settings);
  }

  // ----------------------------------------------------------------------------------------------
  setSettings(data: SettingsDataType): this {
    this._settings = data;
    this.updateSettingsUiFromData();
    this.areSettingsDirty = false;
    return (this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected abstract updateSettingsUiFromData(): void;

  // ----------------------------------------------------------------------------------------------
  protected setSettingProp<T, K extends keyof T>(source: T | undefined, name: K,
    value: T[typeof name]): this {
    if (source !== undefined) {
      (source as T)[name] = value;
    }

    this.areSettingsDirty = true;
    return (this);
  }
}
