import { CjsEvents } from '../../../utils/createjs/events/CjsEvents';
import CjsLabel from '../../../utils/createjs/objects/buttons/CjsLabel';
import CjsGetMovieClipLabels from '../../../utils/createjs/utils/CjsGetMovieClipLabels';
import RecursiveReadonly from '../../../utils/object/RecursiveReadonly';
import IStorybookEngine from '../../IStorybookEngine';
import SbUserSettingsData from '../../data/settings/SbUserSettingsData';
import UpdateCaptionHilitesFromSettings from './UpdateCaptionHilitesFromSettings';

const DisplayObjectNames = {
  AacSymbols: 'aacSymbolsDisplay',
  CaptionHilites: 'captionHilitesContainer',
  Captions: 'captionsContainer',
} as const;

export default class SbContextLayers {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  private readonly captionHilitesLabelsByName: Map<string, CjsLabel[]>;

  private currentCaptionHiliteslabel: string | null | undefined;

  private currentDisplayFrame: number;

  private prevDisplayFrame: number;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    this.engine = engine;
    this.captionHilitesLabelsByName = new Map();
    this.currentDisplayFrame = -1;
    this.prevDisplayFrame = this.currentDisplayFrame;
    this.onStageTick = this.onStageTick.bind(this);
  }

  // ----------------------------------------------------------------------------------------------
  start(): void {
    this.display.stage.addEventListener(CjsEvents.TICK, this.onStageTick);

    // TODO: remove if na
    /*
    if (this.captionHilitesDisplay) {
      this.captionHilitesLabels = CjsGetMovieClipLabels(this.captionHilitesDisplay).reverse();
    }
    */
  }

  // ----------------------------------------------------------------------------------------------
  updateFromSettings(): void {
    this.updateFromSettingsCaptions()
      .updateFromSettingsAacSymbols()
      .updateFromSettingsCaptionHilites();
  }

  // ----------------------------------------------------------------------------------------------
  // TODO: remove if na
  /*
  updateFromTick(): void {
    this.updateFromTickProgressBar();
  }
  */

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private get aacSymbolsDisplay(): createjs.DisplayObject | undefined {
    return (this.findUnitDisplay(DisplayObjectNames.AacSymbols));
  }

  // ----------------------------------------------------------------------------------------------
  private get captionsDisplay(): createjs.MovieClip | undefined {
    return (this.findUnitDisplay(DisplayObjectNames.Captions));
  }

  // ----------------------------------------------------------------------------------------------
  private get captionHilitesDisplay(): createjs.MovieClip | undefined {
    return (this.findUnitDisplay(DisplayObjectNames.CaptionHilites));
  }

  // ----------------------------------------------------------------------------------------------
  private get display(): createjs.MovieClip {
    return (this.engine.exportRoot.display);
  }

  // ----------------------------------------------------------------------------------------------
  private findUnitDisplay(namePrefix: string): createjs.MovieClip | undefined {
    const display = this.display.children
      .find((childDisplayObject): childDisplayObject is createjs.MovieClip => {
        if (!(childDisplayObject instanceof createjs.MovieClip)) {
          return (false);
        }

        return (childDisplayObject.name?.startsWith(namePrefix));
      });

    return (display);
  }

  // ----------------------------------------------------------------------------------------------
  private getCaptionHilitesLabelsOf(hilitesDisplay: createjs.MovieClip): CjsLabel[] {
    let labels = this.captionHilitesLabelsByName.get(hilitesDisplay.name);
    if (!labels) {
      labels = CjsGetMovieClipLabels(hilitesDisplay).reverse();
      this.captionHilitesLabelsByName.set(hilitesDisplay.name, labels);
    }

    return (labels);
  }

  // ----------------------------------------------------------------------------------------------
  private getCurrentCaptionHiliteLabel(): string | null {
    const { captionHilitesDisplay } = this;
    if (!captionHilitesDisplay) {
      return (null);
    }

    const captionHilitesLabels = this.getCaptionHilitesLabelsOf(captionHilitesDisplay);
    // TODO
    // const { currentFrame } = this.display;
    const { currentFrame } = captionHilitesDisplay;

    // TODO
    // const hilitesFrame = currentFrame - this.engine.startTimelineFrame;
    const hilitesFrame = currentFrame;

    const targetLabel = captionHilitesLabels.find((label) => hilitesFrame >= label.position);
    return (targetLabel ? targetLabel.label : null);
  }

  // ----------------------------------------------------------------------------------------------
  private onStageTick(): void {
    if (this.updateCurrentFrame()) {
      this.updateCaptionHilites();
    }

    this.updateCaptions();
  }

  // ----------------------------------------------------------------------------------------------
  private updateCaptionHilites(): void {
    const { captionHilitesDisplay } = this;
    if (!captionHilitesDisplay) {
      return;
    }

    const targetFrame = this.prevDisplayFrame === -1
      ? this.currentDisplayFrame
      : this.currentDisplayFrame + (this.currentDisplayFrame - this.prevDisplayFrame);

    captionHilitesDisplay.gotoAndStop(targetFrame);

    const currentCaptionHiliteLabel = this.getCurrentCaptionHiliteLabel();
    // console.log(currentFrame, captionHilitesDisplay.currentFrame, currentCaptionHiliteLabel);
    if (currentCaptionHiliteLabel !== this.currentCaptionHiliteslabel) {
      this.currentCaptionHiliteslabel = currentCaptionHiliteLabel;
      this.updateFromSettingsCaptionHilites();
    }
  }

  // ----------------------------------------------------------------------------------------------
  private updateCaptions(): void {
    const { captionsDisplay } = this;
    if (captionsDisplay) {
      captionsDisplay.visible = this.userSettingsData.storybook.captionsEnabled;
    }
  }

  // ----------------------------------------------------------------------------------------------
  private updateCurrentFrame(): boolean {
    const { currentFrame } = this.display;
    if (currentFrame === this.currentDisplayFrame) {
      return (false);
    }

    this.prevDisplayFrame = this.currentDisplayFrame;
    this.currentDisplayFrame = currentFrame;
    return (true);
  }

  // ----------------------------------------------------------------------------------------------
  private updateFromSettingsAacSymbols(): this {
    const { aacSymbolsDisplay } = this;
    if (aacSymbolsDisplay) {
      aacSymbolsDisplay.visible = this.userSettingsData.storybook.aacSymbolsEnabled;
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateFromSettingsCaptionHilites(): this {
    const { captionHilitesDisplay } = this;
    if (captionHilitesDisplay) {
      UpdateCaptionHilitesFromSettings(captionHilitesDisplay, this.userSettingsData.storybook);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateFromSettingsCaptions(): this {
    this.updateCaptions();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateFromTickProgressBar(): this {
    const { startTimelineFrame } = this.engine;
    if (startTimelineFrame >= 0) {
      const totalFrames = this.display.totalFrames - startTimelineFrame + 1;
      this.engine.progress = this.display.currentFrame / totalFrames;
    } else {
      this.engine.progress = 0;
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private get userSettingsData(): RecursiveReadonly<SbUserSettingsData> {
    return (this.engine.userSettingsData);
  }
}
