/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McGirlSelectPantsQuiz from './McGirlSelectPantsQuiz';

function pantsTips(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_It-is-good-to-start-by-teaching-pants2',
    'girl_wear_pants_tips_start',
    'girl_wear_pants_tips_end',
  ).then(() => {
    scene.stop('girl_wear_pants_tips_end');
    scene.engine.presentNav({
      next: {
        callback: () => {
          scene.playToEnd(
            'girl_wear_pants_tips_menu_closed',
            () => {
              McGirlSelectPantsQuiz(scene);
            },
          );
        },
      },

      prev: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearPants(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_wear_pants_how_aac_i',
        soundId: 'SBL-0007_I',
      },
      {
        frameOrLabel: 'girl_wear_pants_how_aac_wear',
        soundId: 'SBL-0008_Wear',
      },
      {
        frameOrLabel: 'girl_wear_pants_how_aac_pants',
        soundId: 'SBL-0009_pants',
      },
      {
        frameOrLabel: 'girl_wear_pants_how_aac_legs',
        soundId: 'SBL-0010_legs',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          pantsTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearPants(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-pants-over-my-legs',
    'girl_wear_pants_how_start',
    'girl_wear_pants_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlWearPants(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearPants(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'girl_wear_pants_animation_end',
    'girl_wear_pants_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      girlWearPantsButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'girlWearPantsButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McGirlWearPants(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-pants',
    'girl_wear_pants_start',
    'girl_wear_pants_end',
  ).then(() => {
    wearHow(scene);
  });
}
