const SbInputDevices = {
  DualSwitch: 'dualSwitch',
  EyeTracker: 'eyeTracker',
  SingleSwitch: 'singleSwitch',
  TapMouse: 'tapMouse',
} as const;

type SbInputDevice = typeof SbInputDevices[keyof typeof SbInputDevices];

const SbInputDevicesArray = Object.values(SbInputDevices) as ReadonlyArray<SbInputDevice>;

export {
  SbInputDevice,
  SbInputDevices,
  SbInputDevicesArray,
};
