import GetNumberValue from '../../../../utils/object/GetNumberValue';
import GetStringValue from '../../../../utils/object/GetStringValue';
import RecursiveConcrete from '../../../../utils/object/RecursiveConcrete';
import SbDefaults from '../../../data/defaults/SbDefaults';

type SbMessagePanelDimBackConfig = {
  alpha?: number;
  color?: string;
};

export function SbResolveMessagePanelDimBackConfig(
  config?: SbMessagePanelDimBackConfig,
): RecursiveConcrete<SbMessagePanelDimBackConfig> {
  const _config = config ?? {};
  _config.alpha = GetNumberValue(_config, 'alpha', SbDefaults.MessagePanel.DimBack.Alpha);
  _config.color = GetStringValue(_config, 'color', SbDefaults.MessagePanel.DimBack.Color);
  return (_config as RecursiveConcrete<SbMessagePanelDimBackConfig>);
}

export default SbMessagePanelDimBackConfig;
