/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McGirlSelectShoesQuiz from './McGirlSelectShoesQuiz';

async function shoesTips(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_For-a-start-you-can-let-the-k_48.0k_stereo.mp3',
    'girl_wear_shoes_tips1_start',
    'girl_wear_shoes_tips1_end',
  );

  await scene.playSyncVoice(
    'SBL-0002_It-is-good-to-start-by-teaching-shoes',
    'girl_wear_shoes_tips2_start',
    'girl_wear_shoes_tips2_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        scene.playToEnd(
          'girl_wear_shoes_tips_menu_closed',
          () => {
            McGirlSelectShoesQuiz(scene);
          },
        );
      },
    },

    prev: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    restart: {
      callback: () => {
        McGirlWearShoes(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'girl_wear_shoes_how_aac_i',
        soundId: 'SBL-0015_I',
      },
      {
        frameOrLabel: 'girl_wear_shoes_how_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'girl_wear_shoes_how_aac_shoes',
        soundId: 'SBL-0017_shoes',
      },
      {
        frameOrLabel: 'girl_wear_shoes_how_aac_socks',
        soundId: 'SBL-0018_socks',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          shoesTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearShoes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-shoes-over-my-socks',
    'girl_wear_shoes_how_start',
    'girl_wear_shoes_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McGirlWearShoes(scene);
        },
      },

      restart: {
        callback: () => {
          McGirlWearShoes(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'girl_wear_shoes_animation_end',
    'girl_wear_shoes_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      girlWearShoesButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'girlWearShoesButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McGirlWearShoes(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-shoes',
    'girl_wear_shoes_start',
    'girl_wear_shoes_end',
  ).then(() => {
    wearHow(scene);
  });
}
