import GetStringValue from '../../../../utils/object/GetStringValue';

type SbEngineButtonsMenuConfig = {
  defaultCorrectSoundKey?: string;
  defaultInorrectSoundKey?: string;
};

export function SbResolveEngineButtonsMenuConfig(
  config?: SbEngineButtonsMenuConfig | null,
): SbEngineButtonsMenuConfig {
  const configOut: SbEngineButtonsMenuConfig = config ?? {} as SbEngineButtonsMenuConfig;
  configOut.defaultCorrectSoundKey = GetStringValue(config, 'defaultCorrectSoundKey');
  configOut.defaultInorrectSoundKey = GetStringValue(config, 'defaultInorrectSoundKey');
  return (configOut);
}

export default SbEngineButtonsMenuConfig;
