/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoySelectSocksQuiz from './McBoySelectSocksQuiz';

// ------------------------------------------------------------------------------------------------
function closeTips(scene: SbScene): void {
  scene.playTo(
    'boy_wear_socks_tips_closing_end',
    'boy_wear_socks_tips_closing_begin',
    () => {
      McBoySelectSocksQuiz(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
async function playTipsVoc(scene: SbScene): Promise<void> {
  await scene.playSyncVoice(
    'SBL-0001_Encourage-your-kids-to-explore_48.0k_stereo.mp3',
    'boy_wear_socks_tips_voc_begin',
    'boy_wear_socks_tips_voc_end',
  );

  scene.engine.presentNav({
    next: {
      callback: () => {
        closeTips(scene);
      },
    },

    prev: {
      callback: () => {
        playHowVoiceAac(scene);
      },
    },

    restart: {
      callback: () => {
        McBoyWearSocks(scene);
      },
    },
  });
}

// ------------------------------------------------------------------------------------------------
function openTips(scene: SbScene): void {
  scene.playTo(
    'boy_wear_socks_tips_end',
    'boy_wear_socks_tips_begin',
    () => {
      playTipsVoc(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_wear_socks_how_aac_i',
        soundId: 'SBL-0007_I',
      },
      {
        frameOrLabel: 'boy_wear_socks_how_aac_wear',
        soundId: 'SBL-0012_Wear',
      },
      {
        frameOrLabel: 'boy_wear_socks_how_aac_socks',
        soundId: 'SBL-0013_Socks',
      },
      {
        frameOrLabel: 'boy_wear_socks_how_aac_feet',
        soundId: 'SBL-0014_Feet',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          openTips(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearSocks(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-socks-on-my-feet',
    'boy_wear_socks_how_start',
    'boy_wear_socks_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyWearSocks(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearSocks(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'boy_wear_socks_animation_end',
    'boy_wear_socks_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      boyWearSocksButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'boyWearSocksButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McBoyWearSocks(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-socks',
    'boy_wear_socks_start',
    'boy_wear_socks_end',
  ).then(() => {
    wearHow(scene);
  });
}
