import GetBooleanValue from '../../../../../utils/object/GetBooleanValue';
import GetNumberValue from '../../../../../utils/object/GetNumberValue';

type SbHiliteIncorrectConfig = {
  /** @default 8 */
  numShakes?: number;

  /** @default true */
  shouldUseTicks?: boolean;

  /** @default 2 */
  tiltIntensityInDegrees?: number;

  /** @default 5 */
  tremorIntensity?: number;

  /** @default 1 (in ticks, if shouldUseTicks is true), 25 (in ms otherwise) */
  waitDuration?: number;
};

function ResolveHiliteIncorrectConfig(
  config?: SbHiliteIncorrectConfig,
): Required<SbHiliteIncorrectConfig> {
  const _config = (config ?? {}) as Required<SbHiliteIncorrectConfig>;
  _config.numShakes = GetNumberValue(_config, 'numShakes', 8);
  _config.shouldUseTicks = GetBooleanValue(_config, 'shouldUseTicks', true);
  _config.tiltIntensityInDegrees = GetNumberValue(_config, 'tiltIntensityInDegrees', 2);
  _config.tremorIntensity = GetNumberValue(_config, 'tremorIntensity', 5);
  _config.waitDuration = GetNumberValue(_config, 'waitDuration',
    _config.shouldUseTicks ? 1 : 25);
  return (_config);
}

export {
  ResolveHiliteIncorrectConfig,
  SbHiliteIncorrectConfig,
};
