import EventEmitter from 'eventemitter3';
import IStorybookEngine from '../../IStorybookEngine';

export default class SbContentVideo extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly engine: IStorybookEngine;

  timelineStartFrame: number;

  private contentVideoBitmap: createjs.Bitmap | undefined;

  private buffer: createjs.VideoBuffer | undefined;

  private _videoElement: HTMLVideoElement | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine, videoElement?: HTMLVideoElement) {
    super();

    this.engine = engine;
    this.timelineStartFrame = 0;

    this._videoElement = videoElement;

    if (videoElement) {
      this.init(videoElement);
    }
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.removeAllListeners();
    this.stop();
    this.contentVideoBitmap?.parent?.removeChild(this.contentVideoBitmap);
    this.contentVideoBitmap = undefined;
  }

  // ----------------------------------------------------------------------------------------------
  init(video: HTMLVideoElement): this {
    if (this.contentVideoBitmap) {
      return (this);
    }

    if (this.engine.config?.shouldUseVideoBuffer) {
      this.buffer = new createjs.VideoBuffer(video);
    }

    this.contentVideoBitmap = new createjs.Bitmap(this.buffer || video);
    this.contentVideoBitmap.visible = false;

    const container = this.engine.videoContainer || this.engine.exportRoot.display;
    container.addChildAt(this.contentVideoBitmap, 0);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  async play(): Promise<boolean> {
    if (!this._videoElement || this._videoElement.ended) {
      return (false);
    }

    try {
      await this._videoElement.play();
    } catch (error) {
      console.warn(error);
      return (false);
    }

    return (true);
  }

  // ----------------------------------------------------------------------------------------------
  seekTo(timeInSecs: number): this {
    if (this._videoElement) {
      this._videoElement.currentTime = timeInSecs;
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  seekToFrame(timelineFrame: number): this {
    if (!this._videoElement) {
      return (this);
    }

    const videoFrame = Math.max(0, timelineFrame - this.timelineStartFrame);
    const timeInSecs = videoFrame / this.engine.config.videoFps;
    return (this.seekTo(timeInSecs));
  }

  // ----------------------------------------------------------------------------------------------
  setVisible(isVisible: boolean): this {
    if (this.contentVideoBitmap) {
      this.contentVideoBitmap.visible = isVisible;
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  stop(): this {
    this._videoElement?.pause();
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get videoElement(): HTMLVideoElement | undefined {
    return (this._videoElement);
  }
}
