import CjsDisplayObject from '../../../../../utils/createjs/objects/display/CjsDisplayObject';
import IsOdd from '../../../../../utils/math/IsOdd';
import { ResolveHiliteIncorrectConfig, SbHiliteIncorrectConfig } from './SbHiliteIncorrectConfig';

function SbHiliteIncorrect(target: createjs.DisplayObject | CjsDisplayObject,
  config?: SbHiliteIncorrectConfig): void {
  const display = target instanceof CjsDisplayObject ? target.display : target;
  const _config = ResolveHiliteIncorrectConfig(config);
  const tween = createjs.Tween.get(display, {
    useTicks: _config.shouldUseTicks,
  });

  const originalTransform = {
    rotation: display.rotation,
    x: display.x,
    y: display.y,
  };

  for (let index = 0; index < _config.numShakes; index += 1) {
    if (index > 0) {
      tween.wait(_config.waitDuration);
    }

    const flipFactor = IsOdd(index) ? -1 : 1;
    tween.to({
      rotation: originalTransform.rotation + (_config.tiltIntensityInDegrees * flipFactor),
      x: originalTransform.x + Math.random() * (_config.tremorIntensity * flipFactor),
      y: originalTransform.y + Math.random() * (_config.tremorIntensity * flipFactor),
    });
  }

  tween
    .wait(_config.waitDuration)
    .to({
      rotation: originalTransform.rotation,
      x: originalTransform.x,
      y: originalTransform.y,
    });
}

export default SbHiliteIncorrect;
