/* eslint-disable no-use-before-define */
import SbScene from '../../../../engine/storybookScene/SbScene';
import SbSetSceneSpeakerButton from '../../../../engine/storybookScene/utils/SbSceneSpeakerButton';
import { SbButtonsMenuConfig } from '../../../../engine/ui/buttonsMenu/config/SbButtonsMenuConfig';
import SbButtonsMenuButtonConfigsByName from '../../../../engine/ui/buttonsMenu/config/SbButtonsMenuButtonConfigsByName';
import McGirlOutro from './McGirlOutro';
import McGirlThisHowWhereClothes from './McGirlThisHowWhereClothes';

const NUM_BUTTONS = 6;
const CORRECT_INDEXES_ORDER = [1, 0, 2, 4, 5, 3] as const;

let Scene: SbScene | undefined;
let WorkingCorrectIndexes: number[];

function getButtonName(index: number): string {
  return (`girlHelpWearClothesMenuButton${index}Display`);
}

// ------------------------------------------------------------------------------------------------
function presentCompletedMenu(): void {
  if (!Scene) {
    console.warn('no scene defined');
    return;
  }

  const scene = Scene;
  scene.playTo(
    'girl_help_wear_clothes_quiz_correct_end',
    'girl_help_wear_clothes_quiz_correct_start',
    () => {
      scene.playSyncVoice(
        'SBL-0001_Well-done-Great-job_48.0k_stereo.mp3',
        'girl_well_done_start',
        'girl_well_done_end',
      ).then(() => {
        scene.engine.presentNav({
          next: {
            callback: () => {
              McGirlOutro(scene);
            },
          },

          prev: {
            callback: () => {
              McGirlHelpHerWearClothesQuiz(scene);
            },
          },

          restart: {
            callback: () => {
              McGirlThisHowWhereClothes(scene);
            },
          },
        });
      });
    },
  );
}

// ------------------------------------------------------------------------------------------------
function correctItemSelected(): void {
  if (!Scene) {
    console.warn('no scene defined');
    return;
  }

  WorkingCorrectIndexes.shift();
  if (WorkingCorrectIndexes.length === 0) {
    presentCompletedMenu();
  } else {
    Scene.nextFrame();
    presentMenu(Scene, true);
  }
}

// ------------------------------------------------------------------------------------------------
function buildButtonsConfig(correctIndex: number,
  isEnabled: boolean): SbButtonsMenuButtonConfigsByName {
  const buttonsConfig: SbButtonsMenuButtonConfigsByName = {};

  for (let index = 0; index < NUM_BUTTONS; index += 1) {
    const isCorrect = index === correctIndex;
    const buttonName = getButtonName(index);
    buttonsConfig[buttonName] = {
      isCorrect,
      isDisabled: !isEnabled,
      callback: isCorrect ? correctItemSelected : undefined,
    };
  }

  return (buttonsConfig);
}

// ------------------------------------------------------------------------------------------------
function buildMenuConfig(correctIndex: number, areButtonsEnabled: boolean): SbButtonsMenuConfig {
  const config: SbButtonsMenuConfig = {
    buttons: buildButtonsConfig(correctIndex, areButtonsEnabled),
    defaultButtonName: getButtonName(0),
    isQuiz: true,
  };

  return (config);
}

// ----------------------------------------------------------------------------------------------
function presentMenu(scene: SbScene, areButtonsEnabled: boolean): void {
  const correctIndex = WorkingCorrectIndexes[0] ?? 0;
  scene.engine.presentMenu(buildMenuConfig(correctIndex, areButtonsEnabled));
}

// ------------------------------------------------------------------------------------------------
export default function McGirlHelpHerWearClothesQuiz(scene: SbScene): void {
  Scene = scene;

  Scene.stop('girl_help_wear_clothes_quiz');

  SbSetSceneSpeakerButton(Scene, false);

  WorkingCorrectIndexes = [...CORRECT_INDEXES_ORDER];
  presentMenu(Scene, true);
}
