/**
 * Determines whether an object has a property with the specified name. This function calls
 * `Object.prototype.hasOwnProperty.call(source, key)` to get around ESLint's
 * "no-prototype-builtins" rule:
 * https://eslint.org/docs/rules/no-prototype-builtins
 *
 * @param source - The object to search.
 * @param key - The key for the property on source.
 *
 * @returns True if the property is found; false otherwise.
 */
export default function HasOwnProperty(source: unknown, key: string): boolean {
  return (Object.prototype.hasOwnProperty.call(source, key));
}
