export default function CjsResolveFrame(clipOrTween: createjs.MovieClip | createjs.AbstractTween,
  frameOrLabel?: number | string): number | null {
  const tween = clipOrTween instanceof createjs.MovieClip
    ? clipOrTween.timeline
    : clipOrTween;

  const frame = typeof (frameOrLabel) === 'string'
    ? tween.resolve(frameOrLabel)
    : frameOrLabel;

  if (frame === null || frame === undefined) {
    console.warn('unable to resolve in scene timeline, frame label of value:', frameOrLabel);
    return (null);
  }

  return (frame);
}
