import { CjsEvents } from '../../../utils/createjs/events/CjsEvents';
import CjsLoadItem from '../../../utils/createjs/load/CjsLoadItem';
import Clamp from '../../../utils/math/Clamp';
import GetWindowQueryParamValue from '../../../utils/web/window/GetWindowQueryParamValue';
import SbDesignData from '../../data/SbDesignData';
import { SbQueryParams } from '../../data/SbQueryParams';
import {
  SbAnCompType,
  SbGetComp,
} from './SbCompLib';

import SbMakeResponsive from './SbMakeResponsive';

declare function handleFileLoad(evt: unknown, comp: SbAnCompType): void;
declare function handleComplete(evt: unknown, comp: SbAnCompType): void;

// These variables are needed for the original Adobe Animate / CreateJs export, since we replaced
// the original `init` function in the publishing HTML Template with `SbDocPrepUtil`.
/* eslint-disable camelcase, @typescript-eslint/no-unused-vars */
declare let canvas: HTMLCanvasElement | null;
declare let anim_container: HTMLDivElement | null;
declare let dom_overlay_container: HTMLDivElement | null;
/* eslint-enable camelcase, @typescript-eslint/no-unused-vars */

// ------------------------------------------------------------------------------------------------
function sbEnqueContentVideo(loader: createjs.LoadQueue): void {
  const contentVideoUrl = GetWindowQueryParamValue(SbQueryParams.ContentVideo);
  if (contentVideoUrl) {
    loader.loadFile({
      id: SbDesignData.ContentVideoId,
      src: contentVideoUrl,
      type: 'video',
    });
  }
}

// ------------------------------------------------------------------------------------------------
function setProgress(progress: number): void {
  const progressBarFill = document.getElementById(SbDesignData.HtmlElementIds.ProgressBarFill);
  if (progressBarFill) {
    const value = Math.round((1.0 - Clamp(progress, 0, 1.0)) * 100);
    progressBarFill.style.right = `${value}%`;
  }
}

// ------------------------------------------------------------------------------------------------
function SbDocPrepUtil(): void {
  canvas = document.getElementById(SbDesignData.HtmlElementIds.Canvas) as HTMLCanvasElement;

  /* eslint-disable camelcase */
  anim_container = document.getElementById(SbDesignData.HtmlElementIds.AnimationContainer) as
    HTMLDivElement;

  dom_overlay_container = document.getElementById(
    SbDesignData.HtmlElementIds.DomOverlayContainer,
  ) as HTMLDivElement;
  /* eslint-enable camelcase */

  const comp = SbGetComp();
  if (!comp) {
    console.warn('no compoision library found');
    return;
  }

  const lib = comp.getLibrary();
  const loader = new createjs.LoadQueue(false);
  loader.installPlugin(createjs.Sound);
  loader.addEventListener(CjsEvents.PROGRESS, (evt) => {
    const event = evt as createjs.ProgressEvent;
    setProgress(event.progress);
  });

  loader.addEventListener(CjsEvents.FILE_LOAD, (evt) => {
    const event = evt as createjs.Event;
    const item = event.item as CjsLoadItem;
    if (item.id === SbDesignData.ContentVideoId) {
      lib.contentVideo = event.result;
    } else {
      handleFileLoad(evt, comp);
    }
  });

  loader.addEventListener(CjsEvents.COMPLETE, (evt) => {
    handleComplete(evt, comp);
    loader.destroy();
  });

  loader.loadManifest(lib.properties.manifest);
  sbEnqueContentVideo(loader);

  SbMakeResponsive();
}

export default SbDocPrepUtil;
