import IsValidPositiveZeroInteger from '../../../../../utils/number/IsValidPositiveZeroInteger';
import CloneObject from '../../../../../utils/object/CloneObject';
import IsPlainObject from '../../../../../utils/object/IsPlainObject';
import { IsValidNonEmptyString } from '../../../../../utils/string/IsValidString';
import SbDefaults from '../../../defaults/SbDefaults';
import SbUserStorybookState from '../../userStorybookStates/SbUserStorybookState';
import SbUserStorybooksStates from '../../userStorybookStates/SbUserStorybooksStates';

function parseStoryBookState(source: unknown): SbUserStorybookState | undefined {
  const testData = source as SbUserStorybookState;
  if (!IsPlainObject(source)
  || (!IsValidPositiveZeroInteger(testData.progresFrameOrLabel)
  && !IsValidNonEmptyString(testData.progresFrameOrLabel))) {
    return (undefined);
  }

  return (testData);
}

// ------------------------------------------------------------------------------------------------
function SbParseUserStorybooksStates(source: unknown): SbUserStorybooksStates {
  if (!IsPlainObject(source)) {
    return (CloneObject(SbDefaults.Settings.storybooksStates));
  }

  const target = source as SbUserStorybooksStates;
  const storyBookIds = Object.keys(target);
  storyBookIds.forEach((storyBookId) => {
    const storyBookState = target[storyBookId];
    if (storyBookState) {
      const parsedStoryBookState = parseStoryBookState(storyBookState);
      if (parsedStoryBookState) {
        target[storyBookId] = parsedStoryBookState;
      } else {
        console.warn(`unable to parse storybook state with id: ${storyBookId}`);
      }
    } else {
      delete target[storyBookId];
      console.warn(`unable to parse storybook state with id: ${storyBookId}`);
    }
  });

  return (target);
}

export default SbParseUserStorybooksStates;
