import Clamp from '../../../math/Clamp';
import CjsGetMovieClip from '../CjsGetMovieClip';
import CjsButton from '../buttons/CjsButton';
import CjsGetButton from '../CjsGetButton';
import CjsDragManager from '../../utils/dragManager/CjsDragManager';
import CjsDragManagerEvents from '../../utils/dragManager/CjsDragManagerEvents';
import CjsSliderEvents from './CjsSliderEvents';
import { CjsEvents } from '../../events/CjsEvents';
import CjsEventsHandler from '../../events/CjsEventsHandler';
import IMovieClipDisplayCtor from '../../../adobeAnimate/IMovieClipDisplayCtor';
import CjsSliderCompNames from './CjsSliderCompNames';
import CjsDisplayObject from '../display/CjsDisplayObject';
import ICjsSlider from './ICjsSlider';

export default class CjsSlider<
  SliderDisplayType extends createjs.Container = createjs.Container
> extends CjsDisplayObject<SliderDisplayType> implements ICjsSlider<SliderDisplayType> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly dragManager: CjsDragManager | undefined;

  private readonly handle: CjsButton | undefined;

  private readonly back: CjsEventsHandler | undefined;

  private readonly fill: createjs.MovieClip | undefined;

  private fillMask: createjs.Shape | undefined;

  private fillBounds: createjs.Rectangle | undefined;

  private handleBounds: createjs.Rectangle | undefined;

  private backBounds: createjs.Rectangle | undefined;

  private _shouldUseHandleBoundsForConstraint: boolean;

  private _value!: number;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(display: SliderDisplayType, value?: number,
    handleHitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor) {
    super(display);

    this._shouldUseHandleBoundsForConstraint = true;

    this.handle = this.initHandle(handleHitAreaOrAnLibClipCtor);
    this.fill = this.initFill();
    this.back = this.initBack();

    this.dragManager = this.initDragManager();
    this.resolveSetValue(value ?? 0, true, false);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();
    this.dragManager?.dispose();
    this.back?.dispose();
    this.handle?.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  override setEnabled(isEnabled: boolean): this {
    super.setEnabled(isEnabled);
    this.handle?.setEnabled(isEnabled);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setShouldUseHandleBoundsForConstraint(should: boolean): this {
    if (should !== this._shouldUseHandleBoundsForConstraint) {
      return (this);
    }

    this._shouldUseHandleBoundsForConstraint = should;
    return (this.updateDragBounds().updateHandle());
  }

  // ----------------------------------------------------------------------------------------------
  setValue(value: number, shouldEmitChanged = true): this {
    this.resolveSetValue(value, true, shouldEmitChanged);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get shouldUseHandleBoundsForConstraint(): boolean {
    return (this._shouldUseHandleBoundsForConstraint);
  }

  // ----------------------------------------------------------------------------------------------
  set shouldUseHandleBoundsForConstraint(value: boolean) {
    this.setShouldUseHandleBoundsForConstraint(value);
  }

  // ----------------------------------------------------------------------------------------------
  get value(): number {
    return (this._value);
  }

  // ----------------------------------------------------------------------------------------------
  set value(x: number) {
    this.setValue(x);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private calcDragBounds(): createjs.Rectangle | undefined {
    if (!this.handleBounds || !this.backBounds) {
      return (undefined);
    }

    const handleBoundsWt = this.shouldUseHandleBoundsForConstraint
      ? this.handleBounds.width
      : 0;

    const handleBoundsWt2 = handleBoundsWt / 2;
    const { backBounds } = this;
    const boundxCenterY = backBounds.y + backBounds.height / 2;
    return (new createjs.Rectangle(
      backBounds.x + handleBoundsWt2,
      boundxCenterY,
      backBounds.x + backBounds.width - handleBoundsWt,
      0,
    ));
  }

  // ----------------------------------------------------------------------------------------------
  private handleBackMove(event: createjs.MouseEvent): void {
    if (!this.back || !this.dragManager?.dragBounds) {
      return;
    }

    const x1 = this.dragManager.dragBounds.x;
    const x2 = this.dragManager.dragBounds.x + this.dragManager.dragBounds.width;
    const range = x2 - x1;
    const value = (event.localX - x1) / range;
    this.resolveSetValue(value, true, true);
  }

  // ----------------------------------------------------------------------------------------------
  private initBack(): CjsEventsHandler | undefined {
    const back = CjsGetMovieClip(this.display, CjsSliderCompNames.Back);
    if (!back) {
      return (undefined);
    }

    let backHitArea = CjsGetMovieClip(this.display, CjsSliderCompNames.BackHitArea);
    if (backHitArea) {
      back.hitArea = backHitArea;
    } else {
      backHitArea = back;
    }

    this.backBounds = backHitArea.getBounds().clone();
    this.backBounds.width *= backHitArea.scaleX;
    this.backBounds.height *= backHitArea.scaleY;

    const eventsHandler = new CjsEventsHandler(back);
    eventsHandler.on(CjsEvents.MOUSE_DOWN, this.onBackMouseDown, this);
    return (eventsHandler);
  }

  // ----------------------------------------------------------------------------------------------
  private initDragManager(): CjsDragManager | undefined {
    if (!this.handle) {
      return (undefined);
    }

    const dragBounds = this.calcDragBounds();
    if (!dragBounds) {
      return (undefined);
    }

    const dragManager = new CjsDragManager(this.handle.display)
      .on(CjsDragManagerEvents.DRAG, this.onDragManagerDrag, this)
      .on(CjsDragManagerEvents.START, this.onDragManagerStart, this)
      .on(CjsDragManagerEvents.STOP, this.onDragManagerStop, this)
      .setDragBounds(dragBounds);

    return (dragManager);
  }

  // ----------------------------------------------------------------------------------------------
  private initFill(): createjs.MovieClip | undefined {
    const fill = CjsGetMovieClip(this.display, CjsSliderCompNames.Fill);
    if (fill) {
      this.fillMask = new createjs.Shape();
      fill.mask = this.fillMask;
      this.fillBounds = fill.getBounds().clone();
    }

    return (fill);
  }

  // ----------------------------------------------------------------------------------------------
  private initHandle(
    handleHitAreaOrAnLibClipCtor: createjs.DisplayObject | IMovieClipDisplayCtor | undefined,
  ): CjsButton | undefined {
    const handle = CjsGetButton(this.display, CjsSliderCompNames.Handle, undefined,
      handleHitAreaOrAnLibClipCtor);

    if (handle) {
      this.handleBounds = handle.display.getBounds().clone();
    }

    return (handle);
  }

  // ----------------------------------------------------------------------------------------------
  private onBackMouseDown(event: createjs.MouseEvent): void {
    this.emit(CjsSliderEvents.DRAG_STARTED, this);
    this.handleBackMove(event);
    this.back?.on(CjsEvents.PRESS_MOVE, this.onBackPressMove, this)
      .on(CjsEvents.PRESS_UP, this.onBackPressUp, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onBackPressMove(event: createjs.MouseEvent): void {
    this.handleBackMove(event);
  }

  // ----------------------------------------------------------------------------------------------
  private onBackPressUp(): void {
    this.emit(CjsSliderEvents.DRAG_STOPPED, this);
    this.back?.off(CjsEvents.PRESS_MOVE, this.onBackPressMove, this)
      .off(CjsEvents.PRESS_UP, this.onBackPressUp, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onDragManagerDrag(): void {
    if (!this.handle || !this.dragManager?.dragBounds) {
      return;
    }

    const x1 = this.dragManager.dragBounds.x;
    const x2 = this.dragManager.dragBounds.x + this.dragManager.dragBounds.width;
    const range = x2 - x1;
    const value = (this.handle.x - x1) / range;
    this.resolveSetValue(value, false, true);
  }

  // ----------------------------------------------------------------------------------------------
  private onDragManagerStart(): void {
    this.emit(CjsSliderEvents.DRAG_STARTED, this);
  }

  // ----------------------------------------------------------------------------------------------
  private onDragManagerStop(): void {
    this.emit(CjsSliderEvents.DRAG_STOPPED, this);
  }

  // ----------------------------------------------------------------------------------------------
  private resolveSetValue(value: number, shouldUpdateHandle: boolean,
    shouldEmitChanged: boolean): void {
    value = Clamp(value, 0, 1.0);
    if (value === this._value) {
      return;
    }

    this._value = value;
    this.updateFill();

    if (shouldUpdateHandle) {
      this.updateHandle();
    }

    if (shouldEmitChanged) {
      this.emit(CjsSliderEvents.CHANGED, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private updateDragBounds(): this {
    const newBounds = this.calcDragBounds();
    if (newBounds) {
      this.dragManager?.setDragBounds(newBounds);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateFill(): this {
    if (this.fillMask && this.fill && this.fillBounds) {
      const bounds = this.fillBounds;
      this.fillMask.graphics
        .clear()
        .drawRect(0, 0, bounds.width * this.value, bounds.height);
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  private updateHandle(): this {
    if (!this.handle || !this.dragManager?.dragBounds) {
      return (this);
    }

    const x1 = this.dragManager.dragBounds.x;
    const x2 = x1 + this.dragManager.dragBounds.width;
    const range = x2 - x1;
    this.handle.x = x1 + range * this.value;

    const y1 = this.dragManager.dragBounds.y;
    const y2 = x1 + this.dragManager.dragBounds.height;
    this.handle.y = Clamp(this.handle.y, y1, y2);

    return (this);
  }
}
