import { ISbAnCompLibHeader } from '../../data/SbAssetLib';

type SbAnCompType = {
  getLibrary(): ISbAnCompLibHeader;
};

type SbAdobeAnType = {
  compositions: Record<string, SbAnCompType>;
};

declare const AdobeAn: SbAdobeAnType;

// ------------------------------------------------------------------------------------------------
function SbGetComp(): SbAnCompType | undefined {
  const compId = Object.keys(AdobeAn.compositions)[0];
  return (compId ? AdobeAn.compositions[compId] : undefined);
}

// ------------------------------------------------------------------------------------------------
function SbGetCompLib(comp?: SbAnCompType): ISbAnCompLibHeader | undefined {
  comp ??= SbGetComp();
  return (comp?.getLibrary());
}

export {
  SbAdobeAnType,
  SbAnCompType,
  SbGetComp,
  SbGetCompLib,
};
