import SbDefaults from '../../data/defaults/SbDefaults';
import SbUserSettingsData from '../../data/settings/SbUserSettingsData';
import SbUiComponent from '../SbUiComponent';
import CloneObject from '../../../utils/object/CloneObject';
import SbSettingsAudioSubPanel from './subPanels/audio/SbSettingsAudioSubPanel';
import SbSettingsDevicesSubPanel from './subPanels/devices/SbSettingsDevicesSubPanel';
import SbSettingsStorybookSubPanel from './subPanels/storybook/SbSettingsStorybookSubPanel';
import SbSettingsPanelEvents from './events/SbSettingsPanelEvents';
import IStorybookEngine from '../../IStorybookEngine';
import { SbMessagePanelButtonIds } from '../messagePanel/SbMessagePanelButtonIds';
import SbUiComponentEvents from '../events/SbUiComponentEvents';

export default class SbSettingsPanel extends SbUiComponent {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly audioSubPanel: SbSettingsAudioSubPanel;

  private readonly devicesSubPanel: SbSettingsDevicesSubPanel;

  private readonly storybookSubPanel: SbSettingsStorybookSubPanel;

  private _settingsData: SbUserSettingsData;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: createjs.MovieClip, engine: IStorybookEngine) {
    super(container, engine);

    this.audioSubPanel = new SbSettingsAudioSubPanel(this, engine.lib);
    this.devicesSubPanel = new SbSettingsDevicesSubPanel(this, engine.lib);
    this.storybookSubPanel = new SbSettingsStorybookSubPanel(this, engine.lib);

    this.initButton('saveButtonDisplay', this.onSavePressed);
    this.initButton('exitButtonDisplay', this.onExitPressed);

    this._settingsData = CloneObject(SbDefaults.Settings);
  }

  // ----------------------------------------------------------------------------------------------
  get settingsData(): SbUserSettingsData {
    return (this._settingsData);
  }

  // ----------------------------------------------------------------------------------------------
  set settingsData(value: SbUserSettingsData) {
    this._settingsData = value;
    this.audioSubPanel.setSettings(this._settingsData.audio);
    this.devicesSubPanel.setSettings(this._settingsData.device);
    this.storybookSubPanel.setSettings(this._settingsData.storybook);
  }

  // ----------------------------------------------------------------------------------------------
  present(settingsData: SbUserSettingsData): this {
    this.settingsData = settingsData;
    this.visible = true;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  resetModifiedFlags(): this {
    this.audioSubPanel.areSettingsDirty = false;
    this.devicesSubPanel.areSettingsDirty = false;
    this.storybookSubPanel.areSettingsDirty = false;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  unpresent(): this {
    this.visible = false;
    return (this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected override async onClosePressed(): Promise<void> {
    if (await this.shouldConitnueWithUnsavedChanges()) {
      this.emit(SbUiComponentEvents.CLOSE, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  protected override onInit(): void {
    super.onInit(this.engine.lib.animateClasses.CloseXButtonHitBoxDisplay);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private areSettingsDirty(): boolean {
    return (
      this.audioSubPanel.areSettingsDirty
      || this.devicesSubPanel.areSettingsDirty
      || this.storybookSubPanel.areSettingsDirty
    );
  }

  // ----------------------------------------------------------------------------------------------
  private async shouldConitnueWithUnsavedChanges(): Promise<boolean> {
    if (!this.areSettingsDirty()) {
      return (true);
    }

    const messageId = await this.engine.createMessagePanel(
      'You have unsaved changes.\nContinue to close settings?',
      this.engine.lib.animateClasses.MessagePanelYNDisplay,
    );

    return (messageId === SbMessagePanelButtonIds.Yes);
  }

  // ----------------------------------------------------------------------------------------------
  private async onExitPressed(): Promise<void> {
    if (await this.shouldConitnueWithUnsavedChanges()) {
      this.emit(SbSettingsPanelEvents.EXIT, this);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private onSavePressed(): void {
    this.emit(SbSettingsPanelEvents.SAVE, this);
  }
}
