/* eslint-disable no-use-before-define */
import SbAacSceneHelper from '../../../../engine/storybookScene/SbAacSceneHelper';
import SbScene from '../../../../engine/storybookScene/SbScene';
import McBoySelectCapQuiz from './McBoySelectCapQuiz';

function closeHow(scene: SbScene): void {
  scene.playTo(
    'boy_wear_cap_close_how_end',
    'boy_wear_cap_close_how_start',
    () => {
      McBoySelectCapQuiz(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function playHowVoiceAac(scene: SbScene): void {
  SbAacSceneHelper(
    [
      {
        frameOrLabel: 'boy_wear_cap_how_aac_i',
        soundId: 'SBL-0019_I',
      },
      {
        frameOrLabel: 'boy_wear_cap_how_aac_wear',
        soundId: 'SBL-0016_wear',
      },
      {
        frameOrLabel: 'boy_wear_cap_how_aac_cap',
        soundId: 'SBL-0021_cap',
      },
      {
        frameOrLabel: 'boy_wear_cap_how_aac_head',
        soundId: 'SBL-0022_head',
      },
    ],
    scene,
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          closeHow(scene);
        },
      },

      prev: {
        callback: () => {
          playHowVoice(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearCap(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playHowVoice(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0002_I-wear-my-cap-on-my-head',
    'boy_wear_cap_how_start',
    'boy_wear_cap_how_end',
  ).then(() => {
    scene.engine.presentNav({
      next: {
        callback: () => {
          playHowVoiceAac(scene);
        },
      },

      prev: {
        callback: () => {
          McBoyWearCap(scene);
        },
      },

      restart: {
        callback: () => {
          McBoyWearCap(scene);
        },
      },
    });
  });
}

// ------------------------------------------------------------------------------------------------
function playWearAnimation(scene: SbScene): void {
  scene.playTo(
    'boy_wear_cap_animation_end',
    'boy_wear_cap_animation_start',
    () => {
      playHowVoice(scene);
    },
  );
}

// ------------------------------------------------------------------------------------------------
function wearHow(scene: SbScene): void {
  scene.engine.presentMenu({
    buttons: {
      boyWearCapButtonDisplay: {
        callback: () => {
          playWearAnimation(scene);
        },
      },
    },

    defaultButtonName: 'boyWearCapButtonDisplay',
  });

  scene.engine.startPressIndicator();
}

// ------------------------------------------------------------------------------------------------
export default function McBoyWearCap(scene: SbScene): void {
  scene.playSyncVoice(
    'SBL-0001_I-wear-my-cap',
    'boy_wear_cap_start',
    'boy_wear_cap_end',
  ).then(() => {
    wearHow(scene);
  });
}
