import EventEmitter from 'eventemitter3';
import CjsGetButton from '../../../utils/createjs/objects/CjsGetButton';
import CjsButton from '../../../utils/createjs/objects/buttons/CjsButton';
import ICjsBaseButton from '../../../utils/createjs/objects/buttons/ICjsBaseButton';
import IsPlainObject from '../../../utils/object/IsPlainObject';
import IStorybookEngine from '../../IStorybookEngine';
import { SbButtonsMenuConfig } from './config/SbButtonsMenuConfig';
import SbButtonsMenuButtonConfigsByName from './config/SbButtonsMenuButtonConfigsByName';
import SbHiliteIncorrect from './config/utils/SbHiliteIncorrect';
import { SbButtonsMenuButtonConfig, SbResolveButtonsMenuButtonConfig } from './config/SbButtonsMenuButtonConfig';
import SbInputDeviceHandler from '../inputDeviceHandler/SbInputDeviceHandler';
import SbInputDeviceHandlerEvents from '../inputDeviceHandler/SbInputDeviceHandlerEvents';
import SbQuizData from '../../data/userPerformance/SbQuizData';
import SbButtonsMenuEvents from './events/SbButtonsMenuEvents';

type ResolvedConfigs = {
  buttonConfigsByName: SbButtonsMenuButtonConfigsByName;
  thisConfig: SbButtonsMenuConfig;
};

export default class SbButtonsMenu extends EventEmitter {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  quizData: SbQuizData | undefined;

  private readonly buttonsByConfig: Map<SbButtonsMenuButtonConfig, CjsButton>;

  private readonly buttonsOrder: CjsButton[];

  private readonly engine: IStorybookEngine;

  private readonly inputDeviceHandler: SbInputDeviceHandler;

  private selectedButtonIndex: number;

  private config: SbButtonsMenuConfig | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    super();

    this.engine = engine;
    this.buttonsByConfig = new Map();
    this.buttonsOrder = [];
    this.inputDeviceHandler = this.initInputDeviceHandler();
    this.selectedButtonIndex = -1;
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    super.removeAllListeners();
    this.uninitResources();
  }

  // ----------------------------------------------------------------------------------------------
  private resolveConfigs(sourceConfig: SbButtonsMenuConfig): ResolvedConfigs {
    const buttonConfigsByName: SbButtonsMenuButtonConfigsByName = sourceConfig.buttons ?? {};
    const thisConfig: SbButtonsMenuConfig = {
      buttons: buttonConfigsByName,
      buttonsContainer: sourceConfig.buttonsContainer,
      correctSoundKey: sourceConfig.correctSoundKey,
      defaultButtonName: sourceConfig.defaultButtonName,
      incorrectSoundKey: sourceConfig.incorrectSoundKey,
      isQuiz: sourceConfig.isQuiz,
      shouldCloseMenu: sourceConfig.shouldCloseMenu ?? true,
    };

    this.config = thisConfig;
    return ({
      buttonConfigsByName,
      thisConfig,
    });
  }

  // ----------------------------------------------------------------------------------------------
  start(config: SbButtonsMenuConfig): this {
    const resolvedConfigs = this.resolveConfigs(config);
    this.initButtonsFromConfigs(resolvedConfigs);

    this.inputDeviceHandler.isEnabled = this.inputDeviceHandler.isSingleSwitchEnabled()
      || this.inputDeviceHandler.isDualSwitchEnabled();

    this.initDefaultSelectedButton(resolvedConfigs.thisConfig);

    this.quizData = resolvedConfigs.thisConfig.isQuiz
      ? {
        numAttempts: 0,
        totalSelection: Object.keys(resolvedConfigs.buttonConfigsByName).length,
        percentageCorrect: 0,
      }
      : undefined;

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  updateFromSettings(): void {
    this.inputDeviceHandler
      .setEnabled(this.inputDeviceHandler.isSingleSwitchEnabled()
        || this.inputDeviceHandler.isDualSwitchEnabled())
      .updateFromSettings();
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private confirmSelectedButton(): void {
    const { selectedButton } = this;
    if (selectedButton) {
      this.onButtonPressed(selectedButton);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private disposeButtons(): void {
    this.buttonsByConfig.forEach((button) => {
      button.dispose();
    });

    this.buttonsByConfig.clear();
    this.buttonsOrder.length = 0;
  }

  // ----------------------------------------------------------------------------------------------
  private getDefaultSelectedButtonIndex(thisConfig: SbButtonsMenuConfig): number {
    let index: number;
    const name = thisConfig.defaultButtonName;
    if (thisConfig.defaultButtonName) {
      index = this.buttonsOrder.findIndex((button) => button.name === name);
      if (index === -1) {
        console.warn(`no default menu button found with name: ${name}`);
      }
    } else {
      index = 0;
    }

    return (index);
  }

  // ----------------------------------------------------------------------------------------------
  private static HandleButtonCallback(button: CjsButton,
    buttonConfig: SbButtonsMenuButtonConfig): void {
    if (buttonConfig.callback !== undefined) {
      buttonConfig.callback.call(buttonConfig.callbackContext, button.name);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private handleIfCorrectButton(button: CjsButton,
    buttonConfig: SbButtonsMenuButtonConfig): boolean {
    if (buttonConfig.isCorrect !== true) {
      return (false);
    }

    this.playSound(this.config?.correctSoundKey);

    this.updateQuizPerformance(buttonConfig.isCorrect);
    this.emit(SbButtonsMenuEvents.OPTION_SELECTED, button.name, buttonConfig.isCorrect, this);
    return (true);
  }

  // ----------------------------------------------------------------------------------------------
  private handleIfIncorrectButton(button: CjsButton,
    buttonConfig: SbButtonsMenuButtonConfig): boolean {
    if (buttonConfig.isCorrect !== false) {
      return (false);
    }

    this.playSound(this.config?.incorrectSoundKey);
    SbHiliteIncorrect(button, buttonConfig.incorrectConfig);

    this.updateQuizPerformance(buttonConfig.isCorrect);
    this.emit(SbButtonsMenuEvents.OPTION_SELECTED, button.name, buttonConfig.isCorrect, this);
    return (true);
  }

  // ----------------------------------------------------------------------------------------------
  private initButtonsFromConfigs(resolvedConfigs: ResolvedConfigs): void {
    const displayContainer = resolvedConfigs.thisConfig.buttonsContainer
      || this.engine.exportRoot.display;

    const buttonsConfig = resolvedConfigs.buttonConfigsByName ?? {};
    const buttonNames = Object.keys(buttonsConfig);
    buttonNames.forEach((buttonName) => {
      const button = CjsGetButton(displayContainer, buttonName, this.onButtonPressed,
        undefined, this);

      if (button) {
        const buttonConfig = SbResolveButtonsMenuButtonConfig(buttonsConfig[buttonName]);
        buttonsConfig[buttonName] = buttonConfig;

        this.buttonsByConfig.set(buttonConfig, button);
        this.buttonsOrder.push(button);
        button.enabled = !(buttonConfig.isDisabled === true);
        button.visible = !(buttonConfig.isHidden === true);
      } else {
        console.warn('no menu button found with name:', buttonName, 'on container:',
          displayContainer);

        if (displayContainer instanceof createjs.MovieClip) {
          console.warn('affected frame:', displayContainer.currentFrame);
        }
      }
    });
  }

  // ----------------------------------------------------------------------------------------------
  private initDefaultSelectedButton(thisConfig: SbButtonsMenuConfig): void {
    if (this.inputDeviceHandler.isEnabled) {
      const defaultButtonIndex = this.getDefaultSelectedButtonIndex(thisConfig);
      this.selectButtonAt(defaultButtonIndex);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private initInputDeviceHandler(): SbInputDeviceHandler {
    return (new SbInputDeviceHandler(this.engine)
      .on(SbInputDeviceHandlerEvents.NEXT, this.onInputDeviceNext, this)
      .on(SbInputDeviceHandlerEvents.SELECT, this.onInputDeviceSelect, this));
  }

  // ----------------------------------------------------------------------------------------------
  private onButtonPressed(baseButton: ICjsBaseButton): void {
    const button = baseButton as CjsButton;
    const buttonConfig = this.config?.buttons ? this.config.buttons[button.name] : undefined;
    if (!IsPlainObject(buttonConfig)) {
      console.warn('no menu button config found for menu button with name:', button.name);
      return;
    }

    if (this.handleIfIncorrectButton(button, buttonConfig)) {
      SbButtonsMenu.HandleButtonCallback(button, buttonConfig);
      return;
    }

    this.handleIfCorrectButton(button, buttonConfig);

    if (this.config?.shouldCloseMenu) {
      this.engine.unpresentMenu();
    }

    SbButtonsMenu.HandleButtonCallback(button, buttonConfig);
  }

  // ----------------------------------------------------------------------------------------------
  private onInputDeviceNext(): void {
    this.selectNextButton();
  }

  // ----------------------------------------------------------------------------------------------
  private onInputDeviceSelect(): void {
    this.confirmSelectedButton();
  }

  // ----------------------------------------------------------------------------------------------
  private playSound(soundKey: string | undefined): void {
    if (soundKey) {
      this.engine.playSound(soundKey);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private selectButtonAt(index: number): void {
    const currentSelectedButton = this.selectedButton;
    const newSelectedButton = this.buttonsOrder[index];
    if (currentSelectedButton === newSelectedButton) {
      return;
    }

    this.selectedButtonIndex = index;
    currentSelectedButton?.setSelected(false);
    newSelectedButton?.setSelected(true);
  }

  // ----------------------------------------------------------------------------------------------
  private get selectedButton(): CjsButton | undefined {
    return (this.buttonsOrder[this.selectedButtonIndex]);
  }

  // ----------------------------------------------------------------------------------------------
  private selectNextButton(): void {
    if (this.buttonsOrder.length > 0) {
      this.selectButtonAt((this.selectedButtonIndex + 1) % this.buttonsOrder.length);
    }
  }

  // ----------------------------------------------------------------------------------------------
  private uninitResources(): void {
    this.inputDeviceHandler.dispose();
    this.disposeButtons();
    this.config = undefined;
  }

  // ----------------------------------------------------------------------------------------------
  private updateQuizPerformance(isCorrect: boolean): void {
    if (!this.quizData) {
      return;
    }

    this.quizData.numAttempts += 1;

    if (isCorrect) {
      this.quizData.percentageCorrect = 1 / this.quizData.numAttempts;
    }
  }
}
