import CjsGetBounds from '../createjs/utils/CjsGetBounds';
import ICjsMcSymbolPrototype from '../createjs/utils/ICjsMcSymbolPrototype';
import RgbColor from './color/RgbColor';

function CjsApplyColor(displayObject: ICjsMcSymbolPrototype, color: string | null): void {
  displayObject.uncache();
  if (color) {
    const rgbColor: RgbColor = RgbColor.Create(color);
    const rx = 0;
    const gx = 0;
    const bx = 0;
    const ax = 1;
    const r = rgbColor.red;
    const g = rgbColor.green;
    const b = rgbColor.blue;
    const a = 0;
    const filter = new createjs.ColorFilter(rx, gx, bx, ax, r, g, b, a);
    displayObject.filters = [
      filter,
    ];

    const bnds = CjsGetBounds(displayObject);
    if (bnds) {
      displayObject.cache(bnds.x, bnds.y, bnds.width, bnds.height);
    } else {
      console.warn(`bounds is unavailable on clip: ${displayObject.name}`, displayObject);
    }
  } else {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    displayObject.filters = null;
  }
}

export default CjsApplyColor;
