import { CjsEvents } from '../../../utils/createjs/events/CjsEvents';
import CjsGetDisplayObject from '../../../utils/createjs/objects/CjsGetDisplayObject';
import IStorybookEngine from '../../IStorybookEngine';
import { SbInputDevices } from '../../data/SbInputDevices';
import SbPressIndicator from './SbPressIndicator';

export default class SbPressIndicatorManager {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  static readonly DEFAULT_NAME = 'pressIndicatorDisplay';

  readonly engine: IStorybookEngine;

  private indicator: SbPressIndicator | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(engine: IStorybookEngine) {
    this.engine = engine;
  }

  // ----------------------------------------------------------------------------------------------
  start(
    delayMs: number | undefined = undefined,
    container: createjs.Container | undefined = undefined,
    indicatorDisplayName = SbPressIndicatorManager.DEFAULT_NAME,
  ): void {
    const indicator = CjsGetDisplayObject(container ?? this.engine.exportRoot.display,
      indicatorDisplayName);

    if (!indicator) {
      console.warn(`no display object found with name: ${indicatorDisplayName}`);
      return;
    }

    this.indicator = new SbPressIndicator(indicator);

    if (this.engine.userSettingsData.device.inputDevice !== SbInputDevices.TapMouse) {
      this.indicator.stop();
      return;
    }

    this.indicator.start(delayMs ?? this.engine.config.pressIndicatorDelay);

    this.engine.exportRoot.display.on(CjsEvents.MOUSE_DOWN, () => {
      if (this.indicator) {
        this.indicator.stop();
        this.indicator = undefined;
      }
    }, this, true);
  }

  // ----------------------------------------------------------------------------------------------
  stop(): void {
    this.indicator?.stop();
    this.indicator = undefined;
  }

  // ----------------------------------------------------------------------------------------------
  updateBgmFromSettings(): void {
    if (this.engine.userSettingsData.device.inputDevice !== SbInputDevices.TapMouse) {
      this.stop();
    }
  }

  // ==============================================================================================
  // private
  // ==============================================================================================
}
