import type SbMessagePanel from './SbMessagePanel';

export default class SbMessagePanelSingleton {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  static Instance: SbMessagePanel | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  static Clear(clearOnlyIfMatch?: SbMessagePanel): void {
    if (!clearOnlyIfMatch || clearOnlyIfMatch === SbMessagePanelSingleton.Instance) {
      SbMessagePanelSingleton.Instance = undefined;
    }
  }

  // ----------------------------------------------------------------------------------------------
  static Get(): SbMessagePanel | undefined {
    return (SbMessagePanelSingleton.Instance);
  }

  // ----------------------------------------------------------------------------------------------
  static Set(value: SbMessagePanel): void {
    SbMessagePanelSingleton.Instance = value;
  }

  // ==============================================================================================
  // private
  // ==============================================================================================
  private constructor() {
    throw (new Error('Instances of SbMessagePanelSingleton may not be created.'));
  }
}
