import IStorybookEngine from '../../IStorybookEngine';
import SbUserSettings from '../../data/settings/SbUserSettings';
import SbMessagePanel from '../../ui/messagePanel/SbMessagePanel';
import SbMessagePanelEvents from '../../ui/messagePanel/SbMessagePanelEvents';
import SbMessagePanelSingleton from '../../ui/messagePanel/SbMessagePanelSingleton';
import SbSaveSettingsConfig from './SbSaveSettingsConfig';

async function createResultMessagePanel(engine: IStorybookEngine, message: string,
  shouldAutoCloseSettingsPanel: boolean): Promise<void> {
  return (new Promise<void>((resolve) => {
    SbMessagePanel.Create(engine.exportRoot.stage, message,
      engine.lib.animateClasses.MessagePanelCloseDisplay, {
        autoClose: true,
      });

    if (shouldAutoCloseSettingsPanel) {
      SbMessagePanelSingleton.Get()?.once(SbMessagePanelEvents.CLOSE, () => {
        resolve();
      });
    } else {
      resolve();
    }
  }));
}

// ------------------------------------------------------------------------------------------------
async function SbSaveSettings(engine: IStorybookEngine, userSettings: SbUserSettings,
  config?: SbSaveSettingsConfig): Promise<boolean> {
  return (new Promise<boolean>((resolve) => {
    let resultMessage: string | undefined;
    let shouldAutoCloseSettingsPanel = false;
    const saveOk = userSettings.writeToLocalStorage();

    if (saveOk) {
      resultMessage = config?.successMessage;
      shouldAutoCloseSettingsPanel = true;
    } else {
      console.warn('unable to write settings to local storage');
      resultMessage = config?.failMessage;
    }

    if (resultMessage) {
      (async (): Promise<void> => {
        await createResultMessagePanel(engine, resultMessage, shouldAutoCloseSettingsPanel);
        resolve(saveOk);
      })();

      return;
    }

    resolve(saveOk);
  }));
}

export default SbSaveSettings;
