import { GlobalDomEvents } from '../../../utils/web/dom/GlobalDomEvents';
import { SbGetCompLib } from './SbCompLib';

declare const stage: createjs.Stage | undefined;

type DomContainers = (HTMLElement | null)[];

type ResponsiveProps = {
  domContainers: DomContainers;
  isResp: boolean;
  isScale: boolean;
  lastH: number;
  lastS: number;
  lastW: number;
  respDim: string;
  scaleType: number;
};

let Props: ResponsiveProps;

// ------------------------------------------------------------------------------------------------
function getDomContainers(inOut?: DomContainers): DomContainers {
  const _inOut = inOut ?? [];

  [
    'canvas',
    '_preload_div_',
    'animation_container',
    'dom_overlay_container',
  ].forEach((elemId, index) => {
    const elem = _inOut[index];
    if (!elem) {
      _inOut[index] = document.getElementById(elemId);
    }
  });

  return (_inOut as DomContainers);
}

// ------------------------------------------------------------------------------------------------
function onWindowResize(): void {
  const lib = SbGetCompLib();
  if (!lib) {
    return;
  }

  const w = lib.properties.width;
  const h = lib.properties.height;
  const iw = window.innerWidth;
  const ih = window.innerHeight;
  const pRatio = window.devicePixelRatio || 1;
  const xRatio = iw / w;
  const yRatio = ih / h;
  let sRatio = 1;

  if (Props.isResp) {
    if ((Props.respDim === 'width' && Props.lastW === iw)
    || (Props.respDim === 'height' && Props.lastH === ih)) {
      sRatio = Props.lastS;
    } else if (!Props.isScale) {
      if (iw < w || ih < h) { sRatio = Math.min(xRatio, yRatio); }
    } else if (Props.scaleType === 1) {
      sRatio = Math.min(xRatio, yRatio);
    } else if (Props.scaleType === 2) {
      sRatio = Math.max(xRatio, yRatio);
    }
  }

  const { domContainers } = Props;
  domContainers.forEach((container, index) => {
    if (!container) {
      return;
    }

    if (index === 0 && container instanceof HTMLCanvasElement) {
      container.width = w * pRatio * sRatio;
      container.height = h * pRatio * sRatio;
    } else {
      container.style.width = `${w * sRatio}px`;
      container.style.height = `${h * sRatio}px`;
    }
  });

  Props.lastW = iw;
  Props.lastH = ih;
  Props.lastS = sRatio;

  if (stage) {
    stage.scaleX = pRatio * sRatio;
    stage.scaleY = pRatio * sRatio;
    stage.tickOnUpdate = false;
    stage.update();
    stage.tickOnUpdate = true;
  }
}

// ------------------------------------------------------------------------------------------------
function SbMakeResponsive(isResp = true, respDim = 'both', isScale = false, scaleType = 1): void {
  Props = {
    domContainers: getDomContainers(),
    isResp,
    respDim,
    isScale,
    scaleType,
    lastH: 0,
    lastS: 1,
    lastW: 0,
  };

  window.addEventListener(GlobalDomEvents.Resize, onWindowResize);
  onWindowResize();
}

export default SbMakeResponsive;
