import EventEmitter from 'eventemitter3';
import ICjsDisplayObject from './ICjsDisplayObject';
import IPoint from '../../../geom/IPoint';
import CjsEventsHandler from '../../events/CjsEventsHandler';
import DataObject from '../../../object/DataObject';

export default class CjsDisplayObject<
  DisplayType extends createjs.DisplayObject = createjs.DisplayObject
> extends EventEmitter implements ICjsDisplayObject<DisplayType> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  readonly display: DisplayType;

  protected readonly cjsEventsHandler: CjsEventsHandler<DisplayType>;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(display: DisplayType) {
    super();
    this.display = display;
    (this.display as unknown as DataObject)._cjsData = this;
    this.cjsEventsHandler = new CjsEventsHandler(display);
  }

  // ----------------------------------------------------------------------------------------------
  get alpha(): number {
    return (this.display.alpha);
  }

  // ----------------------------------------------------------------------------------------------
  set alpha(value: number) {
    this.setAlpha(value);
  }

  // ----------------------------------------------------------------------------------------------
  copyPosition(source: IPoint): this {
    return (this.setPosition(source.x, source.y));
  }

  // ----------------------------------------------------------------------------------------------
  static Dispose(displayObject: CjsDisplayObject | undefined): undefined {
    displayObject?.dispose();
    return (undefined);
  }

  // ----------------------------------------------------------------------------------------------
  dispose(): void {
    this.removeAllListeners();
    this.cjsEventsHandler.dispose();
  }

  // ----------------------------------------------------------------------------------------------
  get enabled(): boolean {
    return (this.display.mouseEnabled);
  }

  // ----------------------------------------------------------------------------------------------
  set enabled(value: boolean) {
    this.setEnabled(value);
  }

  // ----------------------------------------------------------------------------------------------
  static Get<
    DisplayType extends createjs.DisplayObject = createjs.DisplayObject
  >(displayObject: createjs.DisplayObject): CjsDisplayObject<DisplayType> | undefined {
    return ((displayObject as unknown as DataObject)._cjsData as CjsDisplayObject<DisplayType>);
  }

  // ----------------------------------------------------------------------------------------------
  moveToBottom(): this {
    this.display.parent?.setChildIndex(this.display, 0);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  moveToTop(): this {
    const { parent } = this.display;
    parent?.setChildIndex(this.display, parent.children.length - 1);
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get name(): string {
    // might be `null` because of createjs' default null assignment
    return (this.display.name ?? '');
  }

  // ----------------------------------------------------------------------------------------------
  set name(value: string) {
    this.setName(value);
  }

  // ----------------------------------------------------------------------------------------------
  get scale(): number {
    return (this.scaleX);
  }

  // ----------------------------------------------------------------------------------------------
  set scale(value: number) {
    this.setScale(value, value);
  }

  // ----------------------------------------------------------------------------------------------
  get scaleX(): number {
    return (this.display.scaleX);
  }

  // ----------------------------------------------------------------------------------------------
  set scaleX(value: number) {
    this.setScaleX(value);
  }

  // ----------------------------------------------------------------------------------------------
  get scaleY(): number {
    return (this.display.scaleY);
  }

  // ----------------------------------------------------------------------------------------------
  set scaleY(value: number) {
    this.setScaleY(value);
  }

  // ----------------------------------------------------------------------------------------------
  setAlpha(alpha: number): this {
    this.display.alpha = alpha;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setEnabled(isEnabled: boolean): this {
    if (isEnabled !== this.enabled) {
      this.display.mouseEnabled = isEnabled;
    }

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setName(value: string): this {
    this.display.name = value ?? ''; // might be `null` because of createjs' default null assignment
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setPosition(x: number, y: number): this {
    this.x = x;
    this.y = y;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setScale(x: number, y?: number): this {
    this.scaleX = x;
    this.scaleY = y ?? x;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setScaleX(value: number): this {
    this.display.scaleX = value;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setScaleY(value: number): this {
    this.display.scaleY = value;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setVisible(isVisible: boolean): this {
    this.display.visible = isVisible;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setX(value: number): this {
    this.x = value;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setY(value: number): this {
    this.y = value;
    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get visible(): boolean {
    return (this.display.visible);
  }

  // ----------------------------------------------------------------------------------------------
  set visible(value: boolean) {
    this.setVisible(value);
  }

  // ----------------------------------------------------------------------------------------------
  get x(): number {
    return (this.display.x);
  }

  // ----------------------------------------------------------------------------------------------
  set x(value: number) {
    this.display.x = value;
  }

  // ----------------------------------------------------------------------------------------------
  get y(): number {
    return (this.display.y);
  }

  // ----------------------------------------------------------------------------------------------
  set y(value: number) {
    this.display.y = value;
  }
}
