import SbScene from './SbScene';

type AacUnit = {
  frameOrLabel: number | string;
  soundId: string,
};

type ResolveCallback = (value: void | PromiseLike<void>) => void;

// ------------------------------------------------------------------------------------------------
function playNextVoice(resolve: ResolveCallback, workingUnits: AacUnit[], scene: SbScene): void {
  const unit = workingUnits.shift();
  if (unit) {
    scene.stop(unit.frameOrLabel);
    scene.playVoices(unit.soundId).then(() => {
      playNextVoice(resolve, workingUnits, scene);
    });
  } else {
    resolve();
  }
}

// ------------------------------------------------------------------------------------------------
export default function SbAacSceneHelper(units: readonly AacUnit[],
  scene: SbScene): Promise<void> {
  const workingUnits = [...units];
  return (new Promise((resolve) => {
    playNextVoice(resolve, workingUnits, scene);
  }));
}
