import CjsButton from './CjsButton';
import CjsGetButton from '../CjsGetButton';
import ICjsButton from './ICjsButton';
import IMovieClipDisplayCtor from '../../../adobeAnimate/IMovieClipDisplayCtor';
import CjsButtonEvents from './CjsButtonEvents';
import CjsDisplayObject from '../display/CjsDisplayObject';
import CjsDefaultButtonDisplayType from './CjsDefaultButtonDisplayType';
import ICjsCompoundButton from './ICjsCompoundButton';

export default class CjsCompoundButton<
  DisplayType extends createjs.Container = createjs.Container,
  ButtonDisplayType extends CjsDefaultButtonDisplayType = CjsDefaultButtonDisplayType
> extends CjsDisplayObject<DisplayType>
  implements ICjsCompoundButton<DisplayType, ButtonDisplayType> {
  // ==============================================================================================
  // properties
  // ==============================================================================================
  private readonly _buttons: (CjsButton<ButtonDisplayType> | undefined)[];

  private readonly defaultButton: CjsButton<ButtonDisplayType> | undefined;

  // ==============================================================================================
  // public
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  constructor(container: DisplayType,
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
    buttonDisplayNames: string | string[] = 'buttonDisplay') {
    super(container);

    if (typeof (buttonDisplayNames) === 'string') {
      buttonDisplayNames = [buttonDisplayNames];
    }

    this._buttons = this.onInitButtons(container, buttonDisplayNames, hitAreaOrAnLibClipCtor)
      || this.initButtons(container, buttonDisplayNames, hitAreaOrAnLibClipCtor);

    this.defaultButton = this._buttons.find((b) => !!b);
  }

  // ----------------------------------------------------------------------------------------------
  get button(): ICjsButton<ButtonDisplayType> | undefined {
    return (this.defaultButton);
  }

  // ----------------------------------------------------------------------------------------------
  override dispose(): void {
    super.dispose();
    this.forEachButton((button) => {
      button.dispose();
    });

    this._buttons.length = 0;
  }

  // ----------------------------------------------------------------------------------------------
  forEachButton(callback: (button: ICjsButton<ButtonDisplayType>) => void,
    context?: unknown): this {
    this._buttons.forEach((button) => {
      if (button) {
        callback.call(context, button);
      }
    });

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  getButtonAt(index: number): ICjsButton<ButtonDisplayType> | undefined {
    return (this._buttons[index]);
  }

  // ----------------------------------------------------------------------------------------------
  getButtonByName(name: string): ICjsButton<ButtonDisplayType> | undefined {
    return (this._buttons.find((button) => button?.name === name));
  }

  // ----------------------------------------------------------------------------------------------
  get isDown(): boolean {
    return (this.defaultButton?.isDown === true);
  }

  // ----------------------------------------------------------------------------------------------
  get isOver(): boolean {
    return (this.defaultButton?.isOver === true);
  }

  // ----------------------------------------------------------------------------------------------
  override setEnabled(isEnabled: boolean): this {
    super.setEnabled(isEnabled);
    this.forEachButton((button) => {
      button.enabled = isEnabled;
    });

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  setShouldUseDisabledState(shouldUseDisabledState: boolean): this {
    this.forEachButton((button) => {
      button.setShouldUseDisabledState(shouldUseDisabledState);
    });

    return (this);
  }

  // ----------------------------------------------------------------------------------------------
  get shouldUseDisabledState(): boolean {
    return (this.defaultButton?.shouldUseDisabledState === true);
  }

  // ----------------------------------------------------------------------------------------------
  set shouldUseDisabledState(value: boolean) {
    this.setShouldUseDisabledState(value);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  protected onButtonCallback(): void {
    this.emit(CjsButtonEvents.PRESSED, this);
  }

  // ==============================================================================================
  // protected
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  // eslint-disable-next-line class-methods-use-this
  protected onInitButtons(
    _container: DisplayType,
    _buttonDisplayNames: string[],
    _hitAreaOrAnLibClipCtor: createjs.DisplayObject | IMovieClipDisplayCtor | undefined,
  ): (CjsButton<ButtonDisplayType> | undefined)[] | undefined {
    return (undefined);
  }

  // ==============================================================================================
  // private
  // ==============================================================================================

  // ----------------------------------------------------------------------------------------------
  private initButtons(
    container: DisplayType,
    buttonDisplayNames: string[],
    hitAreaOrAnLibClipCtor?: createjs.DisplayObject | IMovieClipDisplayCtor,
  ): (CjsButton<ButtonDisplayType> | undefined)[] {
    const buttons: (CjsButton<ButtonDisplayType> | undefined)[] = [];

    buttonDisplayNames.forEach((name) => {
      const button = CjsGetButton(container, name, this.onButtonCallback,
        hitAreaOrAnLibClipCtor, this) as CjsButton<ButtonDisplayType> | undefined;
      buttons.push(button);
    });

    return (buttons);
  }
}
